import {FocusMonitor, FocusOrigin} from '@angular/cdk/a11y';
import {
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ElementRef,
  Input,
  OnDestroy,
  ViewEncapsulation,
  TemplateRef,
  AfterViewInit,
} from '@angular/core';
import {Subscription} from 'rxjs';
import {EsnStepLabel} from './step-label';
import {EsnStepperIntl} from './stepper-intl';
import {EsnStepperIconContext} from './stepper-icon';
import {CdkStepHeader, StepState} from '@angular/cdk/stepper';
import {mixinColor, CanColor} from '@angular/material/core';
import {EsnIconName} from "../icon/icons";

// Boilerplate for applying mixins to EsnStepHeader.
/** @docs-private */
const _EsnStepHeaderBase = mixinColor(
  class EsnStepHeaderBase extends CdkStepHeader {
    constructor(elementRef: ElementRef) {
      super(elementRef);
    }
  },
  'primary',
);

@Component({
  selector: 'esn-step-header',
  templateUrl: 'step-header.html',
  styleUrls: ['step-header.scss'],
  host: {
    'class': 'esn-step-header',
    'role': 'tab',
  },
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class EsnStepHeader
  extends _EsnStepHeaderBase
  implements AfterViewInit, OnDestroy, CanColor
{
  private _intlSubscription: Subscription;

  /** State of the given step. */
  @Input() state: StepState;

  /** Label of the given step. */
  @Input() label: EsnStepLabel | string;

  /** Error message to display when there's an error. */
  @Input() errorMessage: string;

  /** Overrides for the header icons, passed in via the stepper. */
  @Input() iconOverrides: {[key: string]: TemplateRef<EsnStepperIconContext>};

  /** Index of the given step. */
  @Input() index: number;

  /** Whether the given step is selected. */
  @Input() selected: boolean;

  /** Whether the given step label is active. */
  @Input() active: boolean;

  /** Whether the given step is optional. */
  @Input() optional: boolean;

  /** Whether the ripple should be disabled. */
  @Input() disableRipple: boolean;

  /** Customized esn icon */
  @Input() esnIcon: EsnIconName;

  /** Mobile mode flag */
  @Input() isMobile = false;

  /** number of steps of the given step. */
  @Input() stepsNumber: number | undefined;

  constructor(
    public _intl: EsnStepperIntl,
    private _focusMonitor: FocusMonitor,
    _elementRef: ElementRef<HTMLElement>,
    changeDetectorRef: ChangeDetectorRef,
  ) {
    super(_elementRef);
    this._intlSubscription = _intl.changes.subscribe(() => changeDetectorRef.markForCheck());
  }

  ngAfterViewInit() {
    this._focusMonitor.monitor(this._elementRef, true);
  }

  ngOnDestroy() {
    this._intlSubscription.unsubscribe();
    this._focusMonitor.stopMonitoring(this._elementRef);
  }

  /** Focuses the step header. */
  override focus(origin?: FocusOrigin, options?: FocusOptions) {
    if (origin) {
      this._focusMonitor.focusVia(this._elementRef, origin, options);
    } else {
      this._elementRef.nativeElement.focus(options);
    }
  }

  /** Returns string label of given step if it is a text label. */
  _stringLabel(): string | null {
    return this.label instanceof EsnStepLabel ? null : this.label;
  }

  /** Returns EsnStepLabel if the label of given step is a template label. */
  _templateLabel(): EsnStepLabel | null {
    return this.label instanceof EsnStepLabel ? this.label : null;
  }

  /** Returns the host HTML element. */
  _getHostElement() {
    return this._elementRef.nativeElement;
  }

  /** Template context variables that are exposed to the `esnStepperIcon` instances. */
  _getIconContext(): EsnStepperIconContext {
    return {
      index: this.index,
      active: this.active,
      optional: this.optional,
    };
  }

  _getDefaultTextForState(state: StepState, icon: EsnIconName): string {
    if (!icon && state == 'number' || state == 'edit') {
      return `${this.index + 1}`;
    }
    if(!!icon) {
      return icon;
    }
    // if (state == 'edit') {
    //   return 'edit-2';
    // }
    if (state == 'error') {
      return 'warning';
    }
    return state;
  }
}
