import { NgModule } from '@angular/core';
import { EsnRecycleRows, EsnTable } from './table';
import { CdkTableModule } from '@angular/cdk/table';
import {
  EsnCell,
  EsnCellDef,
  EsnColumnDef,
  EsnFooterCell,
  EsnFooterCellDef,
  EsnHeaderCell,
  EsnHeaderCellDef,
} from './cell';
import {
  EsnFooterRow,
  EsnFooterRowDef,
  EsnHeaderRow,
  EsnHeaderRowDef,
  EsnRow,
  EsnRowDef,
  EsnNoDataRow,
} from './row';
import { EsnTextColumn } from './text-column';
import { MatCommonModule } from '@angular/material/core';

import { MatTableModule } from '@angular/material/table';
import { EsnPaginatorModule } from '../paginator/paginator.module';
import { EsnAsyncTable } from './async-table/async-table.component';
import { CommonModule } from '@angular/common';
import { ESN_ASYNC_DATASOURCE_DEFAULT_ADAPTER, EsnAsyncDataSourceAdapter, EsnAsyncDatasourceCallWrapperAdapter } from './async-table/esnAsyncTableAdapter';
import { EsnAsyncDataSourceFactory } from './async-table/esnAsyncDataSourceFactory';



const EXPORTED_DECLARATIONS = [
  // Table
  EsnTable,
  EsnRecycleRows,

  // Template defs
  EsnHeaderCellDef,
  EsnHeaderRowDef,
  EsnColumnDef,
  EsnCellDef,
  EsnRowDef,
  EsnFooterCellDef,
  EsnFooterRowDef,

  // Cell directives
  EsnHeaderCell,
  EsnCell,
  EsnFooterCell,

  // Row directives
  EsnHeaderRow,
  EsnRow,
  EsnFooterRow,
  EsnNoDataRow,

  EsnTextColumn,

  //Async table
  EsnAsyncTable,
];


@NgModule({
  imports: [
    CommonModule,
    CdkTableModule,
    MatCommonModule,
    MatTableModule,
    // Only for Async table
    EsnPaginatorModule,
  ],
  exports: [MatCommonModule, EXPORTED_DECLARATIONS],
  declarations: EXPORTED_DECLARATIONS,
  providers:[
    {provide: EsnAsyncDataSourceAdapter, useClass: ESN_ASYNC_DATASOURCE_DEFAULT_ADAPTER},
    EsnAsyncDataSourceFactory
  ]
})
export class EsnTableModule {}

