import { expand, forkJoin, last, map, Observable, of, switchMap, takeWhile } from 'rxjs';
import { ApiCallWrapper, ApiErrorDisplay, ApiResponse, ListWrapperDto } from '../model/public-api';
import { EsnUtils } from './utils';
import { CustomHttpUrlEncodingCodec } from './controllers-configuration/encoder';
import {
  HttpParams,
} from '@angular/common/http';

export class EsnApiUtils {
  static async retry(
    func: Function,
    condition: Function = () => true,
    stepName: string = '',
    bodySent: any = 'Unavailable',
    retries: number = 2,
    interval: number = 5000
  ) {
    const stepNamePrecision = stepName ? ' for ' + stepName : '';
    let count = 0;
    let resp;
    let error;
    while (count < retries) {
      try {
        resp = await func();
        if (condition(resp)) {
          return resp;
        } else {
          console.log(
            `Call number ${
              count + 1
            } failed${stepNamePrecision}: Condition unfullfilled`
          );
          console.log(resp);
        }
      } catch (err) {
        error = err;
        console.log(
          `Call number ${
            count + 1
          } failed${stepNamePrecision} with following error:`
        );
        console.log(err);
      }
      count++;
      await EsnUtils.sleep(interval);
    }

    console.log(
      `Call failed ${stepNamePrecision} after ${retries} retries. Stopping Synchro:`
    );
    throw {
      message: `Api call error: Call failed${stepNamePrecision}`,
      responseReceived: resp,
      error,
      bodySent,
    };
  }

  static getErrorMessage(error: any, errorType: string = ''): ApiErrorDisplay {
    if (!navigator.onLine) {
      return {
        message: `Merci de vérifier votre connexion et réessayer.`,
        retryable: true,
      };
    }
    const code = error?.status || error?.error?.status;
    if (!code) {
      return {
        message: `Une erreur technique s'est produite, merci de contacter le support.`,
      };
    }
    switch (code) {
      case 400:
        return {
          message: `Requête rejetée par le serveur, merci de contacter le support.`,
          code,
        };
      case 401:
        return {
          message: `Votre authentification Gardian a expiré, merci de vous reconnecter.`,
        };
      case 403:
        return {
          message: `L'accès à cette ressource n'est pas autorisé. Merci de contacter le support.`,
          code,
        };
      case 409:
        return {
          message: `Un autre utilisateur à apporté une modification. Veuillez rafraîchir la page 
          pour visualiser sa modification avant d'effectuer votre opération.`,
          code,
        };
      case 500:
        return {
          message: `Une erreur non gérée s'est produite, merci de contacter le support.`,
          code,
        };
      case 503:
        return {
          message: `Le service est temporairement indisponible, merci de réessayer ultérieurement.`,
          retryable: true,
          code,
        };
      default:
        return {
          message: `Une erreur technique s'est produite, merci de contacter le support.`,
          code,
        };
    }
  }


  static async fetchAllElementsWithCallWrapper(
    callFunc: (page: number, size: number) => Promise<ApiCallWrapper>
  ): Promise<ApiCallWrapper> {
    let allElements: any[] = [];
    let errorDisplay = null;
    let batchSize = 50;
    let page = 0;
    let totalNumberOfResults = 1;
    while (
      page == 0 ||
      (!errorDisplay && page * batchSize < totalNumberOfResults && page < 200)
    ) {
      const resp = await callFunc(page, batchSize);
      if (!!resp.resp?.results) {
        allElements = allElements.concat(resp.resp.results);
        totalNumberOfResults = resp.resp.totalNumberOfResults;
      }
      errorDisplay = resp.error;
      page = page + 1;
      console.log(allElements);
    }

    return { error: errorDisplay as ApiErrorDisplay | undefined, resp: allElements };
  }

  static fetchAllElementsWithoutCallWrapper(
    callFunc: (
      page: number,
      size: number
    ) => Observable<ApiResponse<ListWrapperDto<any>>>
  ): Observable<any> {
    const batchSize = 50;
    return of({
      allElements: [] as any[],
      page: 0,
      totalNumberOfResults: 1,
    }).pipe(
      expand((data) =>
        callFunc(data.page, batchSize).pipe(
          map((resp) => ({
            allElements: data.allElements.concat(resp.content!.results),
            page: data.page + 1,
            totalNumberOfResults: resp.content?.totalNumberOfResults,
          }))
        )
      ),
      takeWhile(
        (data) =>
          (data.page - 1) * batchSize < data.totalNumberOfResults! &&
          data.page < 200
      ),
      map((data) => data.allElements),
      last()
    );
  }

  // Resolves when all calls resolve
  // with resp = array of all resp
  // and error = first error found
  static async forkJoinCallWrappers(
    calls: Promise<ApiCallWrapper>[]
  ): Promise<ApiCallWrapper> {
    const results = await Promise.all(calls);
    return {
      resp: results.map((res) => res.resp),
      error: results.map((res) => res.error).find((err) => !!err),
    };
  }

  static forkJoinXByX(calls: Observable<any>[], batchSize: number = 3) {
    let call = of([]) as Observable<any>;
    for (let i = 0; batchSize * i < calls.length; i++) {
      call = call.pipe(
        switchMap((acc) => {
          const batchToDo = [];
          for (
            let index = i * 3;
            index < calls.length && index < (i + 1) * batchSize;
            index++
          ) {
            batchToDo.push(calls[index]);
          }

          return forkJoin(batchToDo).pipe(
            map((resp) => {
              resp.forEach((r) => acc.push(r));

              return acc;
            })
          );
        })
      );
    }

    return call;
  }


  static makeQueryParameters(queryParams: {[key in string]: any}): HttpParams{
    let queryParameters = new HttpParams({
      encoder: new CustomHttpUrlEncodingCodec(),
    });

    Object.entries(queryParams).forEach(([paramName, paramVal]) =>{
      if (paramVal != null && paramVal !== '' &&  !(Array.isArray(paramVal) && !paramVal.length)) {
        queryParameters = queryParameters.set(paramName, <any>paramVal);
      }
    })
    
    return queryParameters;
  }

}
