import { Injectable, Optional } from '@angular/core';
import { Observable } from 'rxjs';
import { catchError } from 'rxjs/operators';
import {
  ApiCallWrapper,
  ApiCallWrapperOptions,
  EsnApiUtils,
  EsnUtils,
} from '../../public-api';
import { EsnApiCallWrapperNotificationService } from './api-call-wrapper-notifications';
import { EsnUploadStateService } from '../filemanager/upload-state.service';

@Injectable()
export class EsnApiCallWrapper {
  constructor(
    public notificationService: EsnApiCallWrapperNotificationService,
    @Optional() public uploadStateService: EsnUploadStateService
  ) {}

  public async makeApiCall(
    apiCall: Observable<any>,
    options: ApiCallWrapperOptions = {}
  ): Promise<ApiCallWrapper> {
    const usedOptions: ApiCallWrapperOptions = {
      callLabel: '',
      notifyError: false,
      notifySuccess: false,
      successLabel: '',
      uploadToaster: false,
    };
    Object.assign(usedOptions, options);

    const shouldTriggerUploadStateChange =
      !!usedOptions.uploadToaster && !!this.uploadStateService;
    if (usedOptions.uploadToaster && !this.uploadStateService) {
      console.warn(
        'Option "uploadToaster" was passed to the ApiCallWrapper, but this option is supposed to be used with the EsnFilemanagerModule which is not imported.'
      );
    }

    return new Promise((resolve) => {
      this.uploadStateService.uploadStarts(shouldTriggerUploadStateChange);
      
      apiCall
        .pipe(
          catchError((err) => {
            const error = EsnApiUtils.getErrorMessage(
              err,
              usedOptions.callLabel
            );
            if (usedOptions.notifyError) {
              this.notificationService.showNotif(
                'error',
                `Erreur lors de l${
                  ['a', 'e', 'i', 'o', 'u', 'y'].includes(
                    usedOptions.callLabel!.charAt(0)
                  )
                    ? "'"
                    : 'a '
                }${usedOptions.callLabel}`,
                error.message
              );
            }
            resolve({
              error,
            });
              this.uploadStateService.uploadEnds(shouldTriggerUploadStateChange);
            
            throw err;
          })
        )
        .subscribe((resp) => {
          if (usedOptions.notifySuccess) {
            this.notificationService.showNotif(
              'success',
              options.successLabel ||
                `${EsnUtils.capitalize(usedOptions.callLabel!)} réussie`,
              ''
            );
          }

          resolve({ resp: resp?.content || resp });
            this.uploadStateService.uploadEnds(shouldTriggerUploadStateChange);
          
        });
    });
  }
}
