import {
  Component,
  HostListener,
  OnInit,
  TemplateRef,
  ViewChild,
  ViewContainerRef,
} from '@angular/core';

import { ActivatedRoute, Data } from '@angular/router';
import { EsnDialog } from '../dialog/dialog.service';

interface DataWithContentTemplate {contentTemplate: any};

@Component({
  selector: 'esn-dialog-entry',
  templateUrl: './dialog-entry.component.html',
})
export class EsnDialogEntry implements OnInit {
  @ViewChild(TemplateRef, { static: true }) templateRef: TemplateRef<any>;
  data: Data;

  constructor(
    public dialog: EsnDialog,
    public activatedRoute: ActivatedRoute,
    //public routerState: RouterStateService, // TODO: allow providing a service to store the previous router state
    private viewContainerRef: ViewContainerRef
  ) {}

  @HostListener('window:popstate', ['$event'])
  onPopState(): void {
    // Browser back/forward button pressed
    this.openDialog(this.data);
  }

  ngOnInit(): void {
    // TODO: allow providing a service to store the previous router state
    // this.routerState.dialogEntryRouterParams$ = this.activatedRoute.params;

    this.activatedRoute.data.subscribe((data) => {
      this.data = data;
      this.openDialog(data);
    });
  }

  public openDialog(data: any) {
    const dialogRef = this.dialog.open(data.child, {
      viewContainerRef: this.viewContainerRef,
      panelClass: data.panelClass,
      fullScreenDialog: data.fullScreenDialog,
      autoFocus: false,
      data: {},
    });
    (dialogRef.componentInstance as DataWithContentTemplate)['contentTemplate'] = this.templateRef;
  }
}

