import {
  animate,
  state,
  style,
  transition,
  trigger,
} from '@angular/animations';
import { Component, OnInit } from '@angular/core';
import { EsnUploadStateService } from '../../utils/services/filemanager/upload-state.service';

@Component({
  selector: 'esn-upload-progress-toaster',
  templateUrl: './upload-progress-toaster.component.html',
  styleUrls: ['./upload-progress-toaster.component.scss'],
  animations: [
    trigger('enteringAnimation', [
      state(
        'displayed',
        style({
          opacity: 1,
          transform: 'translateX(0)',
        })
      ),
      state(
        'not-displayed',
        style({
          opacity: 0,
          transform: 'translateX(100%)',
        })
      ),
      transition('* => displayed', [animate('0.2s')]),
    ]),
  ],
  host: {
    class: 'esn-upload-progress-toaster',
  },
})
export class EsnUploadProgressToasterComponent implements OnInit {
  public progress: number = 0;

  public displayed: 'displayed' | 'not-displayed' = 'not-displayed';
  constructor(public uploadStateService: EsnUploadStateService) {}

  ngOnInit(): void {
    this.uploadStateService.documentsUploadProgress$.subscribe(
      (val: number | null) => (this.progress = val!)
    );

    this.uploadStateService
      .getDisplayUpload()
      .subscribe((val: boolean | null) => {
        this.displayed = val ? 'displayed' : 'not-displayed';
      });
  }
}
