import { Component, Input, OnChanges, SimpleChanges } from '@angular/core';
import { CountryService } from '../../utils/services/country/country.service';

@Component({
  selector: 'esn-flag',
  templateUrl: './esn-flag.component.html',
  styleUrls: ['./esn-flag.component.scss'],
  host: {
    class: 'esn-flag',
    '[class.esn-flag__circle]': `shape === 'circle' `,
    '[class.esn-flag__rectangle]': `shape === 'rectangle' `,
    '[class.esn-flag__square]': `shape === 'square' `,
  }
})
export class EsnFlagComponent implements OnChanges {
  @Input() shape: 'square' | 'circle' | 'rectangle' = 'circle';
  @Input() size: 'xxs' | 'xs' | 'sm' | 'md' | 'lg' | 'xl' = 'sm';
  @Input() country: string = 'France';

  countriesList: string[] = [];
  countryCode: string | undefined = 'fr';
  constructor(private countryService: CountryService) {
    this.countriesList = this.countryService.getCountriesList();
  }
  ngOnChanges(changes: SimpleChanges): void {
    if(changes['country']) {
      this.countryCode = this.countryService.getCountryCode(this.country);
    }
  }  
}
