import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { ApiResponse } from '../../utils/model/api';
import { BlobItem, DocumentItem } from '../../utils/model/documents';
import { EsnApiUtils } from '../../utils/utils/apiUtils';
import { EsnFileConverter } from '../../utils/utils/public-api';
import { EsnFileInputNotificationService } from './file-input-notification.service';

export abstract class EsnFileInputRemoteFMService {
  public abstract filesDownload(uuid: string): Observable<Blob>;
}

export abstract class EsnFileInputLocalDBFilesService {
  public abstract doesBlobExist(uuid: string): Promise<boolean>;
  public abstract getBlob(uuid: string): Promise<BlobItem>;
}

export class EsnFileInputNoLocalDBFilesService
  implements EsnFileInputLocalDBFilesService
{
  public async doesBlobExist(uuid: string): Promise<boolean> {
    return Promise.resolve(false);
  }
  public getBlob(uuid: string): Promise<BlobItem> {
    throw 'Method EsnFileInputNoLocalDBFilesService.getBlob should never be called';
  }
}

export class EsnFileInputNoRemoteFMService
  implements EsnFileInputRemoteFMService
{
  public filesDownload(uuid: string): Observable<Blob> {
    throw `Please provide an implementation for EsnFileInputRemoteFMService to be able to download files`;
  }
}

export abstract class EsnDocumentRetrievalService {
  public abstract documentDownload(document: DocumentItem): void;
}

@Injectable()
export class EsnLocalOrRemoteDocumentRetrievalService
  implements EsnDocumentRetrievalService
{
  constructor(
    public filesManagerController: EsnFileInputRemoteFMService,
    public filesService: EsnFileInputLocalDBFilesService,
    public notifService: EsnFileInputNotificationService
  ) {}

  public async documentDownload(document: DocumentItem) {
    if (document.metadata.uuid) {
      if (document.metadata.size === 0) {
        EsnFileConverter.saveFile(
          new Blob([]),
          document.metadata.filename || 'doc',
          document.metadata.contentType || ''
        );
        return;
      }
      if (
        !!document.metadata.externalUuid && 
        await this.filesService.doesBlobExist(document.metadata.externalUuid!)
      ) {
        this.dowloadFromLocalBase(document);
      } else {
        this.dowloadFromRemote(document);
      }
    }
  }

  public async dowloadFromLocalBase(document: DocumentItem) {
    const blobItem: BlobItem = await this.filesService.getBlob(
      document.metadata.externalUuid!
    );
    const blob = await EsnFileConverter.base64ToBlob(blobItem.blobString);
    EsnFileConverter.saveFile(
      blob,
      document.metadata.filename || 'doc',
      document.metadata.contentType || ''
    );
  }

  public dowloadFromRemote(document: DocumentItem) {
    this.filesManagerController
      .filesDownload(document.metadata.uuid!)
      .subscribe(
        (data: Blob) => {
          EsnFileConverter.saveFile(
            data,
            document.metadata.filename || 'doc',
            document.metadata.contentType || ''
          );
        },
        (error: ApiResponse<any>) => {
          this.notifService.showNotif(
            'error',
            'Impossible de récupérer le document',
            EsnApiUtils.getErrorMessage(error, 'téléchargement').message
          );
        }
      );
  }
}
