import {Component, TemplateRef, ViewChild} from '@angular/core';
import {MAT_SELECT_TRIGGER} from "@angular/material/select";

@Component({
  selector: 'esn-select-trigger',
  templateUrl: './select-trigger.component.html',
  styleUrls: ['./select-trigger.component.scss'],
  providers: [{provide: MAT_SELECT_TRIGGER, useExisting: EsnSelectTrigger}]
})
export class EsnSelectTrigger {
  @ViewChild(TemplateRef) optTempVar: TemplateRef<any>;
  get viewValue(): TemplateRef<any> {
    return this.optTempVar;
  }

}

