import {ChangeDetectionStrategy, Component, Input, ViewEncapsulation} from '@angular/core';
import {MatDivider} from "@angular/material/divider";
import {BooleanInput, coerceBooleanProperty} from "@angular/cdk/coercion";

@Component({
  selector: 'esn-divider',
  templateUrl: './divider.component.html',
  styleUrls: ['./divider.component.scss'],
  host: {
    'role': 'separator',
    '[attr.aria-orientation]': 'vertical ? "vertical" : "horizontal"',
    '[class.esn-divider-vertical]': 'vertical',
    '[class.esn-divider-horizontal]': '!vertical',
    '[class.esn-divider-inset]': 'inset',
    'class': 'esn-divider',
  },
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class EsnDivider {
  /** Whether the divider is vertically aligned. */
  @Input()
  get vertical(): boolean {
    return this._vertical;
  }
  set vertical(value: BooleanInput) {
    this._vertical = coerceBooleanProperty(value);
  }
  private _vertical: boolean = false;

  /** Whether the divider is an inset divider. */
  @Input()
  get inset(): boolean {
    return this._inset;
  }
  set inset(value: BooleanInput) {
    this._inset = coerceBooleanProperty(value);
  }
  private _inset: boolean = false;
}
