import {
  AfterViewInit,
  Component,
  ElementRef,
  EventEmitter,
  Inject,
  Input,
  OnInit,
  Output,
  ViewChild,
} from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import {
  BlobItem,
  CustomFileMetadataDto,
  DocumentEventEmitter,
  DocumentEventType,
  DocumentItem,
  FileMetadataDto,
} from '../../utils/model/documents';
import { EsnFileConverter, EsnUtils } from '../../utils/utils/public-api';

import { EsnDocumentRetrievalService } from './document-retrieval.service';
import { EsnFileInputNotificationService } from './file-input-notification.service';
import { FileSizePipe } from './file-size.pipe';
import { EsnFileInputConfig } from './fileInputConfig';
import { FILE_MANAGER_PROVIDER } from '../../utils/services/filemanager/filemanager-configuration';

@Component({
  selector: 'esn-file-input',
  templateUrl: './file-input.component.html',
  styleUrls: ['./file-input.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: EsnFileInput,
      multi: true,
    },
  ],
  host: {
    class: 'esn-file-input',
    '[class.esn-file-input-disabled]': `_disabled`
  }
})
export class EsnFileInput implements ControlValueAccessor {
  public selectorNumber: string = '';
  @Input() maxSize?: number;
  @Input() maxCount?: number;
  @Input() acceptedTypes?: string[];
  @Input() cardMode = true;
  @Input() selectorText?: string;
  @Input() multi: boolean = true;
  @Input() processAddedBlobs: (blobItems: BlobItem[], fileMetadata: FileMetadataDto[]) => Promise<boolean> =
    () => Promise.resolve(true);
  @Input() processDeletedDoc: (
    deletedDoc: CustomFileMetadataDto
  ) => Promise<boolean> = () => Promise.resolve(true);

  @Output() documentDelete: EventEmitter<CustomFileMetadataDto> =
    new EventEmitter<CustomFileMetadataDto>();
  @Output() documentsAdd: EventEmitter<CustomFileMetadataDto[]> =
    new EventEmitter<CustomFileMetadataDto[]>();

  onTouch: any = () => {};
  onChange: any = () => {};

  public _documents: DocumentItem[] = [];
  public documents: FileMetadataDto[] | FileMetadataDto | null = [];

  constructor(
    public docRetrievalService: EsnDocumentRetrievalService,
    public notificationService: EsnFileInputNotificationService,
    public fileSizePipe: FileSizePipe,
    public elementRef: ElementRef,
    public config: EsnFileInputConfig,
    @Inject(FILE_MANAGER_PROVIDER) public PROVIDER?: any,
  ) {}

  
  writeValue(obj: any): void {
    this.documents = !this.multi ? obj :  obj || [];
    this._updateConvertedDocs();
  }
  registerOnChange(fn: any): void {
    this.onChange = fn;
  }
  registerOnTouched(fn: any): void {
    this.onTouch = fn;
  }
  setDisabledState(isDisabled: boolean) {
    this.readOnly = isDisabled;
    this._updateActionsPermissions();
  }

  public async onDocumentsAdd(docs: CustomFileMetadataDto[]) {
    this.documentsAdd.emit(docs);

    this._loading = true;
    const blobItems: BlobItem[] =
      await EsnFileConverter.customMetadataDtosToBlobItems(docs);

    docs.forEach((fm) => delete fm.blob);
    const processingOk = await this.processAddedBlobs(blobItems, docs);

    if (processingOk) {
      this.addDocsToValue(docs);
      this._updateConvertedDocs();
      this.onChange(this.documents);
    }

    this._loading = false;
  }

  public async onDocumentsDelete(deletedDoc: FileMetadataDto) {
    this.documentDelete.emit(deletedDoc);

    this._loading = true;

    const processingOk = await this.processDeletedDoc(deletedDoc);

    if (processingOk) {
      this.removeDocFromValue(deletedDoc);
      this._updateConvertedDocs();
      this.onChange(this.documents);
    }

    this._loading = false;
  }


  get _maxSize() {
    return this.maxSize || this.config.FILE_MAX_SIZE;
  }

  get _maxCount() {
    return this.multi ? (this.maxCount || this.config.MAX_NB_FILES) : 1;
  }

  public readOnly: boolean = false;

  @Input() set disabled(value: boolean) {
    if (value === null) {
      return;
    }
    this.readOnly = value;
    this._updateActionsPermissions();
  }

  public _loading: boolean = false;

  @Input() set loading(value: boolean) {
    if (value === null) {
      return;
    }
    this._loading = value;
  }

  public onDocumentEvent(e: DocumentEventEmitter): void {
    switch (e.event) {
      case DocumentEventType.DOWNLOAD:
      case DocumentEventType.VIEW:
        this.handleDocumentDownload(e);
        break;
      case DocumentEventType.DELETE:
        this.onDocumentsDelete(e.document.metadata as CustomFileMetadataDto);
        break;
    }
  }

  public async onFilesAdd(files: File[]): Promise<void> {
    let fileArray = this.convertToFileArray(files);

    if (fileArray.some((file) => !this.checkFileType(file))) {
      this.notificationService.showNotif(
        'info',
        'Format incorrect',
        `Certains documents n'avaient pas le format attendu (${this.acceptedFileTypesLabel}), les documents n'ont pas été ajoutés`
      );
      return;
    }

    if (fileArray.length + this._documents.length > this._maxCount) {
      this.notificationService.showNotif(
        'info',
        'Nombre de document dépassé',
        `Le nombre maximal de document (${this._maxCount}) est dépassé, les documents n'ont pas été ajoutés`
      );
      return;
    }


    let filesTooBig: File[] = [];
    let convertedFiles: CustomFileMetadataDto[] = [];

    for (const file of fileArray) {
      if (file.size > this._maxSize) {
        filesTooBig.push(file);
      } else {
        convertedFiles.push({
          externalUuid: EsnUtils.generateRandomUuid(),
          size: file.size,
          filename: file.name,
          contentType: file.type,
          fileProvider: this.PROVIDER || this.config.FILE_PROVIDER,
          blob: file,
        });
      }
    }

    if (convertedFiles.length > 0) {
      this.onDocumentsAdd(convertedFiles);
    }

    if (filesTooBig.length > 0) {
      let errorMessage: string = '';

      errorMessage += filesTooBig.reduce(
        (acc: string, val: File) =>
          acc +
          `- ${val.name} (${this.fileSizePipe.transform(val.size)}) </br>`,
        `Les fichiers suivants dépassent la taille maximale autorisée (${this.fileSizePipe.transform(
          this._maxSize
        )}) : </br>`
      );

      this.notificationService.showNotif(
        'info',
        'Fichiers volumineux',
        errorMessage
      );
    }
  }

  get acceptedFileTypesLabel(): string{
    return  (this.acceptedTypes || [])
      .map((t) => this.config.FileTypesMap[t].label)
      .join(', ')
  }




  _updateConvertedDocs() {
    this._documents = this.getDocsValAsList().map(
      (x: CustomFileMetadataDto | FileMetadataDto) =>
      EsnFileConverter.convertFileMetaDataToDocument(x, false, {
          delete: !this.readOnly,
          download: true,
          view: false,
          upload: false,
        })
    );
  }

  public _updateActionsPermissions(): void {
    this._documents.forEach((doc) => {
      doc.actions = {
        delete: !this.readOnly,
        download: true,
        view: false,
        upload: false,
      };
    });
  }

  public handleDocumentDownload(e: DocumentEventEmitter) {
    this.docRetrievalService.documentDownload(e.document);
  }

  getAcceptedFileTypes(): string[] {
    return this.acceptedTypes
      ? this.acceptedTypes.reduce((acc: string[], curr: string) => {
          this.config.FileTypesMap[curr].type.forEach((t) => acc.push(t));
          return acc;
        }, [])
      : [];
  }

  get acceptedFileTypes(): string {
    return !!this.acceptedTypes && !!this.acceptedTypes.length
      ? this.getAcceptedFileTypes().join(',')
      : '*';
  }

  public checkFileType(file: File): boolean {
    if (!this.acceptedTypes) {
      return true;
    }
    return this.getAcceptedFileTypes().includes(file.type);
  }

  _checkAcceptedTypesValidity() {
    const validTypes = Object.keys(this.config.FileTypesMap);
    const invalidTypes = this.acceptedTypes?.filter(
      (t) => !validTypes.includes(t)
    );
    if (!!invalidTypes?.length) {
      console.error(
        `Le(s) type(s) de document(s): '${invalidTypes.join(
          ', '
        )}' ne sont pas définis dans la configuration. Aucun contrôle sur le type des documents ne sera réalisé.`
      );
      this.acceptedTypes = [];
    }
  }

  convertToFileArray(files: File[]): File[] {
    const array = [];
    for (const file of files) {
      array.push(file);
    }
    return array;
  }



  public getDocsValAsList(): FileMetadataDto[] {
    if(!this.documents){
      return []
    }
    return this.multi ? this.documents as FileMetadataDto[] : [this.documents as FileMetadataDto]
  }

  public addDocsToValue(docs: FileMetadataDto[]): void {
    if(this.multi){
      (this.documents as FileMetadataDto[]).push(...docs);
    } else {
      !docs.length || (this.documents = docs[0])
    }
  }

  public removeDocFromValue(doc: FileMetadataDto): void {
    if(this.multi){
      this.documents = (this.documents as FileMetadataDto[]).filter(
        (mtd: FileMetadataDto) => !(!!mtd.externalUuid && mtd.externalUuid == doc.externalUuid) && !(!!mtd.uuid && mtd.uuid == doc.uuid)
      );
    } else {
      this.documents = null;
    }
  }
}
