import {
  AfterContentInit,
  Component,
  ContentChildren,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  QueryList,
  SimpleChanges,
} from '@angular/core';
import { EsnSelectableCard } from './selectable-card/selectable-card';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import { Subscription } from 'rxjs';
import { EsnUtils } from '../../utils/utils/utils';

@Component({
  selector: 'esn-selectable-card-group',
  templateUrl: './selectable-card-group.html',
  styleUrls: ['./selectable-card.scss'],
  host: {
    class: 'esn-selectable-card-group',
  },
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: EsnSelectableCardGroup,
      multi: true,
    },
  ],
})
export class EsnSelectableCardGroup
  implements ControlValueAccessor, AfterContentInit, OnChanges, OnDestroy
{
  @Input() multiple: boolean | string = false;
  @Input() radioBtnMode: boolean = false;

  @ContentChildren(EsnSelectableCard, { descendants: true })
  cards: QueryList<EsnSelectableCard>;

  value: any;
  onChange: any = () => {};
  onTouch: any = () => {};
  subs: Subscription = new Subscription();
  contentInitialized: boolean = false;
  multi: boolean = false;

  constructor() {}

  ngOnChanges(changes: SimpleChanges): void {
    if (!!changes['multiple']) {
      this.multi = !!this.multiple || this.multiple === '';
    }

    if (!!changes['radioBtnMode']) {
      this.cards?.forEach((card) => {
        card.radioBtnModeByCardGroup = this.radioBtnMode;
      });
    }
  }

  ngAfterContentInit(): void {
    this.contentInitialized = true;
    this.cards.forEach((card) => {
      this._listenForCardSelectionChanges(card);
    });

    if (this.radioBtnMode != null) {
      this.cards.forEach((card) => {
        card.radioBtnModeByCardGroup = this.radioBtnMode;
      });
    }
  }

  async writeValue(val: any): Promise<void> {
    await this._waitUntilContentInit();
    this.value = this.multi ? val || [] : val;
    this.cards.forEach(
      (card) =>
        (card.selected = this.multi
          ? this.value.includes(card.value)
          : card.value === this.value)
    );
  }
  registerOnChange(fn: any) {
    this.onChange = fn;
  }

  registerOnTouched(onTouched: Function) {
    this.onTouch = onTouched;
  }

  async setDisabledState(isDisabled: boolean): Promise<void> {
    await this._waitUntilContentInit();
    this.cards.forEach((card) => (card.disabledByCardGroup = isDisabled));
  }

  _listenForCardSelectionChanges(card: EsnSelectableCard) {
    this.subs.add(
      card.selectionChange.subscribe((selected) => {
        let newValue;
        if (!this.multi) {
          newValue = selected ? card.value : null;
        } else if (selected) {
          newValue = [...this.value];
          newValue.push(card.value);
        } else {
          newValue = this.value.filter((val: any) => val !== card.value);
        }
        this.writeValue(newValue);
        this.onChange(newValue);
      })
    );
  }

  async _waitUntilContentInit() {
    while (!this.contentInitialized) {
      await EsnUtils.sleep(10);
    }
  }

  ngOnDestroy(): void {
    this.subs.unsubscribe();
  }
}
