import { Component, Input, TemplateRef } from '@angular/core';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import { EsnAutocompleteSelectImplementationBase } from '../autocomplete-select/base/autocomplete-select-base.component';
import { EdfApplicationUser } from '../../utils/model/edfApplicationUser';

@Component({
  selector: 'esn-people-picker',
  templateUrl: './people-picker.component.html',
  styleUrls: ['./people-picker.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: EsnPeoplePicker,
      multi: true,
    },
  ],
})
export class EsnPeoplePicker extends EsnAutocompleteSelectImplementationBase<EdfApplicationUser> {
  @Input() filterFunc: (u: EdfApplicationUser) => boolean = (u) => true;
  @Input() override  idField: keyof EdfApplicationUser = "nni";
  @Input() override  label: string = "Rechercher un utilisateur...";
  @Input() optionDisabledTooltip: string = '';
  @Input() optionTemplate?: TemplateRef<any>;
  @Input() chipTemplate?: TemplateRef<any>;
  @Input() singleElemTemplate?: TemplateRef<any>;
  @Input() noResultsLabel: string = 'Aucun résultat trouvé';
}
