import { ChangeDetectorRef, Component, EventEmitter, Input, OnChanges, OnDestroy, Output, SimpleChanges } from '@angular/core';
import { CountryService } from '../../utils/services/country/country.service';
import { ControlValueAccessor, FormControl, FormGroup, NG_VALUE_ACCESSOR } from '@angular/forms';
import { Subscription } from 'rxjs';
@Component({
  selector: 'esn-country-selector',
  templateUrl: './country-selector.component.html',
  styleUrls: ['./country-selector.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: CountrySelectorComponent,
      multi: true,
    },
  ]
})
export class CountrySelectorComponent implements ControlValueAccessor, OnChanges, OnDestroy {
  @Input() highlight: string;
  @Input() selected: string;
  @Input() disabled = false;
  @Input() autocomplete = false;
  @Input() hint = '';
  @Input() idField: string;
  @Input() label = 'Chercher un pays';
  @Input() placeholder = "Chercher un pays";
  @Input() size: 'sm' | 'md' = 'sm';
  
  @Output() selectionChange: EventEmitter<string> = new EventEmitter<string>();

  public queryFunc: (txt: string) => Promise<any[]> = (txt: string) => Promise.resolve(this._countryService.searchCountry(txt, this.highlight));
  public optionTextFn = (country: string) => `${country}`;

  control: FormControl
  countriesList: string[] = [];
  countryToBeHighlighted: string;
  onChangeSub: Subscription = new Subscription();
  onChange: any = () => {};
  onTouch: any = () => {};

  constructor(private _countryService: CountryService, private cdr: ChangeDetectorRef) {
    this.countriesList = this._countryService.getCountriesList();
    this.control = new FormControl(null);
  }
  ngOnDestroy(): void {
    this.onChangeSub.unsubscribe();
  }

  writeValue(value: any): void {
    if (!!value) {
      this.control.setValue(value);
    }  
  }

  registerOnChange(fn: any): void {
    this.onChangeSub.add(this.control.valueChanges.subscribe(fn));
  }

  registerOnTouched(onTouched: Function) {
    this.onTouch = onTouched;
  }

  setDisabledState(isDisabled: boolean) {
    this.disabled = isDisabled;
    this.updateDisableState();
    this.cdr.detectChanges(); 
  }

  private updateDisableState(){
    this.disabled ? this.control.disable() : this.control.enable();
  }

  getCountryCode(country: string) {
    return this._countryService.getCountryCode(country);
  }

  _selectionChange(country: string) {
    this.selectionChange.emit(country);
  }
  
  ngOnChanges(changes: SimpleChanges): void {
    if(changes['highlight']) {
      const filtredCountriesList = this.countriesList.filter(c => {
        if(c.toLocaleLowerCase().includes(this.highlight.toLocaleLowerCase())){
          this.countryToBeHighlighted = c;
        }
        return !c.toLocaleLowerCase().includes(this.highlight.toLocaleLowerCase())
      });
        if(!!this.countryToBeHighlighted) 
          {
            this.countriesList = [this.countryToBeHighlighted,...filtredCountriesList]
          }
    }

    if(changes['selected'] && !!this.selected) {
      this.control.setValue(this.selected);
    }

    if(changes['disabled']) {
      this.updateDisableState();
    }
  }

}
