import {Directionality} from '@angular/cdk/bidi';
import {
  CdkStep,
  CdkStepper,
  StepContentPositionState,
  STEPPER_GLOBAL_OPTIONS,
  StepperOptions,
} from '@angular/cdk/stepper';
import {AnimationEvent} from '@angular/animations';
import {
  AfterContentInit,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ContentChild,
  ContentChildren,
  ElementRef,
  EventEmitter,
  forwardRef,
  Inject,
  Input,
  OnDestroy, OnInit,
  Optional,
  Output,
  QueryList,
  SkipSelf,
  TemplateRef,
  ViewChildren,
  ViewContainerRef,
  ViewEncapsulation,
} from '@angular/core';
import {AbstractControl, FormGroupDirective, NgForm} from '@angular/forms';
import {ErrorStateMatcher, ThemePalette} from '@angular/material/core';
import {TemplatePortal} from '@angular/cdk/portal';
import {Subject, Subscription} from 'rxjs';
import {takeUntil, distinctUntilChanged, map, startWith, switchMap} from 'rxjs/operators';

import {EsnStepHeader} from './step-header';
import {EsnStepLabel} from './step-label';
import {
  DEFAULT_HORIZONTAL_ANIMATION_DURATION,
  DEFAULT_VERTICAL_ANIMATION_DURATION,
  esnStepperAnimations,
} from './stepper-animations';
import {EsnStepperIcon, EsnStepperIconContext} from './stepper-icon';
import {EsnStepContent} from './step-content';
import {EsnIconName} from "../icon/icons";
import {BreakpointObserver, BreakpointState} from "@angular/cdk/layout";
import { EsnUtils } from '../../utils/public-api';

@Component({
  selector: 'esn-step',
  templateUrl: 'step.html',
  providers: [
    {provide: ErrorStateMatcher, useExisting: EsnStep},
    {provide: CdkStep, useExisting: EsnStep},
  ],
  encapsulation: ViewEncapsulation.None,
  exportAs: 'esnStep',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class EsnStep extends CdkStep implements ErrorStateMatcher, AfterContentInit, OnDestroy {
  private _isSelected = Subscription.EMPTY;

  /** Content for step label given by `<ng-template esnStepLabel>`. */
  @ContentChild(EsnStepLabel) override stepLabel: EsnStepLabel;

  /** Esn Icons */
  @Input() esnIcon: EsnIconName;

  /** Content that will be rendered lazily. */
  @ContentChild(EsnStepContent, {static: false}) _lazyContent: EsnStepContent;

  /** Currently-attached portal containing the lazy content. */
  _portal: TemplatePortal;

  constructor(
    @Inject(forwardRef(() => EsnStepper)) stepper: EsnStepper,
    @SkipSelf() private _errorStateMatcher: ErrorStateMatcher,
    private _viewContainerRef: ViewContainerRef,
    @Optional() @Inject(STEPPER_GLOBAL_OPTIONS) stepperOptions?: StepperOptions,
  ) {
    super(stepper, stepperOptions);
  }

  ngAfterContentInit() {
    this._isSelected = this._stepper.steps.changes
      .pipe(
        switchMap(() => {
          return this._stepper.selectionChange.pipe(
            map(event => event.selectedStep === this),
            startWith(this._stepper.selected === this),
          );
        }),
      )
      .subscribe(isSelected => {
        if (isSelected && this._lazyContent && !this._portal) {
          this._portal = new TemplatePortal(this._lazyContent._template, this._viewContainerRef!);
        }
      });
  }

  ngOnDestroy() {
    this._isSelected.unsubscribe();
  }

  /** Custom error state matcher that additionally checks for validity of interacted form. */
  isErrorState(control: AbstractControl | null, form: FormGroupDirective | NgForm | null): boolean {
    const originalErrorState = this._errorStateMatcher.isErrorState(control, form);

    // Custom error state checks for the validity of form that is not submitted or touched
    // since user can trigger a form change by calling for another step without directly
    // interacting with the current form.
    const customErrorState = !!(control && control.invalid && this.interacted);

    return originalErrorState || customErrorState;
  }
}

@Component({
  selector: 'esn-stepper, esn-vertical-stepper, esn-horizontal-stepper, [esnStepper]',
  exportAs: 'esnStepper, esnVerticalStepper, esnHorizontalStepper',
  templateUrl: 'stepper.html',
  styleUrls: ['stepper.scss'],
  inputs: ['selectedIndex'],
  host: {
    '[class.esn-stepper-horizontal]': 'orientation === "horizontal"',
    '[class.esn-stepper-vertical]': 'orientation === "vertical"',
    '[class.esn-stepper-label-position-end]':
      'orientation === "horizontal" && labelPosition == "end"',
    '[class.esn-stepper-label-position-bottom]':
      'orientation === "horizontal" && labelPosition == "bottom"',
    '[class.esn-stepper-header-position-bottom]': 'headerPosition === "bottom"',
    '[attr.aria-orientation]': 'orientation',
    'role': 'tablist',
  },
  animations: [
    esnStepperAnimations.horizontalStepTransition,
    esnStepperAnimations.verticalStepTransition,
  ],
  providers: [{provide: CdkStepper, useExisting: EsnStepper}],
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class EsnStepper extends CdkStepper implements AfterContentInit, OnInit, OnDestroy {
  /** The list of step headers of the steps in the stepper. */
  @ViewChildren(EsnStepHeader) override _stepHeader: QueryList<EsnStepHeader>;

  /** Full list of steps inside the stepper, including inside nested steppers. */
  @ContentChildren(EsnStep, {descendants: true}) override _steps: QueryList<EsnStep>;

  /** Steps that belong to the current stepper, excluding ones from nested steppers. */
  override readonly steps: QueryList<EsnStep> = new QueryList<EsnStep>();

  /** Custom icon overrides passed in by the consumer. */
  @ContentChildren(EsnStepperIcon, {descendants: true}) _icons: QueryList<EsnStepperIcon>;

  /** Event emitted when the current step is done transitioning in. */
  @Output() readonly animationDone: EventEmitter<void> = new EventEmitter<void>();

  /** Whether ripples should be disabled for the step headers. */
  @Input() disableRipple: boolean;

  /**
   * Whether the label should display in bottom or end position.
   * Only applies in the `horizontal` orientation.
   */
  @Input()
  labelPosition: 'bottom' | 'end' = 'end';

  /**
   * Position of the stepper's header.
   * Only applies in the `horizontal` orientation.
   */
  @Input()
  headerPosition: 'top' | 'bottom' = 'top';

  /**
   * Detach the stepper content.
   * Only applies in the `vertical` orientation.
   */
  @Input()
  detachContent: boolean = false;

  /** Consumer-specified template-refs to be used to override the header icons. */
  _iconOverrides: Record<string, TemplateRef<EsnStepperIconContext>> = {};

  /** Stream of animation `done` events when the body expands/collapses. */
  readonly _animationDone = new Subject<AnimationEvent>();

  /** Duration for the animation. Will be normalized to milliseconds if no units are set. */
  @Input()
  get animationDuration(): string {
    return this._animationDuration;
  }
  set animationDuration(value: string) {
    this._animationDuration = /^\d+$/.test(value) ? value + 'ms' : value;
  }
  private _animationDuration = '';
  private onChangeSub: Subscription = new Subscription();
  isMobile = false;

  constructor(
    @Optional() dir: Directionality,
    changeDetectorRef: ChangeDetectorRef,
    elementRef: ElementRef<HTMLElement>,
    private breakpointObserver: BreakpointObserver,
    private cdr: ChangeDetectorRef) {
    super(dir, changeDetectorRef, elementRef);
    const nodeName = elementRef.nativeElement.nodeName.toLowerCase();
    this.orientation = nodeName === 'esn-vertical-stepper' ? 'vertical' : 'horizontal';
  }

  ngOnInit(): void {
    // detect screen size changes
    this.onChangeSub.add(this.breakpointObserver.observe([
      `(max-width: ${EsnUtils.BREAKPOINTS.mobile})`
    ]).subscribe((result: BreakpointState) => {
      this.isMobile = result.matches;
      this.cdr.detectChanges();
    }));
  }

  override ngOnDestroy() {
    this.onChangeSub.unsubscribe();
  }

  override ngAfterContentInit() {
    super.ngAfterContentInit();
    this._icons.forEach(({name, templateRef}) => (this._iconOverrides[name] = templateRef));

    // Mark the component for change detection whenever the content children query changes
    this.steps.changes.pipe(takeUntil(this._destroyed)).subscribe(() => {
      this._stateChanged();
    });

    this._animationDone
      .pipe(
        // This needs a `distinctUntilChanged` in order to avoid emitting the same event twice due
        // to a bug in animations where the `.done` callback gets invoked twice on some browsers.
        // See https://github.com/angular/angular/issues/24084
        distinctUntilChanged((x, y) => x.fromState === y.fromState && x.toState === y.toState),
        takeUntil(this._destroyed),
      )
      .subscribe(event => {
        if ((event.toState as StepContentPositionState) === 'current') {
          this.animationDone.emit();
        }
      });
  }

  _stepIsNavigable(index: number, step: EsnStep): boolean {
    return step.completed || this.selectedIndex === index || !this.linear;
  }

  _getAnimationDuration() {
    if (this.animationDuration) {
      return this.animationDuration;
    }

    return this.orientation === 'horizontal'
      ? DEFAULT_HORIZONTAL_ANIMATION_DURATION
      : DEFAULT_VERTICAL_ANIMATION_DURATION;
  }
}
