
import { DOCUMENT } from '@angular/common';
import {
  Component,
  ElementRef,
  Inject,
  Input,
  OnChanges,
  Optional,
  SimpleChanges,
} from '@angular/core';
import { EsnIconsRegistry } from './icons-registry';

const DEFAULT_BOXED: boolean = true;


@Component({
  selector: 'esn-icon',
  template: '<ng-content></ng-content>',
  styleUrls: ['./icon.component.scss'],
  host: {
    class: 'esn-icon',
    '[class.esn-icon-boxed]': `boxed`,
    '[class.esn-icon-xs]': `size === 'xs'`,
    '[class.esn-icon-sm]': `size === 'sm'`,
    '[class.esn-icon-md]': `size === 'md'`,
  }
})
export class EsnIcon implements OnChanges {
  @Input() name: string;
  @Input() boxed: boolean = DEFAULT_BOXED;
  @Input() size: 'xs' | 'sm' | 'md'  = 'md';
 
  public _svgIcon: SVGElement;

  constructor(
    public element: ElementRef,
    public iconsRegistry: EsnIconsRegistry,
    @Optional() @Inject(DOCUMENT) public document: any
  ) {}

  ngOnChanges(changes: SimpleChanges): void {
    const svgData = this.iconsRegistry.getIcon(this.name);
    if (!svgData) return;
    
    if (this._svgIcon) this.element.nativeElement.removeChild(this._svgIcon);

    this._svgIcon = this._svgElementFromString(svgData);
    this.element.nativeElement.appendChild(this._svgIcon);
  }

  public _svgElementFromString(svgContent: string): SVGElement {
    const div = this.document.createElement('DIV');
    div.innerHTML = svgContent;
    return (
      div.querySelector('svg') ||
      this.document.createElementNS('http://www.w3.org/2000/svg', 'path')
    );
  }
}
