import { Inject, Injectable, InjectionToken, Optional } from '@angular/core';
import {
  HttpClient,
  HttpEvent,
  HttpHeaders,
  HttpParams,
  HttpResponse,
} from '@angular/common/http';

import { Observable } from 'rxjs';

import { ApiResponse, Configuration, CustomHttpUrlEncodingCodec, ESN_ENVIRONMENT_CONFIG, ListWrapperDto } from '../../utils/public-api';
import { EsnNotificationModel } from './model/notification';

@Injectable()
export class EsnNotificationsController {
  public defaultHeaders = new HttpHeaders();
  public configuration = new Configuration();
  protected basePath = '';

  constructor(
    protected httpClient: HttpClient,
    @Optional()
    @Inject(new InjectionToken<string>('basePath'))
    basePath: string,
    @Optional() configuration: Configuration,
    @Inject(ESN_ENVIRONMENT_CONFIG) public environment: any,

  ) {
    if(!!environment.not_set){
      throw `EsnFilemanagerModule requires an ESN_ENVIRONMENT_CONFIG.
      Please provide one with "{ provide: ESN_ENVIRONMENT_CONFIG, useValue: your_environment_file (from src/environments) }"`;
    }

      this.basePath = basePath ||  `${environment.services.protocol}://${
        environment.services.hostname
      }${
        !!environment.services.gateway.port
          ? ':' + environment.services.gateway.port
          : ''
      }/${environment.services.notifications.route}`;
    
    if (configuration) {
      this.configuration = configuration;
      this.basePath = basePath || configuration.basePath || this.basePath;
    }
    if (!!environment.services.mock?.mockApi) {
      this.basePath = `${environment.services.mock.url}/${environment.services.gdd.route}`;
    }
  }


  public getNotifications(
    page?: number,
    size?: number,
    tags?: string,
    read?: boolean,
    dateBefore?: Date,
    dateAfter?: Date,
    criticality?: string,
    observe?: 'body',
    reportProgress?: boolean
  ): Observable<ListWrapperDto<EsnNotificationModel> >;
  public getNotifications(
    page?: number,
    size?: number,
    tags?: string,
    read?: boolean,
    dateBefore?: Date,
    dateAfter?: Date,
    criticality?: string,
    observe?: 'response',
    reportProgress?: boolean
  ): Observable<HttpResponse<ListWrapperDto<EsnNotificationModel> >>;
  public getNotifications(
    page?: number,
    size?: number,
    tags?: string,
    read?: boolean,
    dateBefore?: Date,
    dateAfter?: Date,
    criticality?: string,
    observe?: 'events',
    reportProgress?: boolean
  ): Observable<HttpEvent<ListWrapperDto<EsnNotificationModel> >>;
  public getNotifications(
    page?: number,
    size?: number,
    tags?: string,
    read?: boolean,
    dateBefore?: Date,
    dateAfter?: Date,
    criticality?: string,
    observe: any = 'body',
    reportProgress: boolean = false
  ): Observable<any> {
    let queryParameters = new HttpParams({
      encoder: new CustomHttpUrlEncodingCodec(),
    });
    if (!!tags) {
      queryParameters = queryParameters.set('tags', <any>tags);
    }
    if (read !== undefined && read !== null) {
      queryParameters = queryParameters.set('read', <any>read);
    }
    if (page !== undefined && page !== null) {
      queryParameters = queryParameters.set('page', <any>page);
    }
    if (size !== undefined && size !== null) {
      queryParameters = queryParameters.set('size', <any>size);
    }
    if (dateBefore !== undefined && dateBefore !== null) {
      queryParameters = queryParameters.set('dateBefore', <any>dateBefore.toISOString());
    }
    if (dateAfter !== undefined && dateAfter !== null) {
      queryParameters = queryParameters.set('dateAfter', <any>dateAfter.toISOString());
    }
    if (criticality !== undefined && criticality !== null) {
      queryParameters = queryParameters.set('criticality', <any>criticality);
    }

    let headers = this.defaultHeaders;

    // to determine the Accept header
    let httpHeaderAccepts: string[] = ['*/*'];
    const httpHeaderAcceptSelected: string | undefined =
      this.configuration.selectHeaderAccept(httpHeaderAccepts);
    if (httpHeaderAcceptSelected != undefined) {
      headers = headers.set('Accept', httpHeaderAcceptSelected);
    }


    // to determine the Content-Type header
    const consumes: string[] = [];

    return this.httpClient.get<ListWrapperDto<EsnNotificationModel> >(
      `${this.basePath}/web-notifications`,
      {
        params: queryParameters,
        withCredentials: this.configuration.withCredentials,
        headers: headers,
        observe: observe,
        reportProgress: reportProgress,
      }
    );
  }


  

  public getCounters(
    tags?: string,
    observe?: 'body',
    reportProgress?: boolean
  ): Observable<ListWrapperDto<EsnNotificationModel> >;
  public getCounters(
    tags?: string,
    observe?: 'response',
    reportProgress?: boolean
  ): Observable<HttpResponse<ListWrapperDto<EsnNotificationModel> >>;
  public getCounters(
    tags?: string,
    observe?: 'events',
    reportProgress?: boolean
  ): Observable<HttpEvent<ListWrapperDto<EsnNotificationModel> >>;
  public getCounters(
    tags?: string,
    observe: any = 'body',
    reportProgress: boolean = false
  ): Observable<any> {
    let queryParameters = new HttpParams({
      encoder: new CustomHttpUrlEncodingCodec(),
    });
    if (!!tags) {
      queryParameters = queryParameters.set('tags', <any>tags);
    }

    let headers = this.defaultHeaders;

    // to determine the Accept header
    let httpHeaderAccepts: string[] = ['*/*'];
    const httpHeaderAcceptSelected: string | undefined =
      this.configuration.selectHeaderAccept(httpHeaderAccepts);
    if (httpHeaderAcceptSelected != undefined) {
      headers = headers.set('Accept', httpHeaderAcceptSelected);
    }



    return this.httpClient.get<ListWrapperDto<EsnNotificationModel> >(
      `${this.basePath}/web-notifications/count`,
      {
        params: queryParameters,
        withCredentials: this.configuration.withCredentials,
        headers: headers,
        observe: observe,
        reportProgress: reportProgress,
      }
    );
  }


  public markNotificationAsRead (
    notificationId: string,
    observe?: 'body',
    reportProgress?: boolean
  ): Observable<ApiResponse<any>>;
  public markNotificationAsRead(
    notificationId: string,
    observe?: 'body',
    reportProgress?: boolean
  ): Observable<HttpResponse<ApiResponse<any>>>;
  public markNotificationAsRead(
    notificationId: string,
    observe?: 'body',
    reportProgress?: boolean
  ): Observable<HttpEvent<ApiResponse<any>>>;
  public markNotificationAsRead(
    notificationId: string,
    observe?: 'body',
    reportProgress?: boolean
  ): Observable<any> {
    let queryParameters = new HttpParams({
      encoder: new CustomHttpUrlEncodingCodec(),
    });

    let headers = this.defaultHeaders;

    // to determine the Accept header
    let httpHeaderAccepts: string[] = ['*/*'];
    const httpHeaderAcceptSelected: string | undefined =
      this.configuration.selectHeaderAccept(httpHeaderAccepts);
    if (httpHeaderAcceptSelected != undefined) {
      headers = headers.set('Accept', httpHeaderAcceptSelected);
    }

    // to determine the Content-Type header
    const consumes: string[] = [];

    return this.httpClient.patch<any>(
      `${this.basePath}/web-notifications/${notificationId}/read`,
      { isRead: true },
      {
        params: queryParameters,
        withCredentials: this.configuration.withCredentials,
        headers: headers,
        observe: observe,
        reportProgress: reportProgress,
      }
    );
  }



  public doActionOnNotification (
    mark: 'VIEWED' | 'READ',
    tags: string,
    lastDate: string,
    observe?: 'body',
    reportProgress?: boolean
  ): Observable<ApiResponse<any>>;
  public doActionOnNotification(
    mark: 'VIEWED' | 'READ',
    tags: string,
    lastDate: string,
    observe?: 'body',
    reportProgress?: boolean
  ): Observable<HttpResponse<ApiResponse<any>>>;
  public doActionOnNotification(
    mark: 'VIEWED' | 'READ',
    tags: string,
    lastDate: string,
    observe?: 'body',
    reportProgress?: boolean
  ): Observable<HttpEvent<ApiResponse<any>>>;
  public doActionOnNotification(
    mark: 'VIEWED' | 'READ',
    tags: string,
    lastDate:string,
    observe?: 'body',
    reportProgress?: boolean
  ): Observable<any> {
    let queryParameters = new HttpParams({
      encoder: new CustomHttpUrlEncodingCodec(),
    });
    if (!!tags) {
      queryParameters = queryParameters.set('tags', <any>tags);
    }
    if (mark !== undefined && mark !== null) {
      queryParameters = queryParameters.set('mark', <any>mark);
    }
    if (lastDate !== undefined && lastDate !== null) {
      queryParameters = queryParameters.set('sendDate', <any>lastDate);
    }

    let headers = this.defaultHeaders;

    // to determine the Accept header
    let httpHeaderAccepts: string[] = ['*/*'];
    const httpHeaderAcceptSelected: string | undefined =
      this.configuration.selectHeaderAccept(httpHeaderAccepts);
    if (httpHeaderAcceptSelected != undefined) {
      headers = headers.set('Accept', httpHeaderAcceptSelected);
    }

    // to determine the Content-Type header
    const consumes: string[] = [];

    return this.httpClient.put<any>(
      `${this.basePath}/web-notifications/action`,
      {},
      {
        params: queryParameters,
        withCredentials: this.configuration.withCredentials,
        headers: headers,
        observe: observe,
        reportProgress: reportProgress,
      }
    );
  }

}
