import {
  Component,
  EventEmitter,
  Input,
  OnChanges,
  Output,
  SimpleChanges,
} from '@angular/core';
import { EsnNotificationAction, EsnNotificationModel } from './model/notification';
import { EsnNotificationProcessingService } from './notification-processing.service';

@Component({
  selector: 'esn-notification',
  templateUrl: './notification.component.html',
  styleUrls: ['./notification.component.scss'],
  host: {
    class: 'esn-notification',
    '[class.esn-notification-read]': `notification.isRead`,
  },
})
export class EsnNotification {
  @Input() notification: EsnNotificationModel;
  @Output() actionClick: EventEmitter<null> =
    new EventEmitter<null>();

  constructor(public processingService:  EsnNotificationProcessingService){}

  public onClick() {
    this.actionClick.emit(null);
    this.processingService.onNotificationClicked(this.notification);
  }

}
