import {
  Directive,
  OnInit,
  OnChanges,
  Input,
  Optional,
  ElementRef,
  SimpleChanges,
  Component,
  AfterViewInit,
  Renderer2,
  NgZone,
} from '@angular/core';
import { _closeDialogVia } from '@angular/material/dialog';
import { BehaviorSubject, debounceTime, distinct, fromEvent, Observable } from 'rxjs';
import { EsnDialog } from './dialog.service';
import { EsnDialogRef } from './dialogRef';

@Directive({
  selector: '[esn-dialog-close], [esnDialogClose]',
  exportAs: 'esnDialogClose',
  host: {
    '(click)': '_onButtonClick($event)',
    '[attr.aria-label]': 'ariaLabel || null',
  },
})
export class EsnDialogClose implements OnInit, OnChanges {
  /** Screen reader label for the button. */
  @Input('aria-label') ariaLabel: string;

  /** Default to "button" to prevents accidental form submits. */

  /** Dialog close input. */
  @Input('esn-dialog-close') dialogResult: any;

  @Input('esnDialogClose') _matDialogClose: any;

  constructor(
    /**
     * Reference to the containing dialog.
     * @deprecated `dialogRef` property to become private.
     * @breaking-change 13.0.0
     */
    // The dialog title directive is always used in combination with a `MatDialogRef`.
    // tslint:disable-next-line: lightweight-tokens
    @Optional() public dialogRef: EsnDialogRef<any>,
    private _elementRef: ElementRef<HTMLElement>,
    private _dialog: EsnDialog
  ) {}

  ngOnInit() {
    if (!this.dialogRef) {
      // When this directive is included in a dialog via TemplateRef (rather than being
      // in a Component), the DialogRef isn't available via injection because embedded
      // views cannot be given a custom injector. Instead, we look up the DialogRef by
      // ID. This must occur in `onInit`, as the ID binding for the dialog container won't
      // be resolved at constructor time.
      this.dialogRef = getClosestDialog(
        this._elementRef,
        this._dialog['_dialog']!.openDialogs
      )!;
    }
  }

  ngOnChanges(changes: SimpleChanges) {
    const proxiedChange =
      changes['_matDialogClose'] || changes['_matDialogCloseResult'];

    if (proxiedChange) {
      this.dialogResult = proxiedChange.currentValue;
    }
  }

  _onButtonClick(event: MouseEvent) {
    // Determinate the focus origin using the click event, because using the FocusMonitor will
    // result in incorrect origins. Most of the time, close buttons will be auto focused in the
    // dialog, and therefore clicking the button won't result in a focus change. This means that
    // the FocusMonitor won't detect any origin change, and will always output `program`.
    _closeDialogVia(
      this.dialogRef,
      event.screenX === 0 && event.screenY === 0 ? 'keyboard' : 'mouse',
      this.dialogResult
    );
  }
}

function getClosestDialog(
  element: ElementRef<HTMLElement>,
  openDialogs: EsnDialogRef<any>[]
) {
  let parent: HTMLElement | null = element.nativeElement.parentElement;

  while (parent && !parent.classList.contains('mat-dialog-container')) {
    parent = parent.parentElement;
  }

  return parent ? openDialogs.find((dialog) => dialog.id === parent!.id) : null;
}

@Component({
  selector: 'esn-dialog-header',
  template: `
    <div class="esn-dialog-header informative-{{this.color}}" [ngClass]="{ 'with-divider': divider}">
    	<ng-content></ng-content>
    </div>`,
  styleUrls: ['./dialog-content.scss'],
  exportAs: 'esnDialogHeader',
})
export class EsnDialogHeader implements OnInit {
  @Input() divider: boolean = true;
  @Input() color: "primary" | "accent" | "error" | "success" | "neutral" | "transparent" = 'transparent';
  
  constructor() {}

  ngOnInit(): void {}
}

@Component({
  selector: 'esn-dialog-content',
  template: `
    <div class="esn-dialog-content">
    	<ng-content></ng-content>
    </div>`,
  styleUrls: ['./dialog-content.scss'],
  exportAs: 'esnDialogContent',
})
export class EsnDialogContent implements OnInit, AfterViewInit {
  public observer: ResizeObserver;
  public dialogContainer: HTMLElement;
  public isFullScreen: boolean = false;

  public heightUpdateTrigger$: BehaviorSubject<any> = new BehaviorSubject<any>(null);

  constructor(
    public elementRef: ElementRef,
    public renderer: Renderer2,
    private zone: NgZone
  ) {}

  ngOnInit(): void {
  }

  ngAfterViewInit(): void {
    this.dialogContainer = _findDialogContainer(this.elementRef)!;
    this.isFullScreen = !!this.dialogContainer.parentElement?.classList.contains('esn-dialog-full-screen');
    const footer = _findFooter(this.dialogContainer);
    const header = _findHeader(this.dialogContainer);

    this.observer = new ResizeObserver(() => {
      this.heightUpdateTrigger$.next(null);
    });

    if (!!footer) {
      this.observer.observe(footer!);
    }
    if (!!header) {
      this.observer.observe(header!);
    }
    this._updateContentHeight();

    this.heightUpdateTrigger$.pipe(debounceTime(35)).subscribe(() => {
      this.zone.run(() => {
        this._updateContentHeight();
      });
    })
  }


  _updateContentHeight() {
    const height = this._getHeaderAndFooterHeight();
    const contentElm = this.elementRef.nativeElement.querySelector(
      '.esn-dialog-content'
    );
    this.renderer.setStyle(
      contentElm,
      `${!this.isFullScreen ? 'max-' : ''}height`,
      `calc(${this.isFullScreen ? 100 : 85}vh - ${height}px - 2rem)`
    );
  }

  _getHeaderAndFooterHeight() {
    const footer = _findFooter(this.dialogContainer);
    const header = _findHeader(this.dialogContainer);
    const footerHeight = !!footer ? (footer as any)['offsetHeight'] : 0;
    const headerHeight = !!header ? (header as any)['offsetHeight'] : 0;

    return footerHeight + headerHeight;
  }

  ngOnDestroy() {
    this.observer.disconnect();
  }
}

@Component({
  selector: 'esn-dialog-footer',
  template: `
    <div class="esn-dialog-footer" (cdkObserveContent)="onContentChange()" [ngClass]="{      'with-divider': divider,      centered: align === 'center',      'space-between': align === 'space-between',      'many-buttons': nbButtons > 2    }">
    	<ng-content></ng-content>
    </div>`,
  styleUrls: ['./dialog-content.scss'],
  exportAs: 'esnDialogFooter',
})
export class EsnDialogFooter implements AfterViewInit {
  @Input() divider: boolean = true;
  @Input() align: 'right' | 'center' | 'space-between' = 'right';

  public nbButtons: number = 0;

  constructor(public elementRef: ElementRef) {}

  ngAfterViewInit(): void {
    this._countButtons();
  }

  public onContentChange() {
    this._countButtons();
  }

  _countButtons() {
    setTimeout(
      () =>
        (this.nbButtons =
          this.elementRef.nativeElement.querySelectorAll('.esn-btn').length)
    );
  }
}

function _findDialogContainer(element: ElementRef<HTMLElement>) {
  let parent: HTMLElement | null = element.nativeElement.parentElement;

  while (parent && !parent.classList.contains('mat-mdc-dialog-container')) {
    parent = parent.parentElement;
  }

  return parent;
}

function _findFooter(element: HTMLElement) {
  return element.querySelector('.esn-dialog-footer');
}

function _findHeader(element: HTMLElement) {
  return element.querySelector('.esn-dialog-header');
}
