import {Component, Input, OnChanges, SimpleChanges} from '@angular/core';
import { EsnNotificationCategoryTypeEnum, EsnNotificationCriticalityEnum, EsnNotificationModel, EsnNotificationViewType } from '../model/notification';



@Component({
  selector: 'esn-notification-avatar',
  templateUrl: './notification-avatar.component.html',
  styleUrls: ['./notification-avatar.component.scss'],
  host: {
    class: 'esn-notification-avatar',
  }
})

export class EsnNotificationAvatar implements OnChanges {


  @Input() notification: EsnNotificationModel;
  public slots: {[key in EsnNotificationViewType]?: number} = {};
  public categoryTypeIconMap: {[key in EsnNotificationCategoryTypeEnum]: string} = {
    REMINDER: 'clock',
    FORM: 'edit-3',
    NOTIFY: 'bell-ringing-2',
    DONE: 'check',
    EXCLAMATION: 'alert-square',
    ANNOUNCEMENT: 'megaphone',
    COMMENT: 'annotation-alert',
  }

  public criticalityTypeIconMap: {[key in EsnNotificationCriticalityEnum]: string} = {
    URGENT: 'exclamation',
    IMPORTANT: 'chevron-up-double',
    NONE: '',
  }

  ngOnChanges(changes: SimpleChanges): void {
    this.readSlots();
  }

  public readSlots(){
    this.slots = {};
    const view = this.notification.view;
    if(!view){
      return;
    }
    for(let i = 3; i>0; i--){
      if(!!view['slot' + i as 'slot1' | 'slot2' | 'slot3']){
        this.slots[view['slot' + i as 'slot1' | 'slot2' | 'slot3']] = i;
      }
    }
  }
}
