import { Injectable } from "@angular/core";

@Injectable()
export abstract class EsnDatabaseService {
  abstract getValueByKey(
    store: string,
    key: any,
    decrypt: boolean
  ): Promise<any>;

  abstract setValueByKey(
    store: string,
    key: any,
    value: any,
    encrypt: boolean
  ): Promise<any>;
}


@Injectable()
export class EsnInMemoryDatabaseService implements EsnDatabaseService{
  public db: any = {};
  public getValueByKey(
    store: string,
    key: any,
    decrypt: boolean
  ): Promise<any>{
    return Promise.resolve(this.db[store]?.[key]);
  };

   public setValueByKey(
    store: string,
    key: any,
    value: any,
    encrypt: boolean
  ): Promise<any>{
    if(!this.db[store]){
      this.db[store] = {};
    }
    this.db[store][key] = value;
    return Promise.resolve(key)
  };

  public reset(){
    this.db = {};
  }
}
