import {
  Component,
  OnInit,
  ElementRef,
  Input,
} from '@angular/core';
import { EsnNotificationsService } from '../notifications.service';
import { BehaviorSubject } from 'rxjs';
import { EsnNotificationProcessingService } from '../notification-processing.service';
import {
  EsnNotificationCriticalityEnum,
  NOTIFICATION_CRITICALITY_LABELS,
} from '../model/notification';
import { FormBuilder, FormGroup } from '@angular/forms';

@Component({
  selector: 'esn-notification-center',
  templateUrl: './notification-center.component.html',
  styleUrls: ['./notification-center.component.scss'],
  host: {
    class: 'esn-notification-center',
  },
})
export class EsnNotificationCenter implements OnInit {
  @Input() scrollElement: HTMLElement;
  public form: FormGroup;
  public totalUnread: number = 0;
  public totalNbNotifs: number = 0;
  public refresh$: BehaviorSubject<boolean> = new BehaviorSubject<boolean>(
    false
  );
  public markAllAsRead$: BehaviorSubject<boolean> = new BehaviorSubject<boolean>(
    false
  );
  public criticalities = Object.keys(
    EsnNotificationCriticalityEnum
  ) as EsnNotificationCriticalityEnum[];
  public criticalityLabels = NOTIFICATION_CRITICALITY_LABELS;

  constructor(
    public notificationsService: EsnNotificationsService,
    public processingService: EsnNotificationProcessingService,
    public elementRef: ElementRef,
    fb: FormBuilder
  ) {
    this.form = fb.group({
      criticality: [null, null],
      date: [null, null],
      unread: [null, null],
    });
    this.form.valueChanges.subscribe((val) => {
      this.refresh$.next(true)
    });
  }

  ngOnInit(): void {
    this.refresh$.next(true);

    this.notificationsService
      .getUnread()
      .subscribe((unread) => (this.totalUnread = unread));
    this.notificationsService
      .getTotal()
      .subscribe((total) => (this.totalNbNotifs = total));
  }

  get dateAtEndOfDay() {
    const date = this.form?.value?.date;
    if (!!date) {
      let endOfDayDate = new Date(date)
      endOfDayDate = new Date(endOfDayDate.getTime() + 24 * 3600 * 1000);
      return endOfDayDate
    }
    return date;
  }

  public markAllAsRead() {
    this.notificationsService.markAllAsRead();
    this.markAllAsRead$.next(true)
  }
}
