import { saveAs } from 'file-saver';
import { BlobItem, CustomFileMetadataDto, DocumentItem, FileMetadataDto, Image } from '../public-api';


export class EsnFileConverter {
  static async cutBlobIntoChunks(item: BlobItem, fileChunkMaxSize: number): Promise<Blob[]> {
    const chunks = [];
    const blob = await EsnFileConverter.base64ToBlob(item.blobString);

    for (
      let start = 0, index = 0;
      start < blob.size;
      start += fileChunkMaxSize, index++
    ) {
      const chunk = blob.slice(start, start + fileChunkMaxSize);
      chunks.push(chunk);
    }
    if (!chunks.length) {
      chunks.push(blob);
    }
    return chunks;
  }

  // To compute the checksum
  static getFileBuffer(file: any): Promise<ArrayBuffer> {
    return new Promise((resolve, reject) => {
      const fr = new FileReader();
      fr.onload = () => {
        resolve(fr.result as ArrayBuffer);
      };

      fr.onerror = (error) => {
        reject(error);
      };
      fr.readAsArrayBuffer(file);
    });
  }

  // To display the image
  static getFileBase64(file: File): Promise<string> {
    return new Promise((resolve, reject) => {
      const fr = new FileReader();
      fr.onload = () => {
        resolve(fr.result as string);
      };
      fr.onerror = (error) => {
        reject(error);
      };
      fr.readAsDataURL(file);
    });
  }

  // To display a thumbnail
  static getImgFromBlob(
    file: File,
    maxWidth: number = 100,
    maxHeight: number = 100
  ): Promise<Image> {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();

      reader.onload = function (e) {
        const img = document.createElement('img');
        img.src = e.target?.result as string;
        img.onload = function (e2) {
          const canvas = document.createElement('canvas');
          const ctx = canvas.getContext('2d');
          ctx?.drawImage(img, 0, 0);

          canvas.width = img.width;
          canvas.height = img.height;

          // calculate the new width / height respecting the original ratio
          if (img.width > img.height) {
            if (img.width > maxWidth) {
              canvas.height *= maxWidth / img.width;
              canvas.width = maxWidth;
            }
          } else {
            if (img.height > maxHeight) {
              canvas.width *= maxHeight / img.height;
              canvas.height = maxHeight;
            }
          }

          // resize
          const ctx2 = canvas.getContext('2d');
          ctx2?.drawImage(img, 0, 0, canvas.width, canvas.height);

          // TODO : convert to webp
          // canvas.toBlob( blob => {
          //   resolve(URL.createObjectURL(blob))
          // }, 'image/webp')

          const dataUrl = canvas.toDataURL(file.type);
          resolve({ dataUrl, width: canvas.width, height: canvas.height });
        };

        img.onerror = (error) => {
          reject(error);
        };
      };
      reader.onerror = (error) => {
        reject(error);
      };
      reader.readAsDataURL(file);
    });
  }

  static getImgDimensions(
    file: File
  ): Promise<{ width: number; height: number }> {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();

      reader.onload = function (e) {
        const img = document.createElement('img');
        img.src = e.target?.result as string;
        img.onload = function (e2) {
          resolve({
            width: img.width,
            height: img.height,
          });
        };

        img.onerror = (error) => {
          reject(error);
        };
      };
      reader.onerror = (error) => {
        reject(error);
      };
      reader.readAsDataURL(file);
    });
  }

  static roundValue(value: number | null | undefined): number {
    return value ? Math.ceil(value) : 0;
  }

  /*******************/

  /* DATA CONVERSION
  /*******************/
  static bufferToBase64(buffer: Uint8Array): string {
    const data = buffer.reduce(
      (acc, i) => (acc += String.fromCharCode.apply(null, [i])),
      ''
    );
    return btoa(data);
  }

  static base64ToBuffer(b64: string): Uint8Array {
    return Uint8Array.from(atob(b64), (c) => c.charCodeAt(<any>null));
  }

  static blobToBase64(blob: Blob): Promise<string> {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.onerror = reject;
      reader.onload = () => {
        resolve(<string>reader.result);
      };
      reader.readAsDataURL(blob);
    });
  }

  static async base64ToBlob(b64: string): Promise<Blob> {
    console.log({ b64 });
    if (b64 === 'data:') {
      return new Blob([]);
    }
    return this.buildBlob(b64);
  }

  static async buildBlob(dataURL: string) {
    const BASE64_MARKER = ';base64,';
    const parts = dataURL.split(BASE64_MARKER);
    const contentType = parts[0].split(':')[1];
    const raw = window.atob(parts[1]);
    const rawLength = raw.length;
    const uInt8Array = new Uint8Array(rawLength);

    for (let i = 0; i < rawLength; ++i) {
      uInt8Array[i] = raw.charCodeAt(i);
    }

    return new Blob([uInt8Array], { type: contentType });
  }

  static blobToFile = (theBlob: Blob, fileName: string): File => {
    var b: any = theBlob;
    b.lastModifiedDate = new Date();
    b.name = fileName;
    return <File>theBlob;
  };

  static async getImageFromBlobItem(
    item: BlobItem,
    maxWidth: number = 320,
    maxHeight: number = 256
  ) {
    const blob = await EsnFileConverter.base64ToBlob(item.blobString);
    const file = EsnFileConverter.blobToFile(blob, item.filename);
    const image = await EsnFileConverter.getImgFromBlob(file, maxWidth, maxHeight);
    return image;
  }

  static async getThumbnailUrlFromBlob(item: BlobItem): Promise<string> {
    return (await EsnFileConverter.getImageFromBlobItem(item)).dataUrl;
  }

  static async customMetadataDtoToBlobItem(
    customMetadata: CustomFileMetadataDto
  ): Promise<BlobItem> {
    const blobString = await EsnFileConverter.blobToBase64(customMetadata.blob!);
    return {
      filename: customMetadata.filename,
      uuid: customMetadata.externalUuid,
      size: customMetadata.size,
      contentType: document.contentType,
      blobString: blobString,
    } as BlobItem;
  }

  static async customMetadataDtosToBlobItems(
    customMetadata: CustomFileMetadataDto[]
  ) {
    const promises: Promise<BlobItem>[] = [];
    customMetadata.forEach((doc) =>
      promises.push(EsnFileConverter.customMetadataDtoToBlobItem(doc))
    );
    return Promise.all(promises);
  }

  static saveFile(blob: Blob, fileName: string, fileType: string): void {
    const file = new Blob([blob], { type: fileType });
    saveAs(file, fileName); // TODO pour plus tard: à changer par https://blog.excalidraw.com/browser-fs-access/
  }

  static convertFileMetaDataToDocument(
    file: FileMetadataDto | CustomFileMetadataDto,
    loading: boolean = false,
    actions: any = {
      delete: false,
      download: true,
      view: false,
      upload: false,
    }
  ): DocumentItem {
    return {
      metadata: file,
      loading,
      actions,
    } as unknown as DocumentItem;
  }
}
