import { Component, Input } from '@angular/core';

export type EsnIconBgShape = 'circle' | 'square' | 'rings' | undefined;
export type EsnIconBgType = 'flat' | 'stroked' | 'mono' | 'duo' | 'basic' | undefined;
export type EsnIconBgShade = 'lighter' | 'darker' | undefined;
export type EsnIconBgColor =
  | 'primary'
  | 'accent'
  | 'info'
  | 'warning'
  | 'success'
  | 'error'
  | 'neutral'
  | 'purple'
  | undefined; 


const DEFAULT_SHAPE: EsnIconBgShape = 'circle';
const DEFAULT_TYPE: EsnIconBgType = 'mono';
const DEFAULT_COLOR: EsnIconBgColor = 'primary';
const DEFAULT_SHADE: EsnIconBgShade = undefined;
const DEFAULT_HASRINGS: boolean = false;

@Component({
  selector: 'esn-icon-bg',
  template: '<ng-content></ng-content>',
  styleUrls: ['./icon-bg.component.scss'],
  host: {
    class: 'esn-icon-bg',
    '[class.esn-icon-bg--primary]': `color === 'primary'`,
    '[class.esn-icon-bg--accent]': `color === 'accent'`,
    '[class.esn-icon-bg--success]': `color === 'success'`,
    '[class.esn-icon-bg--error]': `color === 'error'`,
    '[class.esn-icon-bg--warning]': `color === 'warning'`,
    '[class.esn-icon-bg--neutral]': `color === 'neutral'`,
    '[class.esn-icon-bg--info]': `color === 'info'`,
    '[class.esn-icon-bg--purple]': `color === 'purple'`,
    '[class.esn-icon-bg--lighter]': `shade === 'lighter'`,
    '[class.esn-icon-bg--darker]': `shade === 'darker'`,
    '[class.esn-icon-bg--circle]': `shape === 'circle'`,
    '[class.esn-icon-bg--square]': `shape === 'square'`,
    '[class.esn-icon-bg--flat]': `type === 'flat'`,
    '[class.esn-icon-bg--basic]': `type === 'basic'`,
    '[class.esn-icon-bg--duo]': `type === 'duo'`,
    '[class.esn-icon-bg--mono]': `type === 'mono'`,
    '[class.esn-icon-bg--stroked]': `type === 'stroked'`,
    '[class.esn-icon-bg--always-light]': `theme === 'always-light'`,
    '[class.esn-icon-bg--always-dark]': `theme === 'always-dark'`,
    '[class.esn-icon-bg--has-rings]': `rings`,
  },
})
export class EsnIconBg {
  @Input() shape: EsnIconBgShape = DEFAULT_SHAPE;
  @Input() type: EsnIconBgType = DEFAULT_TYPE;
  @Input() color: EsnIconBgColor = DEFAULT_COLOR;
  @Input() shade: EsnIconBgShade = DEFAULT_SHADE;
  @Input() rings: boolean = DEFAULT_HASRINGS;
  @Input() theme: 'always-dark' | 'always-light' | null = null; 

  
  constructor() {}
}
