import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';

export type EsnButtonType =
  | 'basic'
  | 'stroked'
  | 'flat'
  | 'light'
  | 'link'
  | undefined;
export type EsnButtonSize = 'sm' | 'md' | 'lg' | 'xl' | '2xl' | undefined;
export type EsnButtonColor =
  | 'primary'
  | 'accent'
  | 'success'
  | 'error'
  | 'neutral'
  | undefined;

type MatButtonColor = 'primary' | 'accent' | 'warn' | null | undefined;

const DEFAULT_COLOR: EsnButtonColor = 'primary';
const DEFAULT_SIZE: EsnButtonSize = 'md';
const DEFAULT_TYPE: EsnButtonType = 'flat';

const COLORS_MAP: { [key in EsnButtonColor as string]: MatButtonColor } = {
  primary: 'primary',
  accent: 'accent',
  error: 'warn',
  success: 'primary', // a dark material palette that will be overridden by CSS
  neutral: 'primary', // a dark material palette that will be overridden by CSS
};

/*****************************************************/
/* A whole tag selector is needed in order to
/* render a button with mat-button attribute.
/* But we won't be able to use esn-button on <a> tags
/* https://github.com/angular/angular/issues/8785
/*****************************************************/
@Component({
  selector: 'esn-button',
  templateUrl: './button.component.html',
  styleUrls: ['./button.component.scss'],
  host: {
    class: 'esn-btn',
    '[class.esn-btn-primary]': `_color === 'primary'`,
    '[class.esn-btn-accent]': `_color === 'accent'`,
    '[class.esn-btn-success]': `_color === 'success'`,
    '[class.esn-btn-error]': `_color === 'error'`,
    '[class.esn-btn-neutral]': `_color === 'neutral'`,
    '[class.esn-btn-sm]': `size === 'sm'`,
    '[class.esn-btn-md]': `size === 'md'`,
    '[class.esn-btn-lg]': `size === 'lg'`,
    '[class.esn-btn-xl]': `size === 'xl'`,
    '[class.esn-btn-2xl]': `size === '2xl'`,
    '[class.esn-btn-basic]': `type === 'basic'`,
    '[class.esn-btn-stroked]': `type === 'stroked'`,
    '[class.esn-btn-flat]': `type === 'flat'`,
    '[class.esn-btn-light]': `type === 'light'`,
    '[class.esn-btn-link]': `type === 'link'`,
    '[class.esn-btn-icon]': `iconOnly`,
    '[class.esn-btn-disabled]': `disabled || null`,
    '[class.esn-btn-round]': `round || null`,
  },
})
export class EsnButton {
  @Input() type: EsnButtonType = DEFAULT_TYPE;
  @Input() size: EsnButtonSize = DEFAULT_SIZE;
  @Input() disabled: boolean | string = false;
  @Input() round: boolean = false;
  @Input() iconOnly: boolean = false;

  @Input()
  set color(value: EsnButtonColor) {
    this._color = value;
    this._matColor = COLORS_MAP[this._color as string];
  }
  get color(): EsnButtonColor {
    return this._color;
  }

  @Output() click = new EventEmitter<any>();

  public _color: EsnButtonColor = DEFAULT_COLOR;
  public _matColor: MatButtonColor = COLORS_MAP[DEFAULT_COLOR as string];

  constructor() {}
}
