import {Directive, ElementRef, Inject, Input, NgZone, OnChanges, SimpleChanges, ViewContainerRef} from '@angular/core';
import {ESN_TOOLTIP_DEFAULT_OPTIONS, ESN_TOOLTIP_SCROLL_STRATEGY} from "./tooltip";
import {_MatTooltipBase, MatTooltipDefaultOptions, TooltipComponent} from "@angular/material/tooltip";
import {ComponentType, Overlay, ScrollStrategy} from "@angular/cdk/overlay";
import {ScrollDispatcher} from "@angular/cdk/scrolling";
import {Platform} from "@angular/cdk/platform";
import {AriaDescriber, FocusMonitor} from "@angular/cdk/a11y";
import {Directionality} from "@angular/cdk/bidi";
import {EsnTooltip} from "./models";

@Directive({
  selector: '[esnTooltip]',
})

export class TooltipDirective extends _MatTooltipBase<TooltipComponent>  implements OnChanges {
  protected readonly _tooltipComponent: ComponentType<TooltipComponent> = TooltipComponent;

  @Input() esnTooltip: string | EsnTooltip | null;

  constructor(overlay: Overlay,
              elementRef: ElementRef<HTMLElement>,
              scrollDispatcher: ScrollDispatcher,
              viewContainerRef: ViewContainerRef,
              ngZone: NgZone, platform: Platform,
              ariaDescriber: AriaDescriber,
              focusMonitor: FocusMonitor,
              @Inject(ESN_TOOLTIP_SCROLL_STRATEGY) scrollStrategy: ScrollStrategy,
              dir: Directionality,
              @Inject(ESN_TOOLTIP_DEFAULT_OPTIONS) defaultOptions: MatTooltipDefaultOptions,
  ) {
    super(
      overlay,
      elementRef,
      scrollDispatcher,
      viewContainerRef,
      ngZone,
      platform,
      ariaDescriber,
      focusMonitor,
      scrollStrategy,
      dir,
      defaultOptions,
      document
    )
  }

  ngOnChanges(changes:SimpleChanges): void {
    if(!!this.esnTooltip) {
      this.disabled = false;
      if(typeof this.esnTooltip === 'string') {
        this.message = this.esnTooltip;
      } else {
        if (this.esnTooltip.esnTooltipMessage) {
          this.message = this.esnTooltip.esnTooltipMessage;
        }
        if (this.esnTooltip.esnTooltipDisable) {
          this.disabled = this.esnTooltip.esnTooltipDisable;
        }
        if(this.esnTooltip.esnTooltipPosition) {
          this.position = this.esnTooltip.esnTooltipPosition;
        }
        if(this.esnTooltip.esnTooltipClass) {
          this.tooltipClass = this.esnTooltip.esnTooltipClass;
        }
        if(this.esnTooltip.esnTooltipTouchGestures) {
          this.touchGestures = this.esnTooltip.esnTooltipTouchGestures;
        }
        if (this.esnTooltip.esnTooltipHideDelay) {
          this.hideDelay = this.esnTooltip.esnTooltipHideDelay;
        }
        if (this.esnTooltip.esnTooltipShowDelay) {
          this.showDelay = this.esnTooltip.esnTooltipShowDelay;
        }
      }
    } else {
      this.message = '';
    }
  }

}
