import {
  AfterViewInit,
  Component,
  EventEmitter,
  Input,
  Output,
  ViewChild,
} from '@angular/core';

import { Subject } from 'rxjs';
import {
  MatPaginator,
  MatPaginatorSelectConfig,
} from '@angular/material/paginator';


export  class PageEvent {
  /** The current page index. */
  pageIndex: number;
  /**  Index of the page that was selected previously. */
  previousPageIndex?: number;
  /** The current page size. */
  pageSize: number;
  /** The current total number of items being paged. */
  length: number;
}



@Component({
  selector: 'esn-paginator',
  templateUrl: './paginator.component.html',
  styleUrls: ['./paginator.component.scss'],
  host: {
    class: 'esn-paginator',
  },
})
export class EsnPaginator implements AfterViewInit {
  @Input() disabled: boolean | string;
  @Input() hidePageSize: boolean | string;
  @Input() length: number;
  @Input() pageIndex: number = 0;
  @Input() pageSize: number = 20;
  @Input() pageSizeOptions: number[];
  @Input() selectConfig: MatPaginatorSelectConfig = {};
  @Input() showFirstLastButtons: boolean | string;
  @Output() page: EventEmitter<PageEvent> = new EventEmitter<PageEvent>();

  @ViewChild(MatPaginator) paginator: MatPaginator;
  _initialized: Subject<void> = new Subject<void>();

  constructor() {}
  ngAfterViewInit(): void {
    this.paginator.initialized.subscribe(() => {
      this._initialized.next();
      this._initialized.complete()
    })

    this.page.subscribe(ev => {
      this.pageIndex = ev.pageIndex;
      this.pageSize = ev.pageSize;
      this.length = ev.length;
    })
  }

  get initialized() {
    return this._initialized.asObservable()
  }


  public getNumberOfPages(){
    return this.paginator?.getNumberOfPages();
  }


}
