import {
  AfterViewInit,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ContentChild,
  ContentChildren,
  ElementRef,
  EventEmitter,
  Inject,
  Injector,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  Optional,
  Output,
  QueryList,
  SimpleChanges,
  TemplateRef,
  ViewChild,
  ViewEncapsulation
} from '@angular/core';
import {MAT_FORM_FIELD, MatFormField, MatFormFieldAppearance, MatFormFieldControl} from "@angular/material/form-field";
import {MAT_OPTGROUP, MAT_OPTION_PARENT_COMPONENT} from "@angular/material/core";
import {ControlValueAccessor, FormControl, NG_VALUE_ACCESSOR, NgControl} from "@angular/forms";
import {Subscription} from "rxjs";
import {MAT_SELECT_TRIGGER, matSelectAnimations, MatSelectChange} from "@angular/material/select";
import {EsnOption} from "../option/option.component";
import {EsnOptionGroup} from "../option-group/option-group.component";
import {EsnSelectTrigger} from "../select-trigger/select-trigger.component";
import { EsnUtils } from '../../../utils/public-api';

export interface ProjectionRefModel {
  value: any;
  viewValue: TemplateRef<any>;
  disabled: boolean
}

@Component({
  selector: 'esn-select',
  templateUrl: './select.component.html',
  styleUrls: ['./select.component.scss'],
  inputs: ['disabled', 'disableRipple', 'tabIndex'],
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
  animations: [matSelectAnimations.transformPanel],
  providers: [
    {provide: MatFormFieldControl, useExisting: EsnSelect},
    {provide: MAT_OPTION_PARENT_COMPONENT, useExisting: EsnSelect},
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: EsnSelect,
      multi: true,
    },
  ],
  host: {
    class: 'esn-select',
    '[class.esn-select-sm]': `size === 'sm'`,
    '[class.esn-select-md]': `size === 'md'`,
    '[class.esn-select-fill]': `appearance === 'fill'`,
    '[class.esn-select-outline]': `appearance === 'outline'`,
    '[class.esn-select-underline]': `appearance === 'underline'`,
    '[class.esn-select-dark]': `theme === 'dark'`,
    '[class.esn-select-light]': `theme === 'light'`
  }
})

export class EsnSelect implements OnInit, OnChanges, OnDestroy, ControlValueAccessor, AfterViewInit {
  @ContentChildren(EsnOption, {descendants: true}) optionsList: QueryList<EsnOption>;
  @ContentChildren(EsnOptionGroup, {descendants: true}) optionGroupsList: QueryList<EsnOptionGroup>;
  @ContentChild(EsnSelectTrigger) customTrigger: EsnSelectTrigger;

  @Input() appearance: 'fill' | 'outline' | 'underline' = 'outline';
  @Input() label: string;
  @Input() placeholder: string;
  @Input() disabled: boolean = false;
  @Input() required: boolean = false;
  @Input() multiple: boolean = false;
  @Input() hint: string = '';
  @Input() disableRipple: boolean ;
  @Input() tabIndex: number;
  @Input() error: string | { [key: string]: string };
  @Input() size: 'sm' | 'md' = 'sm';
  @Input() theme: 'dark' | 'light' | null = null;

  @Output() focus: EventEmitter<void> = new EventEmitter<void>();
  @Output() blur: EventEmitter<void> = new EventEmitter<void>();
  @Output() selectionChange: EventEmitter<any> = new EventEmitter<any>();
  @Output() openedChange: EventEmitter<boolean> = new EventEmitter<boolean>();

  @ViewChild(MatFormField) public formField: MatFormField;


  public observer: ResizeObserver;

  _matAppearance: MatFormFieldAppearance;
  control: FormControl
  private onChangeSub: Subscription = new Subscription();
  private parentControl: NgControl;
  errorMessage: string;
  private defaultError?: string;
  private errorsList: string[][] = [];
  options: ProjectionRefModel[];
  optionGroups: { label: any, options: ProjectionRefModel[], disabled: boolean, viewValue: TemplateRef<any> }[];

  constructor(private _injector: Injector,
              @Optional() @Inject(MAT_FORM_FIELD) protected _parentFormField: MatFormField,
              private cdr: ChangeDetectorRef, public elementRef: ElementRef) {
    this.control = new FormControl(['', null])
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (!!changes['disabled']) {
      this.updateDisableState()
    }

    if (typeof this.error === 'object') {
      const err = EsnUtils.cloneDeep(this.error);
      this.defaultError = err['default'];
      delete err.default;
      this.errorsList = Object.entries(err);
    } else {
      this.defaultError = this.error as string;
    }
  }

  ngOnInit(): void {
    this.handleMatAppearance();
    this.onChangeSub.add(
      this.control.valueChanges.subscribe((val) => {
        this.onChange(val);
        this.assignErrors();
        this.computeErrorMessage();
      }));

    try {
      this.parentControl = this._injector.get(NgControl);
    } catch (e) {
    }
  }

  onChange: any = () => {
  };
  onTouch: any = () => {
  };

  writeValue(value: any) {
    this.control.setValue(value);
  }

  registerOnChange(fn: any) {
    this.onChange = fn;
  }

  registerOnTouched(onTouched: Function) {
    this.onTouch = onTouched;
  }

  setDisabledState(isDisabled: boolean) {
    this.disabled = isDisabled
    this.updateDisableState()
  }

  private handleMatAppearance() {
    if (this.appearance === 'underline') {
      this._matAppearance = 'fill';
    } else if (this.appearance === 'fill') {
      this._matAppearance = 'outline';
    } else {
      this._matAppearance = this.appearance;
    }
  }

  private updateDisableState() {
    if (this.disabled) {
      this.control.disable();
    } else {
      this.control.enable();
    }
  }

  private assignErrors() {
    if (!this.parentControl) {
      return;
    }
    const localErrs = this.control?.errors;
    const parentErrors = this.parentControl?.control?.errors;
    if (!!localErrs || !!parentErrors) {
      const errors = {};
      Object.assign(errors, localErrs || {}, parentErrors || {});
      this.control?.setErrors(errors!);
    }
  }

  public computeErrorMessage() {
    const errObj = this.control?.errors;
    if (!errObj) {
      this.errorMessage = '';
    } else {
      const errs = Object.keys(errObj).filter((key) => !!errObj[key]);
      this.errorMessage = (this.errorsList.find((e) => errs.includes(e[0])) || [
        null,
        this.defaultError,
      ])[1] as string;
    }
  }

  ngOnDestroy() {
    this.onChangeSub.unsubscribe();
    if(!!this.observer){
      this.observer.disconnect()
    }
  }

  ngAfterViewInit(): void {
    this.initData();
    this.subscribeToChanges();

      if (this._matAppearance === 'outline' && this.label){
      const notchClass= '.mdc-notched-outline__notch'
          this.observer = new ResizeObserver(() => {
        this.formField._refreshOutlineNotchWidth();
        const notchElm = this.elementRef.nativeElement.querySelector(notchClass)
        notchElm.style.width = this._getNotchWidth();
      });
      const labelClass= '.mdc-floating-label'
  
      this.observer.observe(this.elementRef.nativeElement.querySelector(labelClass))
    }
  
  }

  _selectionChange($event: MatSelectChange) {
    this.selectionChange.emit($event.value)
  }

  _openedChange($event: boolean) {
    this.openedChange.emit($event);
  }

  subscribeToChanges() {
      this.onChangeSub.add(this.optionsList.changes.subscribe((EsnOptions: EsnOption[]) => {
        this.options = EsnOptions.map( EsnOption => {
          return { value: EsnOption.value, viewValue: EsnOption.viewValue, disabled: EsnOption.disabled };
        });
      }));
      this.onChangeSub.add(this.optionGroupsList.changes.subscribe((EsnOptionGroups: EsnOptionGroup[]) => {
        this.optionGroups = EsnOptionGroups.map(EsnOptionGroup => {
          return { label: EsnOptionGroup.label, options: EsnOptionGroup.options, disabled: EsnOptionGroup.disabled, viewValue: EsnOptionGroup.viewValue}
        })
      }));
  }

  private initData() {
    if(!this.optionGroupsList || !this.optionGroupsList.length){
      this.options = this.optionsList.map( EsnOption => {
        return { value:  EsnOption.value, viewValue:  EsnOption.viewValue, disabled:  EsnOption.disabled };
      });
    } else {
      this.optionGroups = this.optionGroupsList.map( EsnOptionGroup => {
        return { label: EsnOptionGroup.label, options: EsnOptionGroup.options, disabled: EsnOptionGroup.disabled, viewValue:  EsnOptionGroup.viewValue}
      })
    }
    this.cdr.detectChanges();
  }

  _getNotchWidth() {
    if (this.formField._notchedOutline?.open) {
      const NOTCH_ELEMENT_PADDING = 8;
      const NOTCH_ELEMENT_BORDER = 1;
      return this.formField._labelWidth > 0
        ? `calc(${
            this.formField._labelWidth
          }px * var(--mat-mdc-form-field-floating-label-scale, 0.75) + ${
            NOTCH_ELEMENT_PADDING + NOTCH_ELEMENT_BORDER
          }px)`
        : '0px';
    }

    return null;
  }
}
