import { AfterContentInit, Component, ContentChild, Directive, EventEmitter, HostListener, Input, Output } from '@angular/core';


@Directive({
  selector: '[esn-selectable-card-text], [esnSelectableCardText]',
})
export class EsnSelectableCardText {}

@Directive({
  selector: '[esn-selectable-card-illustration], [esnSelectableCardIllustration]',
})
export class EsnSelectableCardIllustration {}

@Component({
  selector: 'esn-selectable-card',
  templateUrl: './selectable-card.html',
  styleUrls: ['../selectable-card.scss'],
  host: {
    class: 'esn-selectable-card',
    '[class.esn-selectable-card--selected]': `selected`,
    '[class.esn-selectable-card--disabled]': `disabled || disabledByCardGroup`,
    '[class.esn-selectable-card--radio-mode]': `radioBtnModeByCardGroup`,

  },
})
export class EsnSelectableCard {
  @Input() selected: boolean = false;
  @Input() disabled: boolean = false;
  @Input() disabledByCardGroup: boolean = false;
  @Input() value?: any;
  @Input() radioBtnModeByCardGroup: boolean = false;


  @Output() selectionChange: EventEmitter<boolean> = new EventEmitter<boolean>();

  @ContentChild(EsnSelectableCardText) cardText: EsnSelectableCardText;
  @ContentChild(EsnSelectableCardIllustration) cardIllustration: EsnSelectableCardIllustration;

  @HostListener("click") onClick(){
    if(this.disabled || this.disabledByCardGroup){
      return;
    }
    this.selected = !this.selected
    this.selectionChange.emit(this.selected)
  }
  constructor() {}

}
