import { DOCUMENT } from '@angular/common';
import {
  Component,
  ElementRef,
  Inject,
  Input,
  OnChanges,
  Optional,
  SimpleChanges,
} from '@angular/core';
import { EsnIllustrationsRegistry } from './illustrations-registry';
import { EsnIllustrationName } from './illustrations';

const DEFAULT_BOXED: boolean = true;

@Component({
  selector: 'esn-illustration',
  template: '<ng-content></ng-content>',
  styleUrls: ['./illustration.component.scss'],
  host: {
    class: 'esn-illustration',
  },
})
export class EsnIllustration implements OnChanges {
  @Input() name: EsnIllustrationName;
  @Input() boxed: boolean = DEFAULT_BOXED;
  @Input() size: 'xs' | 'sm' | 'md' = 'md';

  public _svgIllustration: SVGElement;

  constructor(
    public element: ElementRef,
    public illustrationsRegistry: EsnIllustrationsRegistry,
    @Optional() @Inject(DOCUMENT) public document: any
  ) {}

  ngOnChanges(changes: SimpleChanges): void {
    const svgData = this.illustrationsRegistry.getIllustration(this.name);
    if (!svgData) return;

    if (this._svgIllustration)
      this.element.nativeElement.removeChild(this._svgIllustration);

    this._svgIllustration = this._svgElementFromString(svgData);
    this.element.nativeElement.appendChild(this._svgIllustration);
  }

  public _svgElementFromString(svgContent: string): SVGElement {
    const div = this.document.createElement('DIV');
    div.innerHTML = svgContent;
    return (
      div.querySelector('svg') ||
      this.document.createElementNS('http://www.w3.org/2000/svg', 'path')
    );
  }
}
