import { Injectable } from "@angular/core";
import { ToasterService } from "../../../components/toaster/public-api";

export type EsnNotificationType = 'info' | 'error' | 'success';

@Injectable()
export abstract class EsnNotificationService {
  abstract showNotif(type: EsnNotificationType, title: string, message?: string): void;
}

@Injectable()
export class EsnToasterNotificationService
  implements EsnNotificationService
{
  constructor(public toasterService: ToasterService) {}

  showNotif(type: EsnNotificationType, title: string, message: string): void {
    this.toasterService.showToaster(type, title, message);
  }
}

export class EsnConsoleNotificationService
  implements EsnNotificationService
{
  showNotif(type: EsnNotificationType, title: string, message: string): void {
    console.warn(
      `${title}: ${message}\n\nConsider providing an implementation of EsnApiCallWrapperNotificationService to display this message to the user`
    );
  }
}

export const ESN_GLOBAL_CONFIGURATION_DEFAULT_NOTIFICATION_SERVICE =
EsnToasterNotificationService;
