import { Injectable, OnInit } from '@angular/core';
import { BehaviorSubject } from 'rxjs/internal/BehaviorSubject';
import { debounceTime, filter } from 'rxjs/operators';
import { UploadItem } from './model/uploadState';

@Injectable({
  providedIn: 'root',
})
export class EsnUploadStateService implements OnInit {
  public documentsUploadProgress$: BehaviorSubject<number | null> =
    new BehaviorSubject<number | null>(null);

  public uploadOngoing$: BehaviorSubject<boolean | null> = new BehaviorSubject<
    boolean | null
  >(null);

  public displayUpload$: BehaviorSubject<boolean | null> = new BehaviorSubject<
  boolean | null
  >(null);


  constructor() {}

  ngOnInit(): void {
    this.uploadOngoing$
      .pipe(filter((x) => !!x))
      .subscribe(() => this.documentsUploadProgress$.next(null));
  }

  public getDisplayUpload() {
    return this.displayUpload$.pipe(debounceTime(500));
  }

  public getUploadOngoing() {
    return this.uploadOngoing$.pipe(debounceTime(500));
  }

  public updateUploadProgress(uploadItems: UploadItem[]) {
    const uploads = uploadItems.reduce(
      (acc, curr) => ({
        total: acc.total + curr.nbChunksTotal,
        remaining: acc.remaining + curr.nbChunksRemaining,
      }),
      { total: 0, remaining: 0 }
    );

    this.documentsUploadProgress$.next(
      Math.floor(
        !uploads.remaining
          ? 100
          : ((uploads.total - uploads.remaining) * 100) / uploads.total
      )
    );
  }

  public uploadStarts(displayUpload: Boolean) {
    this.uploadOngoing$.next(true);
    if(displayUpload){
      this.displayUpload$.next(true);
    }
  }

  public uploadEnds(hideUpload: Boolean) {
    this.uploadOngoing$.next(false);
    if(hideUpload){
      this.displayUpload$.next(false);
    }
  }
}
