import {
  AfterViewInit,
  Component,
  ContentChildren,
  Input,
  OnChanges,
  QueryList,
  SimpleChanges,
  ViewChild,
} from '@angular/core';
import { EsnPaginator } from '../../paginator/paginator.component';
import { EsnColumnDef } from '../cell';
import { EsnTable } from '../table';
import { EsnAsyncDataSource } from './esn-async-datasource';
import { EsnAsyncDataSourceAdapter } from './esnAsyncTableAdapter';

@Component({
  selector: 'esn-async-table',
  templateUrl: './async-table.component.html',
  styleUrls: ['./async-table.component.scss'],
})
export class EsnAsyncTable implements OnChanges, AfterViewInit {
  @Input() dataSource: EsnAsyncDataSource<any>;
  @Input() displayedColumns: string[];

  @Input() hidePageSize: boolean | string = false;
  @Input() pageSizeOptions: number[] = [10, 20, 50];
  @Input() showFirstLastButtons: boolean | string = true;


  public numberOfResults: number = 0;

  @ViewChild(EsnTable, { static: true }) table: EsnTable<any>;
  @ViewChild(EsnPaginator, { static: false }) paginator: EsnPaginator;
  @ContentChildren(EsnColumnDef) columnDefs: QueryList<EsnColumnDef>;



  constructor() {}

  public refresh() {
    if (this.paginator) {
      this.paginator.pageIndex = 0;
    }
    this.load()
  }

  public load() {
    this.dataSource.load(
      this.paginator.pageIndex,
      this.paginator.pageSize
    );
  }

  ngOnChanges(changes: SimpleChanges): void {
    if(!!changes['dataSource']) {
      this.dataSource.numberOfResults$.subscribe(
        (nb: number) => (this.numberOfResults = nb)
      );
    }
  }

  ngAfterContentInit() {
    this.columnDefs.forEach((columnDef) => this.table.addColumnDef(columnDef));
    
  }

  ngAfterViewInit(): void {
    this.paginator.page.subscribe(() => this.load());
    this.load();
  }
}
