import {
  Component,
  EventEmitter,
  Input,
  OnChanges,
  OnInit,
  Output,
  SimpleChanges,
} from '@angular/core';
import { ApiErrorDisplay } from '../../utils/model/public-api';

@Component({
  selector: 'esn-async-content-wrapper',
  templateUrl: './async-content-wrapper.component.html',
  styleUrls: ['./async-content-wrapper.component.scss'],
})
export class EsnAsyncContentWrapper implements OnInit, OnChanges {
  @Input() isLoading: boolean = false;
  @Input() errorDisplay?: ApiErrorDisplay;
  @Input() isEmpty: boolean = false;
  @Input() emptyStateText: string = 'Aucun élément';
  @Input() errorText: string = 'Une erreur est survenue';
  @Input() errorActionText: string = 'Réessayer';

  @Output() errorActionClick: EventEmitter<void> = new EventEmitter<void>();
  @Output() contentDisplayed: EventEmitter<void> = new EventEmitter<void>();

  constructor() {}

  ngOnInit(): void {}

  ngOnChanges(changes: SimpleChanges): void {
    if (!this.isLoading && !this.errorDisplay && !this.isEmpty) {
      this.contentDisplayed.emit();
    }
  }

  public onErrorActionClick() {
    this.errorActionClick.emit();
  }
}
