import { CollectionViewer, DataSource } from '@angular/cdk/collections';
import { Injectable } from '@angular/core';
import { BehaviorSubject, from, Observable, of } from 'rxjs';
import { debounceTime, map, switchMap, tap } from 'rxjs/operators';
import { EsnAsyncDataSource } from './esn-async-datasource';
import { EsnAsyncDataSourceAdapter } from './esnAsyncTableAdapter';


@Injectable()
export class EsnAsyncDataSourceFactory {
  constructor(public adapter: EsnAsyncDataSourceAdapter) {
    
  }

  public createDataSource<T>(funcToCall: Function) {
    return new EsnAsyncDataSource<T>(funcToCall, this.adapter);
  }
}
