import {
  AfterViewInit,
  Component,
  ElementRef,
  EventEmitter,
  Input, OnChanges,
  OnInit,
  Output, SimpleChanges,
  ViewChild,
} from '@angular/core';
import { EsnUtils } from '../../../utils/public-api';

@Component({
  selector: 'esn-file-selector',
  templateUrl: './file-selector.component.html',
  styleUrls: ['./file-selector.component.scss'],
  host: {
    class: 'esn-file-selector',
  },
})
export class EsnFileSelector implements AfterViewInit, OnInit, OnChanges {
  @Input() multiple: boolean = true;
  @Input() disabled: boolean = true;
  @Input() accept: string = '*';
  @Input() fileTypesLabel?: string;
  @Input() text?: string;
  @Input() maxSize?: number;
  @Output() filesAdded: EventEmitter<File[]> = new EventEmitter();

  public identifier: string = '';
  public acceptedExtensions: string | undefined = '*';

  constructor(public elementRef: ElementRef) {}

  ngOnInit(): void {
    this.identifier = EsnUtils.generateRandomUuid();
  }

  async ngAfterViewInit(): Promise<void> {
    if (this.elementRef) {
      const dropArea = this.elementRef.nativeElement;

      dropArea.addEventListener('dragover', (event: any) => {
        event.stopPropagation();
        event.preventDefault();
        dropArea.classList.add('esn-file-selector--active');
        // Style the drag-and-drop as a "copy file" operation.
        if (event && event.dataTransfer) {
          event.dataTransfer.dropEffect = 'copy';
        }
      });

      dropArea?.addEventListener('dragleave', (event: any) => {
        dropArea.classList.remove('esn-file-selector--active');
      });

      dropArea?.addEventListener('drop', (event: any) => {
        event.stopPropagation();
        event.preventDefault();
        dropArea.classList.remove('esn-file-selector--active');
        if (event && event.dataTransfer) {
          this.filesAdded.emit(event.dataTransfer.files as unknown as File[]);
        }
      });

      if (window.FileList && window.File && dropArea) {
        dropArea.addEventListener('change', async (event: any) => {
          this.filesAdded.emit(event.target.files);
          event.target.value = null;
        });
      }
    }
  }

  ngOnChanges(changes: SimpleChanges): void {
    if(changes['fileTypesLabel']) {
      this.acceptedExtensions = this.fileTypesLabel?.split(',').map(s => '.' + s.trim()).join(',');
    }
  }
}
