import {
  animate,
  state,
  style,
  transition,
  trigger,
} from '@angular/animations';
import { Component, Input, OnInit, Optional } from '@angular/core';
import { ActivatedRoute, NavigationEnd, Router } from '@angular/router';
import { Subscription } from 'rxjs';
import { filter, take } from 'rxjs/operators';
import { ConfirmationDialogComponent } from '../confirmation-dialog';
import { EsnDialog, EsnDialogRef } from '../dialog';
import { DraftTmpElement, EsnDraftService } from '../../utils/services/drafts';
import { EsnWizardStep, EsnWizardStepperStep } from './model/step-parameters';

@Component({
  selector: 'esn-wizard',
  templateUrl: './wizard.component.html',
  styleUrls: ['./wizard.component.scss'],
  host: {
    class: 'esn-wizard',
    '[class.loading]': `isUploadOngoing`,
  },
  animations: [
    trigger('enteringAnimation', [
      state(
        'entered',
        style({
          opacity: 1,
          transform: 'translateX(0)',
        })
      ),
      state(
        'enterFromRight',
        style({
          opacity: 0,
          transform: 'translateX(100%)',
        })
      ),
      state(
        'enterFromLeft',
        style({
          opacity: 0,
          transform: 'translateX(-100%)',
        })
      ),
      transition('* => entered', [animate('0.2s')]),
    ]),
  ],

})
export class EsnWizard implements OnInit {
  @Input() title: string;
  @Input() createButtonLabel: string;
  @Input() steps: EsnWizardStep[];
  @Input() stepperSteps: {
    [key in string]: EsnWizardStepperStep;
  };
  @Input() getRedirectPathOnCreationSuccess: (createdId: string | boolean) => string | void = ()=> {};
  @Input() redirectUrlOnClose?: string;
  @Input() getSubtitle: (elm: DraftTmpElement) => string | undefined = (elm) =>
    undefined;
  @Input() isRouted: boolean = true;
  // The  element uuid can be passed if the wizard is not routed
  @Input() elementUuid?: string;
  // If true the the element is created on init
  @Input() initElement: boolean = false;
  @Input() closeConfirmDialogData?: any;
  @Input() onSuccess: (createdId: string | boolean) => void = () => {};



  public contentPhase: string = 'entered';
  subscriptions: Subscription = new Subscription();
  public subtitle?: string;
  public currentStep: string;

  public stepperStepEnum: string[];
  public stepToDisplay: string;

  public initialElement: DraftTmpElement;
  public currentStepIndex = 0;
  public nextStepLoading: boolean = false;
  public isLastStep: boolean = false;
  public isUploadOngoing: boolean = false;
  public shouldProcessStepChange: boolean = true;
  constructor(
    @Optional() public dialogRef: EsnDialogRef<ConfirmationDialogComponent>,
    public activatedRoute: ActivatedRoute,
    public router: Router,
    public elementCreationService: EsnDraftService<DraftTmpElement>,
    public dialog: EsnDialog
  ) {
    // this._listenForRouteEvents();
  }

  get nextStepDisabled() {
    console.log({aasteps: this.steps, currIndex: this.currentStepIndex})
    
    return (
      !!this.elementCreationService?.forms[
        this.steps[this.currentStepIndex || 0]!.path!
      ]!.invalid || this.nextStepLoading
    );
  }

  async ngOnInit(): Promise<void> {
    this.stepperStepEnum = Object.keys(this.stepperSteps);

    this.elementCreationService.initialElement$.subscribe((el) => {
      this.initialElement = el!;
      this.subtitle = this.getSubtitle(el!);
    });
    if (this.isRouted) {
      this._listenForRouteEvents();
    } else {
      this.stepToDisplay = this.steps[0].path;
      if(this.initElement){
        const elm = await this.elementCreationService.initElementInDb();
        this.elementUuid = elm.externalUuid;
      }
      if(!this.elementUuid){
        throw `No element uuid was provided. Either use the wizard in routed mode ([isRouted]="true"), use the initElement option to initialize an element automaticaly ([initElement]="true"), or provide the uuid of a draft element that exists in db (elementUuid="XXXX")`
      }
      this.elementCreationService.init(this.elementUuid!);
    }

    this._refreshCurrentStep();
  }

  public async onCloseClick() {
    if(this.elementCreationService.elementDoesntExist){
      await this.closeAndRedirect();
    }
    if (!this.elementCreationService.initialized) {
      return;
    }
    if (this.elementCreationService.noChangesMade()) {
      await this.elementCreationService.discardChanges();
      await this.closeAndRedirect();
      return;
    }
    this.dialog
      .open(ConfirmationDialogComponent, {
        disableClose: false,
        autoFocus: false,
        data: this.closeConfirmDialogData || {
          title: 'Quitter la page',
          paragraphs: [
            'Vous êtes sur le point de quitter cette page. Souhaitez-vous sauvegarder les modifications ?',
          ],
          cancelLabel: 'Ne pas sauvegarder',
          confirmLabel: 'Sauvegarder',
          thirdButton: true,
        },
      })
      .componentInstance.decision.subscribe(async (confirm: any) => {
        if (confirm) {
          await this.elementCreationService.saveChanges();
        } else {
          await this.elementCreationService.discardChanges();
        }
        await this.closeAndRedirect();
      });
  }

  _displayIncompleteDialog(stepPath: string) {
    this.dialog
      .open(ConfirmationDialogComponent, {
        disableClose: false,
        autoFocus: false,
        data: {
          title: 'Formulaire incomplet',
          paragraphs: [
            "Certaines étapes du formulaire n'ont pas été complétées.",
          ],
          cancelLabel: 'Annuler',
          confirmLabel: "Aller à l'étape incomplète",
        },
      })
      .componentInstance.decision.subscribe(async (confirm: any) => {
        if (confirm) {
          const incompleteStep =
            this.elementCreationService.getFirstIncompleteStep();
          this._saveAndGoToStep(
            this.steps.findIndex((s) => s.path == incompleteStep)
          );
        }
      });
  }

  get selectedStepperStepIndex() {
    return this.stepperStepEnum.findIndex(
      (step) => this.steps[this.currentStepIndex].stepperStep == step
    );
  }

  public handleBackClick() {
    this._saveAndGoToStep(Math.max(this.currentStepIndex - 1, 0));
  }

  public async handleNextClick() {
    this._saveAndGoToStep(
      Math.min(this.currentStepIndex + 1, this.steps.length - 1)
    );
  }

  async _saveAndGoToStep(stepIndex: number): Promise<void> {
    this.shouldProcessStepChange = false;
    this.elementCreationService.updateAndSave();

    this.nextStepLoading = true;
    if (this.isRouted) {
      this.router.navigate(['./' + this.steps[stepIndex].path], {
        relativeTo: this.activatedRoute,
      });
    } else {
      this.stepToDisplay = this.steps[stepIndex].path;
      this._refreshCurrentStep();
    }

    this.contentPhase =
      stepIndex > this.currentStepIndex ? 'enterFromRight' : 'enterFromLeft';
    setTimeout(() => (this.shouldProcessStepChange = true));
  }

  public backToHome(): void {
    this.dialogRef?.close();
     if (!!this.redirectUrlOnClose) {
      this.router.navigate([this.redirectUrlOnClose]);
    } 
  }

  ngOnDestroy() {
    this.subscriptions.unsubscribe();
  }

  private closeAndRedirect(): Promise<boolean> {
    this.dialogRef?.close();
    const redirectUrl = this.redirectUrlOnClose;
    if (!!redirectUrl) {
      return this.router.navigate([redirectUrl]);
    } 
    return Promise.resolve(true)
  }

  async onStepChange(event: any) {
    if (!this.shouldProcessStepChange) {
      return;
    }
    const incompleteStep = this.steps.findIndex(
      (s) => s.path == this.elementCreationService.getFirstIncompleteStep()
    );
    const targetStep = this.steps.findIndex(
      (s) =>
        this.stepperStepEnum.findIndex(
          (stepperStep) => stepperStep == s.stepperStep
        ) == event.selectedIndex
    );

    if (
      targetStep != this.currentStepIndex &&
      (incompleteStep === -1 ||
        targetStep === 0 ||
        +incompleteStep >= targetStep)
    ) {
      this._saveAndGoToStep(targetStep);
    }
  }

  async onCreate() {
    const incompleteStep = this.elementCreationService.getFirstIncompleteStep();
    if (!!incompleteStep) {
      this._displayIncompleteDialog(incompleteStep);
      return;
    }
    this.isUploadOngoing = true;
    const id = await this.elementCreationService.sendElementToServer();
    if (!!id) {
      // this.toProcessCountersService.updateCounts();
      this.dialogRef?.close();
      this.onSuccess(id);
      const redirectPath = this.getRedirectPathOnCreationSuccess(id);
      if(!!redirectPath){
        this.router.navigate([redirectPath]);
      }
    }
    this.isUploadOngoing = false;
  }

  couldCreate(): boolean {
    return !this.steps.find(
      (s) => this.elementCreationService.forms[s.path].invalid
    );
  }

  _listenForRouteEvents() {
    this.activatedRoute.params.pipe(take(1)).subscribe((params) => {
      this.elementCreationService.init(params['uuid']);
    });
    this.subscriptions.add(
      this.router.events
        .pipe(filter((e) => e instanceof NavigationEnd))
        .subscribe((val) => {
          this._refreshCurrentStep();
        })
    );
  }

  _refreshWithAnimation() {
    setTimeout(() => {
      this.nextStepLoading = false;
      setTimeout(() => {
        this.contentPhase = 'entered';
      });
    });
  }

  _refreshCurrentStep() {
    const nextStep = this.isRouted
      ? this.activatedRoute.snapshot.firstChild?.routeConfig?.path
      : this.stepToDisplay;

      console.log('refreshCurrStep', {nextStep})
    this.currentStepIndex = this.steps.findIndex((s) => s.path === nextStep);
    this.currentStep = nextStep!;
    this._refreshWithAnimation();
  }

  public getStepperStepCompletion(step: string) {
    return (
      this.steps
        .filter((s) => s.stepperStep == step)
        .reduce(
          (acc, curr) =>
            acc && this.elementCreationService.forms[curr.path].valid,
          true
        ) &&
      !(this.selectedStepperStepIndex < this.stepperStepEnum.indexOf(step))
    );
  }
}
