export interface FileMetadataDto {
  aip?: FileMetadataDto.AipEnum;
  checksum?: string;
  commonReference?: string;
  physicalFilename?: string;
  contentType?: string;
  description?: string;
  documentGDDType?: FileMetadataDto.DocumentGDDTypeEnum;
  documentGddIdentifier?: string;
  extension?: string;
  externalUuid?: string;
  fileNature?: string;
  fileProvider?: string;
  filename?: string;
  id?: number;
  initialReference?: string;
  partNumber?: number;
  parts?: Array<FileMetadataDto>;
  size?: number;
  uuid?: string;
  version?: number;
  completionState?: FileMetadataDto.CompletionStateEnum;
  isDeletable?: boolean;
}

export namespace FileMetadataDto {
  export type AipEnum = 'OUI' | 'NON';
  export const AipEnum = {
    OUI: 'OUI' as AipEnum,
    NON: 'NON' as AipEnum,
  };
  export type DocumentGDDTypeEnum = 'SWT_DOC_GDD';
  export const DocumentGDDTypeEnum = {
    SWTDOCGDD: 'SWT_DOC_GDD' as DocumentGDDTypeEnum,
  };

  export type CompletionStateEnum =
    | 'COMPLETED'
    | 'PARTIALY_COMPLETED'
    | 'IN_PROGRESS'
    | 'ABORTED';
  export const CompletionStateEnum = {
    COMPLETED: 'COMPLETED' as CompletionStateEnum,
    PARTIALYCOMPLETED: 'PARTIALY_COMPLETED' as CompletionStateEnum,
    INPROGRESS: 'IN_PROGRESS' as CompletionStateEnum,
    ABORTED: 'ABORTED' as CompletionStateEnum,
  };
}

export interface CustomFileMetadataDto extends FileMetadataDto {
  blob?: Blob;
}

export interface DocumentItem {
  metadata: FileMetadataDto | CustomFileMetadataDto;
  blobUuid: string;
  thumbnailBase64?: string;
  loading: boolean;
  uploading: boolean;
  uploadingProgress: number;
  msg: string;
  actions: DocumentActions;
}

export interface BlobItem {
  uuid: string;
  filename: string;
  size: number;
  contentType: string;
  blobString: string;
}

export interface Thumbnail {
  uuid: string;
  url: string;
}

export enum DocumentReplicationStatus {
  NOTREPLICATED = 'NOTREPLICATED',
  REPLICATING = 'REPLICATING',
  REPLICATED = 'REPLICATED',
  ERROR = 'ERROR',
}

export interface DocumentEventEmitter {
  event: DocumentEventType;
  document: DocumentItem;
}

export enum DocumentEventType {
  DELETE = 'DELETE',
  DOWNLOAD = 'DOWNLOAD',
  UPLOAD = 'UPLOAD',
  VIEW = 'VIEW',
  REPLICATE = 'REPLICATE',
}

export interface DocumentActions {
  delete: boolean;
  download: boolean;
  upload: boolean;
  view?: boolean;
}

export interface Image {
  dataUrl: string;
  width: number;
  height: number;
}

export type BannerTypeEnum =
  | 'info'
  | 'pending'
  | 'warning'
  | 'error'
  | 'success';


export const CONTENT_TYPES_MAP: any = {
  'application/pdf': 'pdf',
  'application/vnd.openxmlformats-officedocument.wordprocessingml.document':
    'doc',
  'application/msword': 'doc',
  'application/vnd.ms-powerpoint': 'ppt',
  'application/vnd.openxmlformats-officedocument.presentationml.presentation':
    'ppt',
  'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet': 'xls',
  'image/jpeg': 'img',
  'image/bmp': 'img',
  'image/png': 'img',
  'image/gif': 'img',
};