import {
  AfterContentInit,
  AfterViewInit,
  ChangeDetectorRef,
  Component,
  ElementRef,
  EventEmitter,
  Injector,
  Input,
  OnChanges,
  OnInit,
  Output,
  SimpleChanges,
  ViewChild,
  forwardRef,
} from '@angular/core';
import {
  ControlValueAccessor,
  FormBuilder,
  FormControl,
  NG_VALUE_ACCESSOR,
  NgControl,
  AbstractControl,
  FormGroup,
  Validator,
  NG_VALIDATORS,
} from '@angular/forms';
import { _MatAutocompleteBase } from '@angular/material/autocomplete';
import { MatChipGrid } from '@angular/material/chips';
import {
  MatFormField,
  MatFormFieldAppearance,
} from '@angular/material/form-field';
import { Subscription } from 'rxjs';
import { EsnUtils } from '../../utils/public-api';

export type EsnInputError = string | { [key: string]: string };

@Component({
  selector: 'esn-input-text-base',
  template: '',
  host: {
    class: 'esn-input',
    '[class.esn-input-sm]': `size === 'sm'`,
    '[class.esn-input-md]': `size === 'md'`,
    '[class.esn-input-fill]': `appearance === 'fill'`,
    '[class.esn-input-outline]': `appearance === 'outline'`,
    '[class.esn-input-underline]': `appearance === 'underline'`,
    '[class.esn-input-dark]': `theme === 'dark'`,
    '[class.esn-input-light]': `theme === 'light'`,
  },
})
export abstract class _EsnInputBase
  implements
    OnChanges,
    OnInit,
    AfterContentInit,
    ControlValueAccessor,
    AfterViewInit,
    Validator
{
  // Input native
  @Input() minlength: number | string | null = null;
  @Input() maxlength: number | string | null = null;
  @Input() pattern: string | RegExp = '';
  @Input() placeholder: string = '';
  @Input() readonly: boolean | string = false;
  @Input() required: boolean | string = false;
  @Input() disabled: boolean | string = false;
  @Input() value?: string;

  // Mat replacements
  @Input() label?: string;
  @Input() hint?: string;
  @Input() error?: EsnInputError;
  @Input() hintAlign: 'start' | 'end' = 'end';
  @Input() floatLabel: 'auto' | 'always' = 'auto';

  // Custom features
  @Input() counterHint?: boolean | string;
  @Input() size: 'sm' | 'md' = 'sm';
  @Input() appearance: 'fill' | 'outline' | 'underline' = 'outline';
  @Input() theme: 'dark' | 'light' | null = null;

  @ViewChild('nativeInput') public nativeInput: ElementRef;

  @Output() focus: EventEmitter<void> = new EventEmitter<void>();
  @Output() blur: EventEmitter<void> = new EventEmitter<void>();

  public errorsList: string[][] = [];
  public defaultError?: string;

  public parentControl: NgControl;
  public onChangeSub: Subscription = new Subscription();
  public errorMessage: string;

  public _matAppearance: MatFormFieldAppearance;
  public control: FormControl;
  public observer: ResizeObserver;

  onChange: any = () => {};
  onTouch: any = () => {};

  shouldAddAsterisk: boolean = false;

  @ViewChild(MatFormField) public formField: MatFormField;

  constructor(private _injector: Injector, public elementRef: ElementRef) {
    this.control = new FormControl(null);
  }

  ngAfterViewInit(): void {
    if (this._matAppearance === 'outline' && this.label) {
      const notchClass = '.mdc-notched-outline__notch';
      this.observer = new ResizeObserver(() => {
        this.formField._refreshOutlineNotchWidth();
        const notchElm =
          this.elementRef.nativeElement.querySelector(notchClass);
        notchElm.style.width = this._getNotchWidth();
      });
      const labelClass = '.mdc-floating-label';

      this.observer.observe(
        this.elementRef.nativeElement.querySelector(labelClass)
      );
    }
  }

  ngAfterContentInit(): void {
    this.processShouldAddAsterisk();
  }

  ngOnInit(): void {
    this.processBooleanInputs();
    this.handleMatAppearance();

    try {
      this.parentControl = this._injector.get(NgControl);
    } catch (e) {}
    this.onChangeSub.add(
      this.control.valueChanges.subscribe((val) => {
        this.onChange(val);
        this.assignErrors();
        this.computeErrorMessage();
      })
    );

    this.blur.subscribe(() => {
      this.assignErrors();
      this.computeErrorMessage();
    });
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (!!changes['disabled']) {
      this.updateDisableState();
    }

    if (typeof this.error === 'object') {
      const err = EsnUtils.cloneDeep(this.error);
      this.defaultError = err['default'];
      delete err.default;
      this.errorsList = Object.entries(err);
    } else {
      this.defaultError = this.error as string;
    }
  }

  writeValue(value: any) {
    this.control.setValue(value);
  }

  registerOnChange(fn: any) {
    this.onChange = fn;
  }

  registerOnTouched(onTouched: Function) {
    this.onTouch = () => {
      onTouched()
      this.control.markAsTouched()
    };
  }

  setDisabledState(isDisabled: boolean) {
    this.disabled = isDisabled;
    this.updateDisableState();
  }

  public computeErrorMessage() {
    const errObj = this.control?.errors;
    if (!errObj) {
      this.errorMessage = '';
    } else {
      const errs = Object.keys(errObj).filter((key) => !!errObj[key]);
      this.errorMessage = (this.errorsList.find((e) => errs.includes(e[0])) || [
        null,
        this.defaultError,
      ])[1] as string;
    }
  }

  public assignErrors() {
    EsnUtils.assignErrors(this.control, this.parentControl);
    if(this.parentControl?.touched && !this.control?.touched){
      this.onTouch()
    }
  }

  public processBooleanInputs() {
    // In order to be able to use them like that: <esn-input readonly></esn-input>
    this.readonly = this.readonly || this.readonly === '';
    this.disabled = this.disabled || this.disabled === '';
    this.required = this.required || this.required === '';
    this.counterHint = this.counterHint || this.counterHint === '';

    if (this.counterHint && !this.maxlength) {
      this.counterHint = false;
      console.warn(
        `Attribute 'counterHint' was used on esn-input without 'maxlength', the hint will not be displayed`
      );
    }
    if (this.counterHint && !!this.hint && this.hintAlign === 'end') {
      this.counterHint = false;
      console.warn(
        `Attribute 'counterHint' was used on an esn-input that already has a hint aligned 'end', the counterHint will not be displayed`
      );
    }
  }

  public handleMatAppearance() {
    if (this.appearance === 'underline') {
      this._matAppearance = 'fill';
    } else if (this.appearance === 'fill') {
      this._matAppearance = 'outline';
    } else {
      this._matAppearance = this.appearance;
    }
  }

  public updateDisableState() {
    if (this.disabled) {
      this.control.disable();
    } else {
      this.control.enable();
    }
  }

  ngOnDestroy() {
    this.onChangeSub.unsubscribe();
    if (!!this.observer) {
      this.observer.disconnect();
    }
  }

  processShouldAddAsterisk() {
    const isParentRequired = this.isControlRequired(
      this.parentControl?.control
    );
    this.shouldAddAsterisk = isParentRequired && !this.required;
  }

  isControlRequired(control: AbstractControl | null) {
    return (
      control?.validator &&
      (control?.validator({} as AbstractControl) || {})['required']
    );
  }

  _getNotchWidth() {
    if (this.formField._notchedOutline?.open) {
      const NOTCH_ELEMENT_PADDING = 8;
      const NOTCH_ELEMENT_BORDER = 1;
      return this.formField._labelWidth > 0
        ? `calc(${
            this.formField._labelWidth
          }px * var(--mat-mdc-form-field-floating-label-scale, 0.75) + ${
            NOTCH_ELEMENT_PADDING + NOTCH_ELEMENT_BORDER
          }px)`
        : '0px';
    }

    return null;
  }

  
  validate(control: AbstractControl) {
    return !!this.control.validator ? this.control.validator(control) : null;;
  }
}

