// ESN DATE RANGE PICKER
@Component({
  selector: 'esn-date-range-picker',
  templateUrl: './datepicker/date-range-picker.html',
  styleUrls: ['./input.scss'],
  host: {
    class: 'esn-date-range-picker',
  },
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: EsnDateRangePicker,
      multi: true,
    },
    {
      provide: NG_VALIDATORS,
      multi: true,
      useExisting: forwardRef(() => EsnDateRangePicker),
    },
  ],
})

export class EsnDateRangePicker extends _EsnDateInputBase implements AfterViewInit {
  @Input() placeholderStartDate: string = 'Start date';
  @Input() placeholderEndDate: string = 'End date';
  @Input() minStartDate: Date;
  @Input() maxStartDate: Date;
  @Input() minEndDate: Date;
  @Input() maxEndDate: Date;
  @Input() startDateErrorMessage = 'Invalid start date';
  @Input() endDateErrorMessage = 'Invalid end date';
  range = new FormGroup({
    start: new FormControl<Date | null>(null),
    end: new FormControl<Date | null>(null),
  });

  constructor(_injector: Injector, elementRef: ElementRef) {
    super(_injector, elementRef);

    this.range.valueChanges.subscribe(val => {
      this.onChange(val);
    })
  }
  
  override writeValue(value: any) {
    this.range.setValue(value);
  }

  override setDisabledState(isDisabled: boolean) {
    this.disabled = isDisabled;
    this.updateDisableState();
  }
  
  override updateDisableState() {
    if (this.disabled) {
      this.range.disable();
    } else {
      this.range.enable();
    }
  }
}
@Component({
  selector: 'esn-input-number',
  templateUrl: './number/input-number.component.html',
  styleUrls: ['./input.scss', './number/input-number.component.scss'],
  host: {
    class: 'esn-input-number',
    '[class.esn-input-number--focused]': 'isFocused',
    '[class.esn-input-number--error]': 'control.touched && !!control.errors',
    '[class.esn-input-number--no-buttons]': '!showButtons',
  },
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      multi: true,
      useExisting: EsnNumberInput,
    },
    {
      provide: NG_VALIDATORS,
      multi: true,
      useExisting: forwardRef(() => EsnNumberInput),
    },
    
  ],
})
export class EsnNumberInput extends _EsnInputBase implements OnInit {
  @Input() max: number | null;
  @Input() min: number | null;
  @Input() showButtons: boolean = true;
  public isFocused: boolean = false;

  previousValue?: number;

  public minusButtonClicked() {
    if (!this.disabled && ((!this.min && this.min !== 0) || +this.control.value > this.min)) {
      this.control.setValue(this.control.value - 1);
    }
  }

  public plusButtonClicked() {
    console.log({
      disa: this.control.disabled,
      max: this.max,
      maxSet: !!this.max || this.max === 0,
      valSup: this.control.value > this.max! - 1,
    });
    if (!this.disabled && ((!this.max && this.max !==0) || +this.control.value < this.max)) {
      this.control.setValue(this.control.value + 1);
    }
  }

  public onFocus() {
    this.isFocused = true;
    this.focus.emit();
  }

  public onBlur() {
    this.isFocused = false;
    this.blur.emit();
  }
}
