import {
  Component,
  EventEmitter,
  Input,
  OnChanges,
  OnInit,
  Output,
  SimpleChanges,
} from '@angular/core';
import {
  BehaviorSubject,
  Observable,
  Subscription,
  debounceTime,
  distinct,
  fromEvent,
  merge,
} from 'rxjs';

@Component({
  selector: 'esn-infinite-scroll',
  templateUrl: './infinite-scroll.component.html',
  styleUrls: ['./infinite-scroll.component.scss'],
})
export class EsnInfiniteScroll implements OnInit, OnChanges {
  @Input() callFunc: (page: number, pageSize: number) => Promise<any[]>;
  @Input() totalNumberOfResults: number;
  @Input() scrollElement: HTMLElement;

  @Input() refreshElementsSubject: BehaviorSubject<null>;
  @Input() pageSize: number = 20;

  // We'll want to display the spinner with the async-content-wrapper component
  // most of the time
  @Input() showLoaderOnRefresh: boolean = false;

  @Output() elmsUpdated: EventEmitter<any[]> = new EventEmitter<any[]>();

  public elements: any[] = [];
  public nbPageLoaded: number = 0;
  public isLoading: boolean = true;
  public subscriptions: Subscription = new Subscription();
  public scroll$!: Observable<any>;
  public resize$!: Observable<any>;
  public manualTrigger$: BehaviorSubject<null> = new BehaviorSubject<null>(
    null
  );

  constructor() {}

  ngOnInit(): void {
    if (!!this.refreshElementsSubject) {
      this.subscriptions.add(
        this.refreshElementsSubject.pipe(debounceTime(200)).subscribe(() => {
          this.refreshElements();
        })
      );
    }
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes['scrollElement'] && !!this.scrollElement) {
      this.handleScroll();
    }
  }

  public async refreshElements(): Promise<void> {
    this.nbPageLoaded = 0;
    this.elements.length = 0;
    await this.loadNextPage(true);
    setTimeout(() => this.manualTrigger$.next(null));
  }

  public async loadNextPage(force: boolean = false): Promise<void> {
    if (this.isLoading && !force) {
      return;
    }
    this.isLoading = true;
    const nbPageLoadedBefore = this.nbPageLoaded;
    const newElms = await this.callFunc(this.nbPageLoaded, this.pageSize);
    if (this.nbPageLoaded === nbPageLoadedBefore) {
      this.nbPageLoaded = this.nbPageLoaded + 1;
      console.log({ newElms });
      if (!!newElms) {
        this.elements.push(...newElms);
      }
      this.elmsUpdated.emit(this.elements);
      this.isLoading = false;
    }
  }

  public handleScroll(): void {
    const scrollArea = this.scrollElement;
    console.log(scrollArea);
    this.scroll$ = fromEvent(scrollArea, 'scroll').pipe(
      debounceTime(100),
      distinct()
    );

    this.resize$ = fromEvent(window, 'resize').pipe(
      debounceTime(200),
      distinct()
    );

    this.subscriptions.add(
      merge(this.scroll$, this.resize$, this.manualTrigger$).subscribe(() => {
        setTimeout(async () => {
          if (
            !!scrollArea.scrollHeight && 
            scrollArea.scrollTop + scrollArea.offsetHeight >
            scrollArea.scrollHeight - 25
          ) {
            if (
              !this.isLoading &&
              this.totalNumberOfResults > this.elements.length
            ) {
              await this.loadNextPage();
              setTimeout(() => this.manualTrigger$.next(null));
            }
          }
        });
      })
    );
  }
}
