import {Component, EventEmitter, Inject} from '@angular/core';
import {ConfirmationDialogDataModel} from "../models/confirmationDialogDataModel";
import {Subject} from "rxjs";
import {EsnDialogRef} from "../../dialog/dialogRef";
import {ESN_DIALOG_DATA} from "../../dialog/dialog.service";

@Component({
  selector: 'lib-confirmation-dialog',
  templateUrl: './confirmation-dialog.component.html',
  styleUrls: ['./confirmation-dialog.component.scss']
})
export class ConfirmationDialogComponent {

  public decision: EventEmitter<any> = new EventEmitter();
  public thirdButtonClick: EventEmitter<void> = new EventEmitter();
  public loading: boolean = false;

  constructor(
    private dialogRef: EsnDialogRef<ConfirmationDialogComponent>,
    @Inject(ESN_DIALOG_DATA)
    public data: ConfirmationDialogDataModel) {}

    public confirm() {
      this.decision.emit(true);
      this.dialogRef.close();
    }
  
    public cancel() {
      this.decision.emit(false);
      this.dialogRef.close();
    }
  
    public thirdButtonClicked() {
      this.thirdButtonClick.emit();
      this.dialogRef.close();
    }
}
