import { Injectable } from "@angular/core";
import { ToasterService } from "../toaster/toaster.service";
import { EsnGlobalConfiguration, EsnNotificationType } from "../../utils/public-api";


@Injectable()
export abstract class EsnFileInputNotificationService {
  abstract showNotif(type: EsnNotificationType, title: string, message: string): void;
}

@Injectable()
export class FileInputToasterNotificationService
  implements EsnFileInputNotificationService
{
  constructor(public toasterService: ToasterService) {}

  showNotif(type: EsnNotificationType, title: string, message: string): void {
    this.toasterService.showToaster(type, title, message);
  }
}

export class FileInputConsoleNotificationService
  implements EsnFileInputNotificationService
{
  showNotif(type: EsnNotificationType, title: string, message: string): void {
    console.warn(
      `${title}: ${message}\n\nConsider providing an implementation of EsnFileInputNotificationService to display this message to the user`
    );
  }
}

@Injectable()
export class EsnFileInputGlobalConfigNotificationService
  implements EsnFileInputNotificationService
{
  constructor(public globalConf: EsnGlobalConfiguration){}
  showNotif(type: EsnNotificationType, title: string, message: string): void {
    this.globalConf.notificationService.showNotif(type, title, message);
  }
}

export const ESN_FILE_SELECTOR_DEFAULT_NOTIFICATION_SERVICE =
  FileInputToasterNotificationService;
