import { Inject, Injectable, Optional } from '@angular/core';
import { ApiCallWrapper, EsnApiCallWrapper } from '../../utils/public-api';
import { EsnNotificationsController } from './notifications-controller.service';
import { BehaviorSubject } from 'rxjs';
import { NOTIFICATION_TAGS } from './notification-configuration';
import { EsnNotificationProcessingService } from './notification-processing.service';

@Injectable()
export class EsnNotificationsService {
  public nbUnread$: BehaviorSubject<number> = new BehaviorSubject<number>(0);
  public nbNotViewed$: BehaviorSubject<number> = new BehaviorSubject<number>(0);
  public nbTotal$: BehaviorSubject<number> = new BehaviorSubject<number>(0);
  public lastReceivedSendDate: Date;

  constructor(
    public callWrapper: EsnApiCallWrapper,
    public controller: EsnNotificationsController,
    @Inject(NOTIFICATION_TAGS) public tags: string,
    @Optional() public processingService: EsnNotificationProcessingService,
  ) {}

  public getNotificationTags(){
    try{
      return this.processingService.getNotificationTags();
    } catch(err){
      return this.tags;
    }
  }

  public setUnread(nbUnread: number) {
    this.nbUnread$.next(nbUnread);
  }

  public getUnread() {
    return this.nbUnread$.asObservable();
  }

  public setNotViewed(nbNotViewed: number) {
    this.nbNotViewed$.next(nbNotViewed);
  }

  public getNotViewed() {
    return this.nbNotViewed$.asObservable();
  }

  public setTotal(nbTotal: number) {
    this.nbTotal$.next(nbTotal);
  }

  public getTotal() {
    return this.nbTotal$.asObservable();
  }

  public async markAllAsRead() {
    if (!this.lastReceivedSendDate) {
      return;
    }
    await this.callWrapper.makeApiCall(
      this.controller.doActionOnNotification(
        'READ',
        this.getNotificationTags(),
        this.lastReceivedSendDate.toISOString()
      )
    );
    this.refreshUnreadAndNotViewed();
  }

  public async markAllAsViewed() {
    if (!this.lastReceivedSendDate) {
      return;
    }
    await this.callWrapper.makeApiCall(
      this.controller.doActionOnNotification(
        'VIEWED',
        this.getNotificationTags(),
        this.lastReceivedSendDate.toISOString()
      )
    );
    this.refreshUnreadAndNotViewed();
  }

  public async markAsRead(notificationId: string) {
    await this.callWrapper.makeApiCall(
      this.controller.markNotificationAsRead(notificationId)
    );
    this.refreshUnreadAndNotViewed();
  }

  public async refreshUnreadAndNotViewed() {
    const resp = await this.getCount(this.getNotificationTags());
    if (!resp.error) {
      this.nbUnread$.next(resp.resp.totalNumberOfNotRead);
      this.nbNotViewed$.next(resp.resp.totalNumberOfNotDisplayed);
    }
  }

  public getNotifications(
    page?: number,
    size?: number,
    tags?: string,
    read?: boolean,
    dateBefore?: Date,
    dateAfter?: Date,
    criticality?: string
  ): Promise<ApiCallWrapper> {
    return this.callWrapper.makeApiCall(
      this.controller.getNotifications(
        page,
        size,
        tags,
        read,
        dateBefore,
        dateAfter,
        criticality
      ),
      {
        callLabel: 'récupération des notifications',
        notifyError: false,
      }
    );
  }

  public getCount(tags: string) {
    return this.callWrapper.makeApiCall(this.controller.getCounters(tags), {
      notifyError: false,
    });
  }

  public updateLastReveivedSendDate(date: string | Date) {
    if (
      !this.lastReceivedSendDate ||
      this.lastReceivedSendDate.getTime() < new Date(date).getTime()
    ) {
      this.lastReceivedSendDate = new Date(new Date(date).getTime() + 1);
    }
  }
}
