import {
  AfterViewInit,
  Component,
  ElementRef,
  Injector,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  SimpleChanges,
  TemplateRef,
  ViewChild,
  ViewEncapsulation,
} from '@angular/core';
import {
  ControlValueAccessor,
  FormBuilder,
  FormControl,
  NgControl,
  NG_VALUE_ACCESSOR,
} from '@angular/forms';
import { COMMA, ENTER } from '@angular/cdk/keycodes';
import { BehaviorSubject, Subscription } from 'rxjs';
import { MatAutocompleteSelectedEvent } from '@angular/material/autocomplete';
import { debounceTime, filter, tap } from 'rxjs/operators';
import { EsnInputError, EsnInputText } from '../../input/input';
import { TitleStrategy } from '@angular/router';
import { ThisReceiver } from '@angular/compiler';
import { EsnUtils } from '../../../utils/public-api';


@Component({
  selector: 'esn-autocomplete-select-base',
  templateUrl: './autocomplete-select-base.component.html',
  styleUrls: ['./autocomplete-select-base.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: EsnAutocompleteSelectBase,
      multi: true,
    },
  ],
  host: {
    class: 'esn-autocomplete-select-base',
    '[class.input-disabled]': `inputted?.disabled`,
  }
})
export class EsnAutocompleteSelectBase<T>
  implements OnInit, OnChanges, ControlValueAccessor, AfterViewInit
{
  @Input() queryFunc: (txt: string) => Promise<T[] | null>;
  @Input() idField: keyof T;
  @Input() multi?: boolean | string = false;
  @Input() label: string;
  @Input() hint: string;
  @Input() optionTemplate?: TemplateRef<any>;
  @Input() chipTemplate?: TemplateRef<any>;
  @Input() singleElemTemplate?: TemplateRef<any>;
  @Input() disabled: boolean | string = false;
  @Input() optionFilterFunc: (elm: T) => boolean = (elm) => true;
  @Input() error?: EsnInputError;
  @Input() minChars: number = 3;
  @Input() noResultsLabel: string = 'Aucun résultat trouvé';
  public inputted: FormControl = new FormControl();
  readonly separatorKeysCodes = [ENTER, COMMA] as const;
  @ViewChild('chipInput') input: EsnInputText;
  public chipInput: ElementRef<HTMLInputElement>;
  public autocompleteChoices: BehaviorSubject<T[]> = new BehaviorSubject<T[]>(
    []
  );
  public addChipStream$: BehaviorSubject<T | null> =
    new BehaviorSubject<T | null>(null);
  public selectedElems: T[] = [];
  public onChangeSub: Subscription = new Subscription();
  public isMulti: boolean = false;
  public noResults: boolean = false;
  public searchingResults: boolean = false;
  public isSearchInError: boolean = false;

  public removeFn: Function;

  onChange: any = () => {};
  onTouch: any = () => {};

  previousInputValue: string;
  nextInputValue: string;
  parentControl: NgControl;

  constructor(private _injector: Injector, public elementRef: ElementRef) {}

  async ngOnInit(): Promise<void> {
    try {
      this.parentControl = this._injector.get(NgControl);
      this.onChangeSub.add(
        this.parentControl.control?.valueChanges.subscribe(() =>
          setTimeout(() => this.assignErrors())
        )
      );
    } catch (e) {}

    this.removeFn = (elm: T) => this.remove(elm);

    if (!!this.multi || this.multi === '') {
      this.isMulti = true;
    }

    this.addChipStream$
      .pipe(
        filter((chip: T | null) => !!chip),
        debounceTime(20)
      )
      .subscribe((chip) => {
        this.selectedElems.push(chip!);
        this.onChangeWrapper(this.selectedElems);
      });

    this.previousInputValue = this.inputted.value;
    this.nextInputValue = this.inputted.value;

    this.inputted.valueChanges
      .pipe(
        tap((val) => {
          this.previousInputValue = this.nextInputValue;
          this.nextInputValue = val;
        }),
        debounceTime(250)
      )
      .subscribe(async (txt) => {
        this.searchResults(txt)
      });
  }

  async searchResults(txt: string) {
    this.noResults = false;
    if ((!!txt || txt === '') && txt.length > this.minChars - 1) {
      this.searchingResults = true;
      let queryResults = null
      try{
        queryResults = await this.queryFunc(txt);
      } catch(e){}
      this.handleSearchResponse(queryResults);
      this.searchingResults = false;
    } else {
      this.autocompleteChoices.next([]);
    }
  }

  public onFocus(){
    if(!this.minChars && !this.inputted.value && !this.autocompleteChoices.getValue().length){
      this.searchResults('');
    }
  }

  ngAfterViewInit(): void {
    this.input?.nativeInput?.nativeElement?.addEventListener(
      'keydown',
      this.removeLastChipOnBackspace
    );
  }

  removeLastChipOnBackspace = (e: any) => {
    if (
      e.keyCode === 8 &&
      !this.inputted.value &&
      !!this.selectedElems.length
    ) {
      this.remove(this.selectedElems[this.selectedElems.length - 1]);
    }
    this.input?.nativeInput?.nativeElement?.focus();
  };

  ngOnChanges(changes: SimpleChanges): void {
    if (!!changes['disabled']) {
      this.updateInputDisabled();
    }
  }

  handleSearchResponse(queryResults: T[] | null) {
    if (!!queryResults) {
      this.isSearchInError = false;
      this.autocompleteChoices.next(
        queryResults.filter(
          (elem) =>
            !this.selectedElems
              .map((u) => u[this.idField])
              .includes(elem[this.idField])
        )
      );
      this.noResults = !this.autocompleteChoices.value.length;
    } else {
      this.isSearchInError = true;
    }
  }

  public updateInputDisabled() {
    if (!!this.disabled || (!this.isMulti && !!this.selectedElems.length)) {
      this.inputted.disable();
    } else {
      this.inputted.enable();
    }
  }

  writeValue(value: any) {
    if (!this.isMulti) {
      this.selectedElems = !!value ? [value] : [];
    } else {
      this.selectedElems = !!value ? value : [];
    }
    this.updateInputDisabled();
  }

  registerOnChange(fn: any) {
    this.onChange = fn;
  }

  registerOnTouched(onTouched: Function) {
    this.onTouch = onTouched;
  }

  setDisabledState(isDisabled: boolean) {
    this.disabled = isDisabled;
    this.updateInputDisabled();
  }

  ngOnDestroy() {
    this.onChangeSub.unsubscribe();
    this.input?.nativeInput?.nativeElement?.removeEventListener(
      'keydown',
      this.removeLastChipOnBackspace
    );
  }

  remove(elem: T): void {
    if (this.disabled) {
      return;
    }
    const index = this.selectedElems
      .map((u) => u[this.idField])
      .indexOf(elem[this.idField]);
    if (index >= 0) {
      this.selectedElems.splice(index, 1);
      this.onChangeWrapper(this.selectedElems);
    }
  }

  selected(event: MatAutocompleteSelectedEvent): void {
    this.addChipStream$.next(event.option.value);
    this.inputted.setValue(null);
    this.autocompleteChoices.next([]);
  }

  onChangeWrapper(value: T[]) {
    if (this.isMulti) {
      this.onChange(value);
    } else {
      this.onChange(value.length ? value[0] : null);
    }
    this.updateInputDisabled();
  }

  assignErrors() {
    EsnUtils.assignErrors(this.inputted, this.parentControl);
  }

  public onInputBlur() {
    this.assignErrors();
  }
}

@Component({
  selector: 'esn-autocomplete-impl-base',
  template: '',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: EsnAutocompleteSelectImplementationBase,
      multi: true,
    },
  ],
})
export class EsnAutocompleteSelectImplementationBase<T>
  implements ControlValueAccessor, OnInit, OnDestroy, AfterViewInit
{
  @Input() queryFunc: (txt: string) => Promise<T[] | null>;
  @Input() idField: keyof T;
  @Input() multi?: boolean | string = false;
  @Input() disabled: boolean | string = false;
  @Input() optionFilterFunc: (elm: T) => boolean = (elm) => true;
  @Input() label: string;
  @Input() placeholder: string;
  @Input() error?: EsnInputError;
  onChange: any = () => {};
  onTouch: any = () => {};

  public control: FormControl;
  onChangeSub: Subscription = new Subscription();
  parentControl: NgControl;

  constructor(private _injector: Injector) {
    this.control = new FormControl(null);
  }
  ngAfterViewInit(): void {
    if (!!this.parentControl) {
      this.onChangeSub.add(
        this.parentControl.control?.valueChanges.subscribe(() => {
          this.assignErrors();
        })
      );
      setTimeout(() => this.assignErrors());
    }
  }

  ngOnInit(): void {
    try {
      this.parentControl = this._injector.get(NgControl);
    } catch (e) {}

    this.onChangeSub.add(
      this.control.valueChanges.subscribe((val) => {
        this.onChange(val);
      })
    );
  }

  writeValue(value: any): void {
    this.control.setValue(value);
  }

  registerOnChange(fn: any) {
    this.onChange = fn;
  }

  registerOnTouched(onTouched: Function) {
    this.onTouch = onTouched;
  }

  setDisabledState(isDisabled: boolean) {
    this.disabled = isDisabled;
    this.updateDisableState();
  }

  public updateDisableState() {
    if (this.disabled) {
      this.control.disable();
    } else {
      this.control.enable();
    }
  }

  ngOnDestroy(): void {
    this.onChangeSub.unsubscribe();
  }

  assignErrors() {
    EsnUtils.assignErrors(this.control, this.parentControl);
  }
}
