import {
  FocusTrapFactory,
  FocusMonitor,
  InteractivityChecker,
} from '@angular/cdk/a11y';
import {
  BooleanInput,
  coerceBooleanProperty,
  coerceNumberProperty,
  NumberInput,
} from '@angular/cdk/coercion';
import { MediaMatcher } from '@angular/cdk/layout';
import { Platform } from '@angular/cdk/platform';
import { CdkScrollable, ScrollDispatcher } from '@angular/cdk/scrolling';
import { DOCUMENT } from '@angular/common';
import {
  AfterViewInit,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ContentChild,
  ContentChildren,
  ElementRef,
  forwardRef,
  Inject,
  InjectionToken,
  Input,
  NgZone,
  OnDestroy,
  OnInit,
  Optional,
  QueryList,
  ViewEncapsulation,
} from '@angular/core';
// import { MatDrawer, MatDrawerContainer, MatDrawerContent, MAT_DRAWER_CONTAINER } from './drawer';
import {
  MatDrawer,
  matDrawerAnimations,
  MatDrawerContainer,
  MatDrawerContent,
} from '@angular/material/sidenav';
import { EsnUtils } from '../../utils/public-api';

export const MAT_DRAWER_CONTAINER = new InjectionToken('MAT_DRAWER_CONTAINER');

@Component({
  selector: 'esn-sidenav-content',
  template: `
    <esn-button 
      *ngIf="isToggleButtonDisplayed" 
      class="esn-sidenav-toggle-button"
      (click)="toggle()"
      [color]="'neutral'"
      [iconOnly]="true"
      [type]="'basic'">
      <esn-icon name="menu"></esn-icon>
    </esn-button>
    <ng-content></ng-content>`,
  host: {
    class: 'mat-drawer-content mat-sidenav-content',
    '[style.margin-left.px]': '_container._contentMargins.left',
    '[style.margin-right.px]': '_container._contentMargins.right',
  },
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  providers: [
    {
      provide: CdkScrollable,
      useExisting: EsnSidenavContent,
    },
  ],
})
export class EsnSidenavContent
  extends MatDrawerContent
  implements AfterViewInit
{
  public isToggleButtonDisplayed: boolean = false;
  constructor(
    public changeDetectorRef: ChangeDetectorRef,
    @Inject(forwardRef(() => EsnSidenavContainer))
    container: EsnSidenavContainer,
    elementRef: ElementRef<HTMLElement>,
    scrollDispatcher: ScrollDispatcher,
    ngZone: NgZone
  ) {
    super(changeDetectorRef, container, elementRef, scrollDispatcher, ngZone);
  }

  ngAfterViewInit(): void {
    this._container._allDrawers.forEach((dr) => {
      if (!!(dr as EsnSidenav).mobileQuery) {
        (dr as EsnSidenav).mobileQuery.addEventListener('change', () =>
          this.updateToggleButtonDisplay()
        );
        (dr as EsnSidenav).updateMode();
        this.updateToggleButtonDisplay()
      }
    });
  }

  updateToggleButtonDisplay() {
    setTimeout(() => {
      this.isToggleButtonDisplayed = this._container._allDrawers.some(
        (dr) => dr.mode === 'over'
      );
      this.changeDetectorRef.detectChanges();

    }, 0);
  }

  public toggle() {
    this._container._allDrawers.forEach((dr) => {
      dr.opened ? dr.close() : dr.open();
    });
  }
}

@Component({
  selector: 'esn-sidenav',
  exportAs: 'esnSidenav',
  templateUrl: 'drawer.html',

  animations: [matDrawerAnimations.transformDrawer],
  host: {
    class: 'mat-drawer mat-sidenav',
    tabIndex: '-1',
    // must prevent the browser from aligning text based on value
    '[attr.align]': 'null',
    '[class.mat-drawer-end]': 'position === "end"',
    '[class.mat-drawer-over]': 'mode === "over"',
    '[class.mat-drawer-push]': 'mode === "push"',
    '[class.mat-drawer-side]': 'mode === "side"',
    '[class.mat-drawer-opened]': 'opened',
    '[class.mat-sidenav-fixed]': 'fixedInViewport',
    '[style.top.px]': 'fixedInViewport ? fixedTopGap : null',
    '[style.bottom.px]': 'fixedInViewport ? fixedBottomGap : null',
  },
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
})
export class EsnSidenav
  extends MatDrawer
  implements OnDestroy, AfterViewInit, OnInit
{
  /** Whether the sidenav is fixed in the viewport. */
  @Input()
  get fixedInViewport(): boolean {
    return this._fixedInViewport;
  }
  set fixedInViewport(value: BooleanInput) {
    this._fixedInViewport = coerceBooleanProperty(value);
  }
  private _fixedInViewport = false;

  /**
   * The gap between the top of the sidenav and the top of the viewport when the sidenav is in fixed
   * mode.
   */
  @Input()
  get fixedTopGap(): number {
    return this._fixedTopGap;
  }
  set fixedTopGap(value: NumberInput) {
    this._fixedTopGap = coerceNumberProperty(value);
  }
  private _fixedTopGap = 0;

  /**
   * The gap between the bottom of the sidenav and the bottom of the viewport when the sidenav is in
   * fixed mode.
   */
  @Input()
  get fixedBottomGap(): number {
    return this._fixedBottomGap;
  }
  set fixedBottomGap(value: NumberInput) {
    this._fixedBottomGap = coerceNumberProperty(value);
  }
  private _fixedBottomGap = 0;

  // Esn custom
  @Input() reactiveMode: boolean | string = false;
  @Input() reactiveBreakpointWidth?: string;

  public mobileQuery: MediaQueryList;
  public _mobileQueryListener: () => void = () => {};
  modeInitialized: boolean = false;

  constructor(
    _elementRef: ElementRef<HTMLElement>,
    _focusTrapFactory: FocusTrapFactory,
    _focusMonitor: FocusMonitor,
    _platform: Platform,
    _ngZone: NgZone,
    _interactivityChecker: InteractivityChecker,
    public media: MediaMatcher,
    public changeDetectorRef: ChangeDetectorRef,

    @Optional() @Inject(DOCUMENT) _doc: any,
    @Optional() @Inject(MAT_DRAWER_CONTAINER) _container?: MatDrawerContainer
  ) {
    super(
      _elementRef,
      _focusTrapFactory,
      _focusMonitor,
      _platform,
      _ngZone,
      _interactivityChecker,
      _doc,
      _container
    );
  }
  ngOnInit() {
    this.autoFocus = false;
    this.reactiveMode = this.reactiveMode || this.reactiveMode === '';
    if (this.reactiveMode) {
      this.mobileQuery = this.media.matchMedia(
        `(max-width: ${this.reactiveBreakpointWidth || EsnUtils.BREAKPOINTS.tabletLarge})`
      );
      this._initListener();
    }
  }

  _initListener() {
    this._mobileQueryListener = () => {
      // this.isSidebarOpened = !this.mobileQuery.matches;
      this.changeDetectorRef.detectChanges();
      this.updateMode();
    };
    
    this.mobileQuery.addEventListener('change', this._mobileQueryListener);
  }

  override ngAfterViewInit() {
    super.ngAfterViewInit();
    if (this.reactiveMode) {
      this.updateMode()
    }
  }

  override ngOnDestroy(): void {
    super.ngOnDestroy();
    if (this.reactiveMode) {
      this.mobileQuery.removeEventListener('change', this._mobileQueryListener);
    }
  }

  public updateMode() {
    setTimeout(() => {
      this.mode = this.mobileQuery.matches ? 'over' : 'side';
      if (this.mode === 'over') {
        this.close();
      } else {
        this.open();
      }
      if(!this.modeInitialized) {
        this.modeInitialized = true;
        setTimeout(() => this._mobileQueryListener())
      }
    });
  }

  public onContentChanges(){
    setTimeout(() => { this.updateMode() })
  }
}

@Component({
  selector: 'esn-sidenav-container',
  exportAs: 'esnSidenavContainer',
  templateUrl: 'sidenav-container.html',
  styleUrls: ['drawer.scss', 'sidenav.scss'],
  host: {
    class: 'mat-drawer-container mat-sidenav-container',
    '[class.mat-drawer-container-explicit-backdrop]': '_backdropOverride',
  },
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  providers: [
    {
      provide: MAT_DRAWER_CONTAINER,
      useExisting: EsnSidenavContainer,
    },
  ],
})
export class EsnSidenavContainer extends MatDrawerContainer {
  @ContentChildren(EsnSidenav, {
    // We need to use `descendants: true`, because Ivy will no longer match
    // indirect descendants if it's left as false.
    descendants: true,
  })
  override _allDrawers: QueryList<EsnSidenav>;

  @ContentChild(EsnSidenavContent) override _content: EsnSidenavContent;
}
