import { ComponentType, Overlay, OverlayContainer } from '@angular/cdk/overlay';
import {
  ANIMATION_MODULE_TYPE,
  Inject,
  Injectable,
  Injector,
  Optional,
  SkipSelf,
  TemplateRef,
} from '@angular/core';
import {
  MatDialog,
  MatDialogConfig,
  MatDialogContainer,
  MAT_DIALOG_DATA,
  MAT_DIALOG_DEFAULT_OPTIONS,
  MAT_DIALOG_SCROLL_STRATEGY,
  _MatDialogBase,
} from '@angular/material/dialog';
import { EsnDialogRef } from './dialogRef';
import { EsnDialogConfig } from './model';
import { ResponsiveService } from '../../utils/services/responsive/public-api';

export const ESN_DIALOG_DATA = MAT_DIALOG_DATA;
export const ESN_DIALOG_DEFAULT_OPTIONS = MAT_DIALOG_DEFAULT_OPTIONS;
export const ESN_DIALOG_SCROLL_STRATEGY = MAT_DIALOG_SCROLL_STRATEGY;

@Injectable()
export class EsnDialog extends _MatDialogBase<MatDialogContainer> {
  isMobile$ = this.responsiveService.isMobile$;

  constructor(
    private responsiveService: ResponsiveService,
    overlay: Overlay,
    injector: Injector,
    @Optional() location: Location,
    @Optional()
    @Inject(ESN_DIALOG_DEFAULT_OPTIONS)
    defaultOptions: MatDialogConfig,
    @Inject(ESN_DIALOG_SCROLL_STRATEGY) scrollStrategy: any,
    @Optional() @SkipSelf() parentDialog: MatDialog,
    overlayContainer: OverlayContainer,
    @Optional()
    @Inject(ANIMATION_MODULE_TYPE)
    animationMode?: 'NoopAnimations' | 'BrowserAnimations',
  ) {
    super(
      overlay,
      injector,
      defaultOptions,
      parentDialog,
      overlayContainer,
      scrollStrategy,
      EsnDialogRef,
      MatDialogContainer,
      ESN_DIALOG_DATA,
      animationMode
    );

    this._idPrefix = 'mat-mdc-dialog-';
  }

  override open<T, D = any, R = any>(
    component: ComponentType<T>,
    config?: EsnDialogConfig<D>
  ): EsnDialogRef<T, R>;
  override open<T, D = any, R = any>(
    template: TemplateRef<T>,
    config?: EsnDialogConfig<D>
  ): EsnDialogRef<T, R>;
  override open<T, D = any, R = any>(
    template: ComponentType<T> | TemplateRef<T>,
    config?: EsnDialogConfig<D>
  ): EsnDialogRef<T, R>;
  override open<T, D = any, R = any>(
    componentOrTemplateRef: ComponentType<T> | TemplateRef<T>,
    config?: EsnDialogConfig<D>
  ): EsnDialogRef<T, R> {
    const matConfig = config || {};

    let panelClasses = matConfig.panelClass || [];
    if(!Array.isArray(panelClasses)) {
      panelClasses = [panelClasses]
    }
    panelClasses.push('esn-dialog');
    
    if (!!matConfig?.fullScreenDialog) {
      this.setFullScreen(matConfig, panelClasses);
    }

    this.isMobile$.subscribe(isMobile => {
      if(isMobile) {
        this.setFullScreen(matConfig, panelClasses);
      }
    });

    matConfig.panelClass = panelClasses;
    // Set autoFocus to false by default
    matConfig.autoFocus = !!matConfig.autoFocus

    return super.open(componentOrTemplateRef, matConfig);
  }

  setFullScreen(matConfig: EsnDialogConfig<any>, panelClasses: string | string[])  {
      matConfig.width = '100vw';
      matConfig.maxWidth = 'none';
      matConfig.height = '100vh';
      if(Array.isArray(panelClasses)) {
        panelClasses.push('esn-dialog-full-screen');
      }
  }
}
