import { Injectable } from '@angular/core';
import { EsnNotificationModel } from './model/notification';

@Injectable()
export abstract class EsnNotificationProcessingService {
  abstract onNotificationClicked(notification: EsnNotificationModel): void;
  abstract goToNotificationPage(): void;
  abstract getNotificationTags(): string;
}

export class EsnNotificationNoProcessingService
  implements EsnNotificationProcessingService
{
  onNotificationClicked(notification: EsnNotificationModel): void {
    console.warn(
      'Please provide an implementation of EsnNotificationProcessingService to execute actions when a notification is clicked.'
    );
  }
  goToNotificationPage(){
    console.warn(
      'Please provide an implementation of EsnNotificationProcessingService to allow redirections to the notification page.'
    );
  }

  getNotificationTags(){
    return '';
  }
}