import { Injectable } from "@angular/core";
import { ToasterService } from "../../../components/toaster/public-api";
import { EsnGlobalConfiguration } from "../public-api";

export type EsnApiCallWrapperNotifType = 'info' | 'error' | 'success';

@Injectable()
export abstract class EsnApiCallWrapperNotificationService {
  abstract showNotif(type: EsnApiCallWrapperNotifType, title: string, message?: string): void;
}

@Injectable()
export class ApiCallWrapperToasterNotificationService
  implements EsnApiCallWrapperNotificationService
{
  constructor(public toasterService: ToasterService) {}

  showNotif(type: EsnApiCallWrapperNotifType, title: string, message: string): void {
    this.toasterService.showToaster(type, title, message);
  }
}

export class ApiCallWrapperConsoleNotificationService
  implements EsnApiCallWrapperNotificationService
{
  showNotif(type: EsnApiCallWrapperNotifType, title: string, message: string): void {
    console.warn(
      `${title}: ${message}\n\nConsider providing an implementation of EsnApiCallWrapperNotificationService to display this message to the user`
    );
  }
}

@Injectable()
export class ApiCallWrapperGlobalConfigNotificationService
  implements EsnApiCallWrapperNotificationService
{
  constructor(public globalConf: EsnGlobalConfiguration){}
  showNotif(type: EsnApiCallWrapperNotifType, title: string, message: string): void {
    this.globalConf.notificationService.showNotif(type, title, message);
  }
}

export const ESN_API_CALL_WRAPPER_DEFAULT_NOTIFICATION_SERVICE =
ApiCallWrapperGlobalConfigNotificationService;
