import { Component } from '@angular/core';
import { EsnUtils } from '../../utils/public-api';
import { Router } from '@angular/router';

@Component({
  selector: 'esn-browser-error',
  templateUrl: './browser-error.component.html',
  styleUrls: ['./browser-error.component.scss'],
})
export class EsnBrowserErrorComponent {
  constructor(public router: Router) {}
  
  ngOnInit() {
    if (EsnUtils.isBrowserSupported()) {
      this.router.navigate(['/']);
    }
  }
}
