import { Component, Input, TemplateRef } from '@angular/core';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import { EsnAutocompleteSelectImplementationBase } from '../base/autocomplete-select-base.component';

@Component({
  selector: 'esn-autocomplete-select',
  templateUrl: './autocomplete-select.component.html',
  styleUrls: ['./autocomplete-select.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: EsnAutocompleteSelect,
      multi: true,
    },
  ],
})
export class EsnAutocompleteSelect extends EsnAutocompleteSelectImplementationBase<any> {
  @Input() optionTextFn?: (elm: any) => string;
  @Input() chipTextFn?: (elm: any) => string;
  @Input() optionTemplate?: TemplateRef<any>;
  @Input() chipTemplate?: TemplateRef<any>;
  @Input() singleElemTemplate?: TemplateRef<any>;
  @Input() minChars: number = 3;
  @Input() hint: string;
  @Input() noResultsLabel: string  = 'Aucun résultat trouvé';
}
