
export abstract class EsnFileInputConfig {
  public FILE_PROVIDER: string;
  public FILE_MAX_SIZE: number;
  public MAX_NB_FILES: number;
  public FileTypesMap: { [key: string]: { type: string[]; label: string } };
}


export class EsnFileInputDefaultConfig implements EsnFileInputConfig {
  public FILE_PROVIDER = 'NOT_PROVIDED';
  public FILE_MAX_SIZE = 20 * 1024 * 1024; // 20 Mo
  public MAX_NB_FILES = 10;
  public FileTypesMap = DEFAULT_FILE_TYPES_MAP;
}



