import {
  Component,
  EventEmitter,
  Input,
  Output,
  SimpleChanges,
} from '@angular/core';
import { MatCheckboxChange } from '@angular/material/checkbox';
import {
  ControlValueAccessor,
  FormBuilder,
  FormControl,
  FormGroup,
  NG_VALUE_ACCESSOR,
} from '@angular/forms';
import { Subscription } from 'rxjs';

type MatCheckboxColor = 'primary' | 'accent' | 'warn' | undefined;
export type EsnCheckboxColor =
  | 'primary'
  | 'accent'
  | 'success'
  | 'error'
  | 'neutral'
  | undefined;

export class EsnCheckboxChange {
  /** The source checkbox of the event. */
  source: EsnCheckboxComponent;
  /** The new `checked` value of the checkbox. */
  checked: boolean;
}

const COLORS_MAP: { [key in MatCheckboxColor as string]: MatCheckboxColor } = {
  primary: 'primary',
  accent: 'accent',
  error: 'warn',
  success: 'primary', // a dark material palette that will be overridden by CSS
  neutral: 'primary', // a dark material palette that will be overridden by CSS
};
const DEFAULT_COLOR: EsnCheckboxColor = 'primary';

@Component({
  selector: 'esn-checkbox',
  templateUrl: './checkbox.component.html',
  styleUrls: ['./checkbox.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: EsnCheckboxComponent,
      multi: true,
    },
  ],
  host: {
    class: 'esn-checkbox',
    '[class.esn-checkbox-primary]': `_color === 'primary'`,
    '[class.esn-checkbox-accent]': `_color === 'accent'`,
    '[class.esn-checkbox-success]': `_color === 'success'`,
    '[class.esn-checkbox-error]': `_color === 'error'`,
    '[class.esn-checkbox-neutral]': `_color === 'neutral'`,
    '[class.esn-checkbox-disabled]': `disabled || null`,
  },
})
export class EsnCheckboxComponent implements ControlValueAccessor {
  @Input() id: string;
  @Input() name: string | null = null;
  @Input() value: string;
  @Input() checked = false;
  @Input() required = false;
  @Input() disabled = false;
  @Input() indeterminate = false;
  @Input() disableRipple = false;
  @Input() labelPosition: 'before' | 'after' = 'after';
  @Input()
  set color(value: EsnCheckboxColor) {
    this._color = value;
    this._matColor = COLORS_MAP[this._color as string];
  }
  get color(): EsnCheckboxColor {
    return this._color;
  }
  @Input() tabIndex: number;

  @Output() change = new EventEmitter<any>();
  @Output() indeterminateChange = new EventEmitter<boolean>();
  form: FormGroup = this._formBuilder.group({
    control: this.checked,
  });

  public onChangeSub: Subscription = new Subscription();
  public _color: EsnCheckboxColor = DEFAULT_COLOR;
  public _matColor: MatCheckboxColor = COLORS_MAP[DEFAULT_COLOR as string];

  onChange: any = () => {};
  onTouch: any = () => {};

  constructor(private _formBuilder: FormBuilder) {}

  ngOnChanges(changes: SimpleChanges): void {
    if (!!changes['disabled']) {
      this.updateDisableState();
    }

    if (!!changes['checked'] && this.checked !== null) {
      this.form.controls['control'].setValue(this.checked);
    }
  }

  onChanges($event: any) {
    this.change.emit($event);
  }

  onIndeterminateChange($event: boolean) {
    this.indeterminateChange.emit($event);
  }

  writeValue(value: any) {
    if (!!value) {
      this.form.controls['control'].setValue(value);
    }
  }

  registerOnChange(fn: any) {
    this.onChangeSub.add(
      this.form.controls['control'].valueChanges.subscribe(fn)
    );
  }

  registerOnTouched(onTouched: Function) {
    this.onTouch = onTouched;
  }

  setDisabledState(isDisabled: boolean) {
    this.disabled = isDisabled;
    this.updateDisableState();
  }

  public updateDisableState() {
    this.disabled ? this.form.disable() : this.form.enable();
  }
}
