import { BooleanInput, coerceBooleanProperty } from "@angular/cdk/coercion";
import { Component, ViewEncapsulation, ChangeDetectionStrategy, ContentChildren, QueryList, ViewChild, ElementRef, ChangeDetectorRef, Inject, Optional, ANIMATION_MODULE_TYPE, Input } from "@angular/core";
import { MAT_TAB_GROUP, _MatTabGroupBase,  MAT_TABS_CONFIG, MatTabsConfig, MatTab, MatTabHeader, MatTabsModule } from "@angular/material/tabs";
import { EsnTab } from "./tab";



interface MatTabGroupBaseHeader {
  _alignInkBarToSelectedTab(): void;
  updatePagination(): void;
  focusIndex: number;
}



@Component({
  selector: 'esn-tab-group',
  exportAs: 'esnTabGroup',
  templateUrl: 'tab-group.html',
  styleUrls: ['tab-group.scss'],
  encapsulation: ViewEncapsulation.None,
  // tslint:disable-next-line:validate-decorators
  changeDetection: ChangeDetectionStrategy.Default,
  inputs: ['color', 'disableRipple'],
  providers: [
    {
      provide: MAT_TAB_GROUP,
      useExisting: EsnTabGroup,
    },
  ],
  host: {
    'class': 'mat-tab-group',
    '[class.mat-tab-group-dynamic-height]': 'dynamicHeight',
    '[class.mat-tab-group-inverted-header]': 'headerPosition === "below"',
    '[class.mat-mdc-tab-group-stretch-tabs]': 'stretchTabs',
  },
})
export class EsnTabGroup extends _MatTabGroupBase {
  @ContentChildren(EsnTab, {descendants: true}) _allTabs: QueryList<MatTab>;
  @ViewChild('tabBodyWrapper') _tabBodyWrapper: ElementRef;
  @ViewChild('tabHeader') _tabHeader: MatTabGroupBaseHeader;

    /** Whether the ink bar should fit its width to the size of the tab label content. */
    @Input()
    get fitInkBarToContent(): boolean {
      return this._fitInkBarToContent;
    }
    set fitInkBarToContent(v: BooleanInput) {
      this._fitInkBarToContent = coerceBooleanProperty(v);
      this._changeDetectorRef.markForCheck();
    }
    private _fitInkBarToContent = false;
  
    /** Whether tabs should be stretched to fill the header. */
    @Input('mat-stretch-tabs')
    get stretchTabs(): boolean {
      return this._stretchTabs;
    }
    set stretchTabs(v: BooleanInput) {
      this._stretchTabs = coerceBooleanProperty(v);
    }
    private _stretchTabs = true;

    
  constructor(
    elementRef: ElementRef,
    changeDetectorRef: ChangeDetectorRef,
    @Inject(MAT_TABS_CONFIG) @Optional() defaultConfig?: MatTabsConfig,
    @Optional() @Inject(ANIMATION_MODULE_TYPE) animationMode?: string,
  ) {
    super(elementRef, changeDetectorRef, defaultConfig, animationMode);
    this.fitInkBarToContent =
    defaultConfig && defaultConfig.fitInkBarToContent != null
      ? defaultConfig.fitInkBarToContent
      : false;
  }

}
