import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { CdkScrollableModule } from '@angular/cdk/scrolling';
import { MatCommonModule } from '@angular/material/core';
import { MatDrawer, MatDrawerContainer, MatDrawerContent, MatSidenavModule } from '@angular/material/sidenav';
// import { MatDrawer, MatDrawerContainer, MatDrawerContent } from './drawer';
import { EsnSidenav, EsnSidenavContainer, EsnSidenavContent } from './sidenav.component';
import { EsnSidenavButton, EsnSidenavFooter, EsnSidenavHeader, EsnSidenavSection } from './sidenav-content';
import { EsnButtonModule } from '../button/button.module';
import { RouterModule } from '@angular/router';
// import { MatDrawer, MatDrawerContainer, MatDrawerContent } from './drawer';
import {LayoutModule} from '@angular/cdk/layout';
import { ObserversModule } from '@angular/cdk/observers';
import { EsnIconsRegistry } from '../icon/icons-registry';
import { CloseIcon } from '../icon/icons';
import { EsnIconModule } from '../icon/icon.module';
@NgModule({
  declarations: [

    EsnSidenav,
    EsnSidenavContainer,
    EsnSidenavContent,
    EsnSidenavButton,
    EsnSidenavFooter,
    EsnSidenavSection,
    EsnSidenavHeader
  ],
  imports: [
    CommonModule,
    MatSidenavModule,
    EsnButtonModule,
    RouterModule,
    LayoutModule,
    ObserversModule,
    EsnIconModule,
  ],
  providers: [EsnIconsRegistry],
  exports: [
    EsnSidenav,
    EsnSidenavContainer,
    EsnSidenavContent,
    EsnSidenavButton,
    EsnSidenavFooter,
    EsnSidenavSection,
    EsnSidenavHeader
  ],
})
export class EsnSidenavModule {
  constructor(private esnIconsRegistry: EsnIconsRegistry) {
    this.esnIconsRegistry.registerIcons([CloseIcon]);
  }
}
