import { Injectable } from "@angular/core";
import { ToasterService } from "../../../components/toaster/public-api";
import { EsnGlobalConfiguration } from "../public-api";
import { BlobItem } from "../../public-api";


@Injectable()
export abstract class EsnBlobRetreiverService {
  abstract getBlob(uuid: string): Promise<BlobItem>;
  abstract deleteBlobs(uuids: string[]): Promise<boolean>;
}

export class EsnNoBlobRetreiverService
  implements EsnBlobRetreiverService
{
  NOT_IMPLEMENTED_MESSAGE = `No implementation of 'EsnBlobRetreiverService' was provided`;

  async getBlob(uuid: string): Promise<BlobItem> {
    throw this.NOT_IMPLEMENTED_MESSAGE;
  }

  async deleteBlobs(uuids: string[]): Promise<boolean> {
    throw this.NOT_IMPLEMENTED_MESSAGE;
  }
}


