import {Component, EventEmitter, Input, Output} from '@angular/core';

export type EsnProgressBarMode = 'determinate' | 'indeterminate' | 'buffer' | 'query';
export type EsnProgressBarColor = 'primary' | 'accent' | 'success' | 'error' | 'neutral' | undefined;
export interface EsnProgressAnimationEnd {
  value: number;
}

const DEFAULT_COLOR: EsnProgressBarColor = 'primary';
const DEFAULT_MODE: EsnProgressBarMode = 'indeterminate';

@Component({
  selector: 'esn-progress-bar',
  templateUrl: './progress-bar.component.html',
  styleUrls: ['./progress-bar.component.scss'],
  host: {
    class: 'esn-progressbar',
    '[class.esn-progressbar-primary]': `color === 'primary'`,
    '[class.esn-progressbar-accent]': `color === 'accent'`,
    '[class.esn-progressbar-success]': `color === 'success'`,
    '[class.esn-progressbar-error]': `color === 'error'`,
    '[class.esn-progressbar-neutral]': `color === 'neutral'`,
  },
})

export class ProgressBarComponent {
  @Input() color: EsnProgressBarColor = DEFAULT_COLOR;
  @Input() mode: EsnProgressBarMode = DEFAULT_MODE ;
  @Input() bufferValue = 0;
  @Input() value: number;

  @Output() animationEnd: EventEmitter<EsnProgressAnimationEnd> = new EventEmitter<EsnProgressAnimationEnd>()

  onAnimationEnd($event: EsnProgressAnimationEnd) {
      this.animationEnd?.emit($event);
    
  }
}
