import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import {
  CONTENT_TYPES_MAP,
  DocumentEventEmitter,
  DocumentEventType,
  DocumentItem,
  FileMetadataDto,
} from '../../../../utils/model/documents';
import { EsnFileConverter } from '../../../../utils/utils/public-api';

@Component({
  selector: 'esn-file-item',
  templateUrl: './file-item.component.html',
  styleUrls: ['./file-item.component.scss'],
  host: {
    class: 'esn-file-item',
  }
})
export class EsnFileItem implements OnInit {
  @Output()
  event: EventEmitter<DocumentEventEmitter> =
    new EventEmitter<DocumentEventEmitter>();
  public _fileType: any;
  public isDownloadableFromRemote: boolean = false;

  constructor() {}
  public _document: DocumentItem = <DocumentItem>{};

  @Input() set document(value: DocumentItem | FileMetadataDto) {
    let valToUse = value as DocumentItem;
    if(!(value as DocumentItem)['metadata']){
      valToUse = EsnFileConverter.convertFileMetaDataToDocument(value as FileMetadataDto)
    }
    this._document = valToUse;
    if (valToUse.metadata.contentType) {
      this._fileType = CONTENT_TYPES_MAP[valToUse.metadata.contentType] || 'default';
    }
  }

  @Input() disabled = false;

  ngOnInit(): void {
    this.isDownloadableFromRemote = (
      this._document?.actions?.download &&
      !!this._document?.metadata &&
      !!this._document?.metadata?.uuid &&
      this._document.metadata?.uuid !== ''
    );
  }

  public onDeleteClick(): void {
    this.event.emit({
      event: DocumentEventType.DELETE,
      document: this._document,
    });
  }

  public onDownloadClick(): void {
    this.event.emit({
      event: DocumentEventType.DOWNLOAD,
      document: this._document,
    });
  }

  public onUploadClick(): void {
    this.event.emit({
      event: DocumentEventType.UPLOAD,
      document: this._document,
    });
  }

  public roundValue(value: number): number {
    return value ? Math.ceil(value) : 0;
  }
}
