import {Component, EventEmitter, Inject, ViewEncapsulation} from '@angular/core';
import {EsnDialogRef} from "../dialog/dialogRef";
import {ESN_DIALOG_DATA} from "../dialog/dialog.service";
import {ConfirmationDialogDataModel} from "./models/confirmationDialogDataModel";
import { DomSanitizer } from '@angular/platform-browser';



@Component({
  selector: 'esn-confirmation-dialog',
  templateUrl: './confirmation-dialog.component.html',
  styleUrls: ['./confirmation-dialog.component.scss'],
  host:{
    class: 'esn-confirmation-dialog'
  }

})
export class EsnConfirmationDialogComponent {

  public decision: EventEmitter<any> = new EventEmitter();
  public thirdButtonClick: EventEmitter<void> = new EventEmitter();
  public loading: boolean = false;

  constructor(
    private dialogRef: EsnDialogRef<EsnConfirmationDialogComponent>,
    @Inject(ESN_DIALOG_DATA)
    public data: ConfirmationDialogDataModel, public sanitizer: DomSanitizer) {}

    public confirm() {
      this.decision.emit(true);
      this.dialogRef.close();
    }
  
    public cancel() {
      this.decision.emit(false);
      this.dialogRef.close();
    }
  
    public thirdButtonClicked() {
      this.thirdButtonClick.emit();
      this.dialogRef.close();
    }


    get htmlTemplate(){
      // TODO: find a better way to apply styles without introducing a security risk ?
      // because this will not work if an element has multiple classes
      if(!this.data.contentTemplate){
        return null;
      }
      let sanitizedHtml = this.sanitizer.sanitize(1, this.data.contentTemplate)

      if(!!this.data.templateStyles){
        Object.entries(this.data.templateStyles).forEach(([className, style])=>{
          const classString = 'class="' + className + '"';
          sanitizedHtml = sanitizedHtml!.split(classString).join(`${classString} style="${style}"`)
        })
      }

      return sanitizedHtml;

    }
}
