import { CanActivate, Route } from "@angular/router";
import {  EsnWizardStep } from "../../../../components/wizard/model/step-parameters";
import { EsnDialogEntry } from "../../../../components/dialog-entry/dialog-entry.component";
import { EsnUtils } from "../../../utils/utils";


export class EsnDraftUtils {
  static getCreationRoutes(containerComponent: any, creationSteps: EsnWizardStep[], guards: CanActivate[] = []): Route{
    return {
      path: 'create/:uuid',
      component: EsnDialogEntry,
      data: {
        child: containerComponent,
        panelClass: ['no-padding-dialog'],
        fullScreenDialog: true,
      },
      // TODO: add DatabaseExistGuard to the lib and make it the default guard for this
      // canActivate: [DatabaseExistGuard],
      canActivate: guards,
      children: [
        ...creationSteps.map((step) => ({
          path: step.path,
          component: step.component,
        })),
        {
          path: '',
          redirectTo: creationSteps[0].path,
          pathMatch: 'full',
        },
      ],
    }
  }

  static initDraftElement(additionalFields: {[key in string]: string} = {}) {
    return {
      externalUuid: EsnUtils.generateRandomUuid(),
      creationDate: new Date(),
      initialCreation: true,
      ...additionalFields
    };
  }
}