import { Injectable } from '@angular/core';
import { EsnIconObj, EsnIconName } from './icons';

@Injectable()
export class EsnIconsRegistry {
  private registry = new Map<string, string>();

  public registerIcons(icons: EsnIconObj[]): void {
    icons.forEach((icon: EsnIconObj) => this.registry.set(icon.name, icon.data));
  }

  public getIcon(name: string): string | undefined {
    if (!this.registry.has(name)) {
      console.warn(
        `Icon name ${name} not found, did you add it to the registry ?`
      );
    }

    return this.registry.get(name);
  }
}
