import { Injectable } from '@angular/core';

export enum EsnAsyncDataSourceType {
  PROMISE = 'PROMISE',
  OBSERVABLE = 'OBSERVABLE',
}

export abstract class EsnAsyncDataSourceAdapter {
  callReturnType: EsnAsyncDataSourceType = EsnAsyncDataSourceType.PROMISE;
  debounceTime: number = 150;

  getResults(resp: any): any[] {
    return [];
  }

  getTotalNbResults(resp: any) {
    return 0;
  }

  // Gets the error from a successful call to the function
  getError(resp: any) {
    return null;
  }

  // Gets the error thrown when a call fails
  processError(err: any) {
    return err;
  }
}

@Injectable()
export class EsnAsyncDatasourceCallWrapperAdapter extends EsnAsyncDataSourceAdapter {
  override getResults(resp: any) {
    return resp.resp?.results || [];
  }

  override getTotalNbResults(resp: any) {
    return resp.resp?.totalNumberOfResults || 0;
  }

  override getError(resp: any) {
    return resp.error;
  }

  override processError(err: any) {
    return { message: 'Une erreur est survenue.' };
  }
}

@Injectable()
export class EsnAsyncDatasourceApiResponseAdapter extends EsnAsyncDataSourceAdapter {
  override callReturnType = EsnAsyncDataSourceType.OBSERVABLE;

  override getResults(resp: any) {
    return resp.content.results;
  }

  override getTotalNbResults(resp: any) {
    return resp.content.totalNumberOfResults;
  }

  override processError(err: any) {
    // Maybe transform the error like the apiCallWrapper does here ?
    return err;
  }
}

export const ESN_ASYNC_DATASOURCE_DEFAULT_ADAPTER =
  EsnAsyncDatasourceCallWrapperAdapter;
