
export interface ApiError {
  code?: string;
  description?: string;
  severity?: ApiError.SeverityEnum;
  type?: ApiError.TypeEnum;
}

export namespace ApiError {
  export type SeverityEnum = 'INFO' | 'WARNING' | 'ERROR';
  export const SeverityEnum = {
    INFO: 'INFO' as SeverityEnum,
    WARNING: 'WARNING' as SeverityEnum,
    ERROR: 'ERROR' as SeverityEnum,
  };
  export type TypeEnum = 'TECHNICAL' | 'FUNCTIONAL';
  export const TypeEnum = {
    TECHNICAL: 'TECHNICAL' as TypeEnum,
    FUNCTIONAL: 'FUNCTIONAL' as TypeEnum,
  };
}

export interface ApiErrorDisplay {
  message: string;
  retryable?: boolean;
  code?: number;
}
