import { Injectable } from '@angular/core';
import { EsnUtils } from '../../utils';
import { EsnDatabaseService, EsnInMemoryDatabaseService } from '../global-configuration';

@Injectable()
export abstract class EsnElementDatabaseService {
  public elements?: any[];

  // To override
  public storeName: string;
  public idName: string;

  constructor(
    public idbService: EsnDatabaseService,
  ) {}

  public async getAllElements(): Promise<any[]> {
    return await this._fetchOrCreateElements(10);
  }

  public async getElementById(id: string): Promise<any | undefined> {
    const elements = await this._fetchOrCreateElements(10);
    return elements!.find((req: any) => req[this.idName] === id);
  }

  public async getFirstThatExists(ids: string[]): Promise<any | undefined> {
    const elements = await this._fetchOrCreateElements(10);
    let el;
    for (let id of ids) {
      el = elements!.find((req: any) => req[this.idName] === id);
      if (el) {
        return el;
      }
    }
    return el;
  }

  public async getElements(ids: string[]): Promise<any | undefined> {
    const elements = await this._fetchOrCreateElements(10);
    return ids.map((id) =>
      elements!.find((req: any) => req[this.idName] === id)
    );
  }

  public async saveElement(element: any): Promise<string> {
    !!this.elements || (await this._fetchElements());
    this._updateOrAdd(EsnUtils.cloneDeep(element));
    return await this._save();
  }

  public async deleteElementById(id: string): Promise<void> {
    !!this.elements || (await this._fetchElements());
    this._delete(id);
    console.log('delete ' + id);
    await this._save();
  }

  public async saveElements(elements: any[]): Promise<void> {
    !!this.elements || (await this._fetchElements());
    elements.forEach((req) => this._updateOrAdd(req));
    await this._save();
  }

  public async _fetchElements() {
    this.elements = await this._fetchOrCreateElements(10);
  }

  public async _save(): Promise<string> {
    return await this._saveOrRetry(50);
  }

  public _updateOrAdd(element: any) {
    const index = this.elements!.findIndex(
      (req) => req[this.idName] === element[this.idName]
    );
    if (index > -1) {
      this.elements!.splice(index, 1, element);
    } else {
      this.elements!.push(element);
    }
  }

  public _delete(id: string) {
    const index = this.elements!.findIndex((req) => req[this.idName] === id);
    if (index > -1) {
      this.elements!.splice(index, 1);
    }
  }

  public async _fetchOrCreateElements(retries: number): Promise<any[]> {
    if (retries < 0) {
      throw 'Failed to fetch elements';
    }
    const value = await this.idbService.getValueByKey(this.storeName, 'all', false);
    if (value !== undefined) {
      return value;
    }
    await this.idbService.setValueByKey(this.storeName, 'all', [], false);
    await EsnUtils.sleep(200);
    return await this._fetchOrCreateElements(retries - 1);
  }
  

  public async _saveOrRetry(retries: number): Promise<string> {
    if (retries < 0) {
      throw 'Failed to save elements';
    }
    const value = await this.idbService.setValueByKey(
      this.storeName,
      'all',
      this.elements,
      false
    );
    if (value === 'all') {
      return value;
    }
    await EsnUtils.sleep(200);
    return await this._saveOrRetry(retries - 1);
  }
}


@Injectable()
export class EsnInMemoryDraftDbService extends EsnElementDatabaseService {
  override storeName: string = 'inMemoryDraft';
  override idName: string = 'externalUuid';
  constructor(public inMemoryDbService: EsnInMemoryDatabaseService){
    super(inMemoryDbService)
  }
}