import { Injectable } from '@angular/core';
import { EsnIllustrationObj } from './illustrations';

@Injectable()
export class EsnIllustrationsRegistry {
  private registry = new Map<string, string>();

  public registerIllustrations(illustrations: EsnIllustrationObj[]): void {
    illustrations.forEach((illustration: EsnIllustrationObj) => this.registry.set(illustration.name, illustration.data));
  }

  public getIllustration(name: string): string | undefined {
    if (!this.registry.has(name)) {
      console.warn(
        `Illustration name ${name} not found, did you add it to the registry ?`
      );
    }

    return this.registry.get(name);
  }
}
