import { Component, EventEmitter, Input, Output } from '@angular/core';
import { DocumentEventEmitter, DocumentItem, FileMetadataDto } from '../../../utils/model/documents';

@Component({
  selector: 'esn-file-list',
  templateUrl: './file-list.component.html',
  styleUrls: ['./file-list.component.scss'],
  host: {
    class: 'esn-file-list',
    '[class.empty]': `!documents.length`
  }
})
export class EsnFileList {
  @Input() documents: (DocumentItem | FileMetadataDto)[] = [];
  @Input() disabled: boolean = false;
  @Input() readOnly: boolean = false;
  @Output() event: EventEmitter<DocumentEventEmitter> = new EventEmitter();


}
