import { Component, Input, OnInit } from '@angular/core';
import {EdfApplicationUser} from "../../utils/model/edfApplicationUser";
import { EsnUtils } from '../../utils/utils/public-api';

@Component({
  selector: 'esn-person-avatar',
  templateUrl: './person-avatar.component.html',
  styleUrls: ['./person-avatar.component.scss'],
})
export class EsnPersonAvatar implements OnInit {
  @Input() user!: EdfApplicationUser;
  @Input() size?: 'xs' | 'sm' | '' | 'lg' = '';
  public nniColorNumber: number = 0;
  constructor() {}

  ngOnInit(): void {
    this.nniColorNumber = Math.abs(EsnUtils.hashCode(this.user.nni || '')) % 10;
  }
}
