import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';

@Component({
  selector: 'esn-error-state',
  templateUrl: './error-state.component.html',
  styleUrls: ['./error-state.component.scss'],
})
export class EsnErrorState implements OnInit {
  @Input() retryPossible: boolean = false;
  @Input() retryOngoing: boolean = false;
  @Input() btnText?: string;
  @Output() btnClicked: EventEmitter<void> = new EventEmitter<void>();

  constructor() {}

  ngOnInit(): void {}

  public onClick() {
    if (!this.retryOngoing) {
      this.btnClicked.emit();
    }
  }
}
