import {
  AfterViewChecked,
  AfterViewInit, ChangeDetectionStrategy, ChangeDetectorRef,
  Component,
  ContentChildren,
  Input,
  QueryList,
  TemplateRef,
  ViewChild, ViewEncapsulation
} from '@angular/core';
import {MAT_OPTGROUP} from "@angular/material/core";
import {EsnOption} from "../option/option.component";
import {ProjectionRefModel} from "../select/select.component";

@Component({
  selector: 'esn-optgroup',
  templateUrl: './option-group.component.html',
  styleUrls: ['./option-group.component.scss'],
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
  providers: [{provide: MAT_OPTGROUP, useExisting: EsnOptionGroup}]
})
export class EsnOptionGroup implements AfterViewChecked{
  @ViewChild(TemplateRef) optTempVar: TemplateRef<any>;
    @ContentChildren(EsnOption, {descendants: true}) optionsList: QueryList<EsnOption>;

  @Input() label: string;
  @Input() disabled = false;

  get viewValue(): TemplateRef<any> {
    return this.optTempVar;
  }

  options: ProjectionRefModel[];

  constructor(private cdr: ChangeDetectorRef) {}

  ngAfterViewChecked(): void {
    if(!this.options || !this.options?.length) {
      this.options = this.optionsList.map(x => {
        return { value: x.value, viewValue: x.viewValue, disabled: x.disabled };
      });
      this.cdr.detectChanges();
    }
  }
}
