import { Injectable } from '@angular/core';
import { ConfirmationDialogComponent } from "../legacy/confirmation-dialog.component";
import { ConfirmationDialogDataModel } from "../models/confirmationDialogDataModel";
import { Observable } from "rxjs";
import { ConfirmationDialogModule } from "../confirmation-dialog.module";
import { EsnDialogRef } from "../../dialog/dialogRef";
import { EsnDialog } from "../../dialog/dialog.service";
import { ResponsiveService } from '../../../utils/services/responsive/responsive.service';

@Injectable({
  providedIn: ConfirmationDialogModule
})
export class ConfirmationDialogService {
  public isMobile$ = this.responsiveService.isMobile$;

  constructor(private dialog: EsnDialog, private responsiveService: ResponsiveService) {}

  show(data: ConfirmationDialogDataModel): EsnDialogRef<ConfirmationDialogComponent> {
    return this.dialog.open(ConfirmationDialogComponent, {
      data,
      fullScreenDialog: this.isMobile$.getValue() // fullscreen if mobile device
    });
  }

  setLoading(esnDialogRef: EsnDialogRef<ConfirmationDialogComponent>, loading: boolean) {
    esnDialogRef.componentInstance.loading = loading;
  }


  onDecision(esnDialogRef: EsnDialogRef<ConfirmationDialogComponent>): Observable<boolean> {
    return esnDialogRef.componentInstance.decision.asObservable();
  }
}
