import {
  AfterViewInit,
  Component,
  OnInit,
  ElementRef,
  Input,
} from '@angular/core';
import { EsnNotificationDrawerService } from './notification-drawer.service';
import {
  animate,
  state,
  style,
  transition,
  trigger,
} from '@angular/animations';
import { EsnNotificationsService } from '../notifications.service';
import { BehaviorSubject } from 'rxjs';
import { EsnNotificationProcessingService } from '../notification-processing.service';

@Component({
  selector: 'esn-notification-drawer',
  templateUrl: './notification-drawer.component.html',
  styleUrls: ['./notification-drawer.component.scss'],
  animations: [
    trigger('enteringAnimation', [
      state(
        'displayed',
        style({
          transform: 'translateX(-100%)',
        })
      ),
      state(
        'not-displayed',
        style({
          transform: 'translateX(0)',
        })
      ),
      transition('* => *', [animate('0.2s')]),
    ]),
  ],
  host: {
    class: 'esn-notification-drawer',
  },
})
export class EsnNotificationDrawer implements OnInit, AfterViewInit {
  @Input() totalUnread: number = 0;
  public totalNbNotifs: number = 0;
  public displayed: 'displayed' | 'not-displayed' = 'not-displayed';
  public refreshAll$: BehaviorSubject<boolean> = new BehaviorSubject<boolean>(
    false
  );
  public refreshUnread$: BehaviorSubject<boolean> =
    new BehaviorSubject<boolean>(false);

  public markAllAsRead$: BehaviorSubject<boolean> = new BehaviorSubject<boolean>(
    false
  );
  public scrollElementAll: HTMLElement;
  public scrollElementUnread: HTMLElement;
  public activeIndex: number = 0;

  constructor(
    public drawerService: EsnNotificationDrawerService,
    public notificationsService: EsnNotificationsService,
    public processingService: EsnNotificationProcessingService,
    public elementRef: ElementRef
  ) {}

  ngOnInit(): void {
    this.drawerService.isOpen$.subscribe(async (open) => {
      this.displayed = open ? 'displayed' : 'not-displayed';
      if (open) {
        this.refreshActiveTab();
      }
    });

    this.notificationsService
      .getUnread()
      .subscribe((unread) => (this.totalUnread = unread));
    this.notificationsService
      .getTotal()
      .subscribe((total) => {
        const prevTotal = this.totalNbNotifs;
        this.totalNbNotifs = total;
        if(!prevTotal && !!total){
          this.updateScrollElms()
        }
      });
  }

  ngAfterViewInit(): void {
    this.updateScrollElms()
  }

  public updateScrollElms(){
    setTimeout(() => {
      const tabBodies = this.elementRef.nativeElement.getElementsByClassName(
        'mat-mdc-tab-body-content'
      );
      this.scrollElementAll = tabBodies[0] as HTMLElement;
      this.scrollElementUnread = tabBodies[1] as HTMLElement;
    });
  }

  public closeDrawer() {
    this.drawerService.closeDrawer();
  }

  public tabChanged(e: any) {
    this.activeIndex = e.index;
    this.refreshActiveTab();
  }

  public refreshActiveTab() {
    if (this.activeIndex == 0) {
      this.refreshAll$.next(true);
    } else {
      this.refreshUnread$.next(true);
    }
  }

  public goToNotificationPage() {
    this.processingService.goToNotificationPage();
    this.closeDrawer();
  }

  public markAllAsRead() {
    this.notificationsService.markAllAsRead();
    this.markAllAsRead$.next(true);
  }
}
