import {ElementRef, Inject, Injectable, NgZone, ViewContainerRef} from '@angular/core';
import {
  _MatTooltipBase,
  MAT_TOOLTIP_DEFAULT_OPTIONS,
  MAT_TOOLTIP_SCROLL_STRATEGY,
  MatTooltipDefaultOptions,
  TooltipComponent
} from "@angular/material/tooltip";
import {ComponentType, Overlay, ScrollStrategy} from "@angular/cdk/overlay";
import {ScrollDispatcher} from "@angular/cdk/scrolling";
import {Platform} from "@angular/cdk/platform";
import {AriaDescriber, FocusMonitor} from "@angular/cdk/a11y";
import {Directionality} from "@angular/cdk/bidi";

export const ESN_TOOLTIP_SCROLL_STRATEGY = MAT_TOOLTIP_SCROLL_STRATEGY;
export const ESN_TOOLTIP_DEFAULT_OPTIONS = MAT_TOOLTIP_DEFAULT_OPTIONS;
const MIN_VIEWPORT_TOOLTIP_THRESHOLD = 8;


@Injectable()
export class EsnTooltip extends _MatTooltipBase<TooltipComponent> {
  protected readonly _tooltipComponent: ComponentType<TooltipComponent>;

  constructor(overlay: Overlay,
              elementRef: ElementRef<HTMLElement>,
              scrollDispatcher: ScrollDispatcher,
              viewContainerRef: ViewContainerRef,
              ngZone: NgZone, platform: Platform,
              ariaDescriber: AriaDescriber,
              focusMonitor: FocusMonitor,
              @Inject(ESN_TOOLTIP_SCROLL_STRATEGY) scrollStrategy: ScrollStrategy,
              dir: Directionality,
              @Inject(ESN_TOOLTIP_DEFAULT_OPTIONS) defaultOptions: MatTooltipDefaultOptions,
  ) {
    super(
      overlay,
      elementRef,
      scrollDispatcher,
      viewContainerRef,
      ngZone,
      platform,
      ariaDescriber,
      focusMonitor,
      scrollStrategy,
      dir,
      defaultOptions,
      document
    );
    this._viewportMargin = MIN_VIEWPORT_TOOLTIP_THRESHOLD;

  }

  setMessage(msg: string) {
    this.message = msg;
  }
}
