import { CollectionViewer, DataSource } from '@angular/cdk/collections';
import { BehaviorSubject, from, Observable, of, throwError } from 'rxjs';
import { catchError, debounceTime, map, switchMap, tap } from 'rxjs/operators';
import {
  ESN_ASYNC_DATASOURCE_DEFAULT_ADAPTER,
  EsnAsyncDataSourceAdapter,
  EsnAsyncDatasourceCallWrapperAdapter,
  EsnAsyncDataSourceType,
} from './esnAsyncTableAdapter';

export class EsnAsyncDataSource<T> implements DataSource<T> {
  private elementsSubject = new BehaviorSubject<T[]>([]);
  private loadingSubject = new BehaviorSubject<boolean>(false);
  private nbOfResultsSubject = new BehaviorSubject<number>(0);
  private errorDisplaySubject = new BehaviorSubject<any>(null);

  public loading$ = this.loadingSubject.asObservable();
  public numberOfResults$ = this.nbOfResultsSubject.asObservable();
  public errorDisplay$ = this.errorDisplaySubject.asObservable();

  _adapter: EsnAsyncDataSourceAdapter;

  public load$ = new BehaviorSubject<any[]>([]);

  constructor(
    public funcToCall: Function,
    adapter?: EsnAsyncDataSourceAdapter,
  ) {
    this._adapter = adapter || new ESN_ASYNC_DATASOURCE_DEFAULT_ADAPTER();
    this.load$
      .pipe(
        tap(() => this.loadingSubject.next(true)),
        debounceTime(this._adapter.debounceTime),
        tap(() => this.loadingSubject.next(true)),
        switchMap((args) =>
          this._adapter.callReturnType === EsnAsyncDataSourceType.PROMISE
            ? from(this.funcToCall(...args))
            : this.funcToCall(...args)
        ),
        catchError((err) => {
          this.loadingSubject.next(false);
          this.errorDisplaySubject.next(this._adapter.processError(err));
          return throwError(err);
        })
      )
      .subscribe((resp) => {
        this.loadingSubject.next(false);
        this.elementsSubject.next(this._adapter.getResults(resp));
        this.nbOfResultsSubject.next(this._adapter.getTotalNbResults(resp));
        this.errorDisplaySubject.next(this._adapter.getError(resp));
      });
  }

  connect(collectionViewer: CollectionViewer): Observable<readonly T[]> {
    return this.elementsSubject.asObservable();
  }

  disconnect(collectionViewer: CollectionViewer): void {
    this.elementsSubject.complete();
    this.loadingSubject.complete();
    this.nbOfResultsSubject.complete();
    this.errorDisplaySubject.complete();
  }

  load(...args: any[]) {
    this.load$.next(args);
  }
}
