import { Component, Input, OnDestroy, OnInit, Optional } from '@angular/core';
import { EdfApplicationUser } from '../../utils/public-api';
import { EsnMenu } from '../menu/menu';
import { EsnNotificationsService } from '../notification/notifications.service';
import { EsnNotificationDrawerService } from '../notification/notification-drawer/notification-drawer.service';

@Component({
  selector: 'esn-app-bar',
  templateUrl: './app-bar.component.html',
  styleUrls: ['./app-bar.component.scss'],
  host: {
    class: 'esn-app-bar',
  },
})
export class EsnAppBar implements OnInit, OnDestroy {
  @Input() user: EdfApplicationUser;
  @Input() userMenu?: EsnMenu;
  @Input() avatarTooltip: string;
  public nbNotViewed: number = 0;
  public nbNotRead: number = 0;
  public notificationShaking: boolean = false;
  public interval: any;
  constructor(
    @Optional() public notifService: EsnNotificationsService,
    @Optional() public notifDrawerService: EsnNotificationDrawerService
  ) {}

  ngOnInit(): void {
    if (!!this.notifService) {
      this._initiateNotificationsSubscriptions();
    }
  }

  public onNotificationsClick() {
    this.notifDrawerService.toggleDrawer();
  }

  _initiateNotificationsSubscriptions() {
    this.notifService.getNotViewed().subscribe((nbNotViewed) => {
      this.nbNotViewed = nbNotViewed;

      if (!!nbNotViewed && !this.interval) {
        this.interval = setInterval(() => {
          this.notificationShaking = true;
          setTimeout(() => (this.notificationShaking = false), 2000);
        }, 30000);
      } else if (!!this.interval) {
        clearInterval(this.interval);
        this.interval = null;
      }
    });
    this.notifService.getUnread().subscribe((nbNotRead) => this.nbNotRead = nbNotRead);
    this.notifService.refreshUnreadAndNotViewed();
  }

  ngOnDestroy(){
    if (!!this.interval) {
      clearInterval(this.interval);
    }
  }
}
