import {
  AfterViewInit,
  Component,
  EventEmitter,
  Inject,
  Input,
  OnDestroy,
  OnInit,
  Output,
} from '@angular/core';
import {
  animate,
  state,
  style,
  transition,
  trigger,
} from '@angular/animations';
import { EsnNotificationsService } from '../notifications.service';
import { EsnNotificationModel } from '../model/notification';
import {
  ApiCallWrapper,
  ApiErrorDisplay,
  EsnUtils,
  ListWrapperDto,
} from '../../../utils/public-api';
import { BehaviorSubject, Subscription, filter, tap } from 'rxjs';
import { NOTIFICATION_TAGS } from '../notification-configuration';

@Component({
  selector: 'esn-notification-list',
  templateUrl: './notification-list.component.html',
  styleUrls: ['./notification-list.component.scss'],
  host: {
    class: 'esn-notification-list',
  },
})
export class EsnNotificationList implements OnInit, OnDestroy {
  @Input() refreshSubject$: BehaviorSubject<boolean> =
    new BehaviorSubject<boolean>(false);
  @Input() scrollElement: HTMLElement;
  @Input() markAllAsReadSubject$: BehaviorSubject<any>;
  @Input() unread?: boolean;
  @Input() dateBefore?: Date;
  @Input() dateAfter?: Date;
  @Input() criticality?: string;
  public notifications: EsnNotificationModel[] = [];
  public loading = true;
  public errorDisplay?: ApiErrorDisplay;
  public searchText = '';
  public totalNumberOfResults = 0;
  public refreshElementsSubject: BehaviorSubject<null> =
    new BehaviorSubject<null>(null);
  public callFunc?: (
    page: number,
    pageSize: number
  ) => Promise<EsnNotificationModel[]>;
  public initialized: boolean = false;
  public refreshId: string = '';
  public subs: Subscription = new Subscription();

  constructor(
    public notificationsService: EsnNotificationsService
  ) {}

  ngOnInit(): void {
    this.callFunc = (a: number, b: number) => this.retreiveElms(a, b);
    this.subs.add(
      this.refreshSubject$
        .pipe(filter((x: boolean) => x))
        .subscribe(() => this.onRefresh())
    );
    this.subs.add(
      this.markAllAsReadSubject$
        .pipe(filter((x: boolean) => x))
        .subscribe(() => this.notifications.map((n) => (n.isRead = true)))
    );
  }

  public async retreiveElms(page: number, size: number): Promise<any> {
    this.loading = true;
    const id = this.refreshId;
    const callWrapper: ApiCallWrapper =
      await this.notificationsService.getNotifications(
        page,
        size,
        this.notificationsService.getNotificationTags(),
        !!this.unread ? false : undefined,
        this.dateBefore,
        this.dateAfter,
        this.criticality
      );
    if (this.refreshId != id) {
      return;
    }

    this.errorDisplay = callWrapper.error;
    if (!!callWrapper.resp) {
      const response: ListWrapperDto<EsnNotificationModel> = callWrapper.resp;
      this.loading = false;
      this.totalNumberOfResults = response.totalNumberOfResults!;

      if (!this.unread) {
        this.notificationsService.setTotal(response.totalNumberOfResults!);
      }

      if (!!response.results?.length) {
        this.notificationsService.updateLastReveivedSendDate(
          response.results[0].sendDate!
        );

        if (page == 0) {
          this.notificationsService.markAllAsViewed();
        }
      }

      return response.results;
    }
    this.loading = false;
    this.totalNumberOfResults = 0;
    return [];
  }

  public onElmsUpdated(elms: EsnNotificationModel[]) {
    this.notifications = elms;
  }

  public onRefresh() {
    this.refreshId = EsnUtils.generateRandomUuid();
    this.initialized = true;
    this.refreshElementsSubject.next(null);
  }

  public onNotificationClick(notification: EsnNotificationModel) {
    if (!notification.isRead) {
      this.notificationsService.markAsRead(notification.notificationId);
      notification.isRead = true;
    }
  }

  ngOnDestroy(): void {
    this.subs.unsubscribe();
  }
}
